<template>
    <div v-on:click="viewRequestDetail" class="each-item">
        <div class="display-left target-user-border">
            <user-icon
                :userIconStyle="userIconStyle"
                :imageId="item.borrowerImageId">
            </user-icon>
        </div>
        <div class="display-left" style="margin-left:15px;width:410px;">
            <!--user-name-->
            <div class="half-row-top display-left each-item-text">
                <span class="medium-text-form-m text-limit text-muted title-name-color">{{item.borrowerName}}</span>
            </div>
            <!--request-content-->
            <div class="half-row-bottom small-text-form-r text-limit display-left clear-left each-item-text">
                <span class="fa fa-hand-o-right"></span>
                <span>{{item.editionTitle}}</span>
            </div>
        </div>
        <div class="display-right small-text-form-r" style="margin-top:4px">
            {{label.datetime(item.updateDate)}}
        </div>
    </div>
</template>

<script>
import Label from '../../../common/Label.js'
import UserIcon from '../userIcon/UserIcon.vue'

export default {
    props: [ "item" ],
    mixins: [ Label ],
    components: { UserIcon },
    methods: {
        viewRequestDetail(){
            this.$router.push({ name: this.label.NAME_USER_REQUEST_DETAIL_PAGE, params: {id: this.item.recordId}});
        }
    },
    data() {
        return {
            //style of user icon
            userIconStyle: {
                'background-color': 'white',
                'border-radius': '28px',
                'width': '48px',
                'height': '48px'
            },
        }
    },

}
</script>

<style>

</style>



