import * as CONST from './const.js'
import LabelVi from './LabelVi.js'
import LabelEn from './LabelEn.js'

export default {
    mixins: [LabelVi, LabelEn],
    mounted(){
        this.language = localStorage.getItem("language");
        if (!this.language) {
            localStorage.setItem("language", "vni");
        }
        if (this.language == 'vni') {
            this.label = this.labelVni;
        } else {
            this.label = this.labelEng;
        }
    },
    data(){
        return {
            language: '',
            language_filter: ['vni', 'eng'],
            label: {
                from_to: ["", ""],
                requestType: ["", "", "", ""],
                visitorSharingBookNA(userName){
                    return "";
                },
                visitorSharingBook(userName, numb){
                    return "";
                },
                totalResult(numb){
                    return "";
                },
                recordStatus(status){
                    return "";
                },
                sendBorrowRequestConfirm(userName, title) {
                    return "";
                },
                sendOnholdRequestConfirm(userName, title) {
                    return "";
                },
                visitorReviewNA(userName) {
                    return "";
                },
                visitorReview(userName){
                    return "";
                },
                sendRequest(id, title) {
                    return "";
                },
                receiveRequest(id, title) {
                    return "";
                },
                datetime(offset) {
                    return "";
                },
                sendRequestDetail(id, title) {
                    return "";
                },
                receiveRequestDetail(id, title, userId, name, userTypeFlag) {
                    return "";
                },
                getDateTime(offset){
                    return "";
                },
                getHourMinute(offset){
                    return "";
                },
                updateRequestSttDialogContent(newStt, userName, title){
                    return "";
                },
                getDateTimeMessageSplit(offset){
                    return "";
                },
                getNotificationContent(type, targetId, title, counting, referUserId, referUserName, referUserTypeFlag) {
                    return "";
                },
                getNotificationContentHeader(type, targetId, title, counting, referUserId, referUserName) {
                    return "";
                },
                deleteInstanceConfirm(title) {
                    return "";
                },
                getTextNumberShareBook(sharingCount) {
                    return sharingCount + " " + this.sharingBook;
                },
                txt_number_user_share_book_1: "",
                txt_number_user_share_book_2: "",
                txt_number_user_share_book_3: "",
                txt_user_action: ["", "", ""],
                txt_record_status: ["", "", "", ""],
                txt_action_with_book: "",
                txt_action_array: [{ status: 2, text: "" }, { status: 1, text: "" }, { status: 0, text: "" }, { status: -1, text: "" }],
            }
        }
    }
    
}


