import Vue from 'vue'
import Router from 'vue-router'
import Meta from 'vue-meta'
import Home from '@/views/home/layout/Home'
import Login from '@/views/user_page/user_login/layout/UserLogin'
import ForgotPass from '@/views/user_page/forgot_pass/layout/ForgotPass'
import UserProfile from '@/views/user_page/user_profile/layout/UserProfile'
import ListBooks from '@/views/listBooks/layout/ListBooks.vue'
import SearchBook from '@/views/search_page/search_book/layout/SearchBook.vue'
import SearchUser from '@/views/search_page/search_user/layout/SearchUser.vue'
import UserRequest from '@/views/user_page/user_request/layout/UserRequest'
import UserMessage from '@/views/user_page/user_message/layout/UserMessage'
import UserNotification from '@/views/user_page/user_notification/layout/UserNotification'
import BookDetail from '@/views/bookDetail/layout/BookDetail.vue'
import UserNearYou from '@/views/userNearYou/layout/UserNearYou.vue'
import EventDetail from '@/views/EventDetail/layout/EventDetail.vue'
import ListBookstops from '@/views/listBookstops/layout/ListBookstops.vue'
import BookstopDetail from '@/views/bookstopDetail/layout/BookstopDetail.vue'
import * as CONST from '@/common/const.js'
import EditReview from '@/views/bookDetail/component/editUserReview/EditUserReview.vue'

Vue.use(Router)
Vue.use(Meta)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: CONST.PATH_HOME_PAGE,
      name: CONST.NAME_HOME_PAGE,
      component: Home
    },
    {
      path: CONST.PATH_FORGOT_PASS_PAGE,
      name: CONST.NAME_FORGOT_PASS_PAGE,
      component: ForgotPass
    },
    {
      path: CONST.PATH_LOGIN_PAGE,
      name: CONST.NAME_LOGIN_PAGE,
      component: Login
    },
    {
      path: CONST.PATH_SHARE_BOOKS_PAGE,
      name: CONST.NAME_SHARE_BOOKS_PAGE,
      component: ListBooks,
      props: { category: 0 } 
    },
    {
      path: CONST.PATH_SEARCH_BOOK_PAGE,
      name: CONST.NAME_SEARCH_BOOK_PAGE,
      component: SearchBook,
      props: {
        searchBy: 0
      }
    },
    {
      path: CONST.PATH_SEARCH_AUTHOR_PAGE,
      name: CONST.NAME_SEARCH_AUTHOR_PAGE,
      component: SearchBook,
      props: {
        searchBy: 1
      }
    },
    {
      path: CONST.PATH_SEARCH_USER_PAGE,
      name: CONST.NAME_SEARCH_USER_PAGE,
      component: SearchUser
    },
    {
      path: CONST.PATH_USER_REQUEST_PAGE,
      name: CONST.NAME_USER_REQUEST_PAGE,
      component: UserRequest
    },
    {
      path: CONST.PATH_USER_REQUEST_DETAIL_PAGE,
      name: CONST.NAME_USER_REQUEST_DETAIL_PAGE,
      component: UserRequest
    },
    {
      path: CONST.PATH_USER_VISITOR_PAGE,
      name: CONST.NAME_USER_VISITOR_PAGE,
      component: UserProfile
    },
    {
      path: CONST.PATH_USER_MESSAGE_PAGE,
      name: CONST.NAME_USER_MESSAGE_PAGE,
      component: UserMessage
    },
    {
      path: CONST.PATH_USER_MESSAGE_DETAIL_PAGE,
      name: CONST.NAME_USER_MESSAGE_DETAIL_PAGE,
      component: UserMessage
    },
    {
      path: CONST.PATH_USER_NOTIFICATION_PAGE,
      name: CONST.NAME_USER_NOTIFICATION_PAGE,
      component: UserNotification
    },
    {
      path: CONST.PATH_BOOK_DETAIL_PAGE,
      name: CONST.NAME_BOOK_DETAIL_PAGE,
      component: BookDetail
    },
    {
      path: CONST.PATH_NEAR_BY_USER,
      name: CONST.NAME_NEAR_BY_USER,
      component: UserNearYou
    },
    {
      path: CONST.PATH_EVENT_DETAIL_PAGE,
      name: CONST.NAME_EVENT_DETAIL_PAGE,
      component: EventDetail
    },
    {
      path: CONST.PATH_LIST_BOOKSTOPS_PAGE,
      name: CONST.NAME_LIST_BOOKSTOPS_PAGE,
      component: ListBookstops
    },
    {
      path: CONST.PATH_BOOKSTOP_DETAIL_PAGE,
      name: CONST.NAME_BOOKSTOP_DETAIL_PAGE,
      component: BookstopDetail
    },
    {
      path: CONST.PATH_EDIT_REVIEW_PAGE,
      name: CONST.NAME_EDIT_REVIEW_PAGE,
      component: EditReview
    }
  ]
})



