<template>
    <div class="email-login-form">
        <loading-dialog :loadingDialogName="'email-login-loading'" :isLoadingOpen="isSystemExecuting"></loading-dialog>
        <error-dialog
            :errorDialogName="'email-login-fail'"
            :isErrorOpen="isApiserviceErrorDialogOpen"
            :errorMessage="apiErrorDialogContent"
            :errorStatus="errorStatus"
            @errorDialogClosed="isApiserviceErrorDialogOpen = false"></error-dialog>
        <ul class="nav nav-tabs">
            <li class="login-signup-tab" :class="{'active': $route.params.tab=='signin'}">
                <router-link :to="{ name: label.NAME_LOGIN_PAGE, params: {tab: 'signin'}}" data-toggle="tab">{{label.txt_login}}</router-link>
            </li>
            <li class="login-signup-tab" :class="{'active': $route.params.tab=='signup'}">
                <router-link :to="{ name: label.NAME_LOGIN_PAGE, params: {tab: 'signup'}}" data-toggle="tab">{{label.txt_register}}</router-link>
            </li>
        </ul>
        <div class="tab-content">
            <div v-on:keyup.enter="signinByEmail" id="login-form" class="tab-pane fade" :class="{'in active': $route.params.tab=='signin'}">
                <div style="margin-top:36px;">
                    <input v-model="signin_email" type="text" class="form-control gat-form" v-bind:placeholder="label.txt_email">
                </div>
                <div style="margin-top:30px;">
                    <input v-model="signin_password" type="password" class="form-control gat-form" v-bind:placeholder="label.txt_pw">
                </div>
                <div style="margin-left:6px; margin-top:35px;">
                    <div class="checkbox">
                        <label>
                            <input style="transform: scale(1.6);background-color:#fff" type="checkbox" value="">&nbsp;{{label.txt_pw_remember}}</label>
                    </div>
                </div>
                <div style="margin-top:30px;">
                    <button v-on:click="signinByEmail" type="button" class="btn btn-gat btn-circle btn-email">
                        {{label.txt_login}}
                    </button>
                </div>
            </div>
            <div v-on:keyup.enter="signupByEmail" id="register-form" class="tab-pane fade" :class="{'in active': $route.params.tab=='signup'}">
                <div style="margin-top:30px;">
                    <input v-model="signup_email" type="text" class="form-control gat-form" v-bind:placeholder="label.txt_email">
                </div>
                <div style="margin-top:30px;">
                    <input v-model="signup_password" type="password" class="form-control gat-form" v-bind:placeholder="label.txt_pw">
                </div>
                <div style="margin-top:30px;">
                    <button v-on:click="signupByEmail" type="button" class="btn btn-gat btn-circle btn-email">
                        {{label.txt_register}}
                    </button>
                </div>
            </div>
            <div class="align-center" style="margin-top:35px;">
                <router-link :to="{name: label.NAME_FORGOT_PASS_PAGE, params: {tab: 'input-email'}}" >
                    {{label.forgot_pass}}
                </router-link>
            </div>
        </div>
    </div>
</template>

<script>
import Label from '../../../../common/Label.js'
import LoadingDialog from '../../../common/dialog/LoadingDialog.vue'
import BookService from '../../../../services/BookService.js'
import * as CONST from '../../../../common/const.js'
export default {
    mixins: [ BookService, Label ],
    components: { LoadingDialog },
    methods: {
        signinByEmail() {
            this.isSystemExecuting = true;
            this.signup_mode = false;
            var data = {
                email: this.signin_email,
                password: this.signin_password,
                uuid: window.uuid
            };
            this.api.callPostApi(this.api.api_login_by_email, data, {}).then(this.initData, this.handleErrorFunction);
        },
        signupByEmail() {
            this.isSystemExecuting = true;
            this.signup_mode = true;
            var data = {
                name: this.signup_email.split('@')[0],
                email: this.signup_email,
                password: this.signup_password,
                uuid: window.uuid
            };
            this.api.callPostApi(this.api.api_register_by_email, data, {}).then(this.initData, this.handleErrorFunction);
        },
        initData(response) {
            var _self = this;
            //login to firebase by general account
            firebase.auth().signInWithEmailAndPassword(CONST.GENERAL_FIREBASE_EMAIL, CONST.GENERAL_FIREBASE_PASSWORD).catch(function(error) {
                // Show error log
                console.log(error.code);
                console.log(error.message);
            });
            if (response.data.data.loginToken) {
                localStorage.setItem(CONST.LOGIN_TOKEN, response.data.data.loginToken);
            }
            if (response.data.data.firebasePassword) {
                localStorage.setItem(CONST.FIREBASE_PASS, response.data.data.firebasePassword);
                //sign up to firebase
                if (_self.signup_mode) {
                    firebase.auth().createUserWithEmailAndPassword(_self.signup_email, response.data.data.firebasePassword).catch(function(error) {
                        // Show error log
                        console.log(error.code);
                        console.log(error.message);
                    });
                }
            }
            var config = {
                headers: {
                    'Authorization': response.data.data.loginToken
                }
            };
            _self.api.callGetApi(_self.api.api_get_user_private_info, config)
                .then(response => {
                        localStorage.setItem(CONST.USER_ID, response.data.data.resultInfo.userId);
                        if(response.data.data.resultInfo.usuallyLocation.length==0){
                            response.data.data.resultInfo.usuallyLocation[0] = {};
                        }
                        _self.$setItem(CONST.LOGIN_USER, response.data.data.resultInfo);
                        _self.$router.push({ name: "home"});
                        _self.isSystemExecuting = false;
                    })
                .catch(_self.handleErrorFunction);
            _self.$emit('firebaseRegister');
        }

    },
    data() {
        return {
            signin_email: "",
            signin_password: "",
            signup_email: "",
            signup_password: "",
            signup_mode: false
        }
    }
}
</script>

<style>

</style>



