<template>
    <div class="box-full box-margin-top">
        <div class="row-header">
            <span class="biger-text-form-m text-color-default">{{label.txt_suggest_book}}</span>
        </div>
        <hr class="line-break reset-padding reset-margin">
        <div class="box-list-suggest-book">
            <div v-for="(book, index) in books" :key="book.editionId">
                <book-info :editionId="book.editionId" :title="book.title" :imageId="book.imageId" :author="book.author" :rateAvg="book.rateAvg" :numberRate="book.number_rate"></book-info>
            </div>
        </div>
    </div>
</template>


<script>
import BookService from '../../../../services/BookService.js';
import CommonService from '../../../../services/CommonService.js';
import BookInfo from '../../../common/bookInfo/BookInfo.vue';
import Label from '../../../../common/Label.js';

export default {
    name: 'list-book-same-category',
    components: { BookInfo },
    mixins: [CommonService, BookService, Label],
    mounted() {
        this.initData();
    },
    data() {
        return {
            books: []
        }
    },
    methods: {
        initData() {
            if (this.isNotNull(this.category)) {
                // var config = {
                //     params: {
                //         per_page: 5,
                //         page: 1,
                //         categoryIds: this.category
                //     }
                // }
                // this.api.callGetApi(this.api.api_get_book_same_category, config).then(this.setListBookSameCategory, this.errorCallApiGetBookSameCategory);
                var config = {
                    params: {
                        per_page: 5,
                        page: 1
                    }
                }
                this.api.callGetApi(this.api.api_list_sharing_books, config).then(this.setListBookSameCategory, this.errorCallApiGetBookSameCategory);
            }
        },
        setListBookSameCategory(response) {
            if (200 == response.status) {
                this.books = response.data.data.resultInfo;
            }
        },
        errorCallApiGetBookSameCategory(error) {
            this.$emit('showErrorDialog', error.response.data.message);
        }
    },
    watch: {
        category: function() {
            this.initData();
        }
    },
    props: {
        category: {
            required: true
        },
    }
}
</script>
<style lang="scss" scoped>
@import 'ListRelatedBook.scss';
</style>



