import axios from 'axios'
import * as CONST from '../common/const.js'
import ErrorDialog from '../views/common/dialog/ErrorDialog.vue'

export default {
  components: {
    ErrorDialog
  },
  mounted() {
    this.loginUserId = localStorage.getItem(CONST.USER_ID);
    this.loginToken = localStorage.getItem(CONST.LOGIN_TOKEN);
    axios.defaults.baseURL = CONST.BASE_URL;
    axios.defaults.headers.common['Content-Type'] = 'application/x-www-form-urlencoded';
    var language = localStorage.getItem("language");
    if (language == 'vni') {
      axios.defaults.headers.common['Accept-Language'] = CONST.VI;
    } else {
      axios.defaults.headers.common['Accept-Language'] = CONST.EN;
    }
  },
  watch: {
    '$route': function () {
      this.loginUserId = localStorage.getItem(CONST.USER_ID);
      this.loginToken = localStorage.getItem(CONST.LOGIN_TOKEN);
    }
  },
  data() {
    return {
      loginUserId: null,
      loginToken: null,
      isShowLoading: false,
      //for screen has 3 tabs
      isShowLoading_1: false,
      isShowLoading_2: false,
      isShowLoading_3: false,
      isApiserviceErrorDialogOpen: false,
      apiErrorDialogContent: "",
      isSystemExecuting: false,
      errorStatus: null,

      api: {
        api_list_sharing_books: "suggestion/book_suggestion",
        api_list_favorite_books: "suggestion/most_reading",
        api_list_most_share: "suggestion/most_sharing",
        api_near_by_book: "suggestion/nearby_book",
        api_user: "search/top_sharing_user",
        api_near_user: "search/nearby_user",
        api_login_by_social: "user/login_by_social",
        api_register_by_social: "user/register_by_social",
        api_login_by_email: "user/login_by_email",
        api_register_by_email: "user/register_by_email",
        api_request_reset_password: "user/request_reset_password",
        api_verify_reset_token: "user/verify_reset_token",
        api_reset_password: "user/reset_password",
        api_get_twitter_oauth_token: "user/get_twitter_oauth_token",
        api_get_twitter_user: "user/get_twitter_user",
        api_get_user_private_info: "user/get_user_private_info",
        api_get_user_public_info: "user/get_user_public_info",
        api_firebase_token_register: "user/firebase_token_register",
        api_selfget_book_instance: "book/selfget_book_instance_web",
        api_selfget_book_instance_total: "book/selfget_book_instance_total_web",
        api_get_user_sharing_editions: "book/get_user_sharing_editions_web",
        api_get_user_sharing_editions_total: "book/get_user_sharing_editions_total_web",
        api_selfchange_instance_stt: "book/selfchange_instance_stt",
        api_selfremove_instance: "book/selfremove_instance",
        api_get_user_reading_editions: "book/get_user_reading_editions_web",
        api_get_user_reading_editions_total: "book/get_user_reading_editions_total_web",
        api_selfupdate_reading_stt: "book/selfupdate_reading_stt",
        api_selfupdate_book_evaluation: "book/selfupdate_book_evaluation",
        api_update_user_info: "user/update_user_info",
        api_update_user_info_ios: "user/update_user_info_ios",
        api_update_usually_location: "user/update_usually_location",
        api_link_social_acc: "user/link_social_acc",
        api_unlink_social_acc: "user/unlink_social_acc",
        api_add_email_pass: "user/add_email_pass",
        api_change_password: "user/change_password",
        api_get_user_notification: "user/get_user_notification",
        api_get_user_notification_web: "user/get_user_notification_web",
        api_sign_out: "user/sign_out",
        api_get_request_summary: "share/get_request_summary",
        api_get_request_list_web: "share/get_request_list_web",
        api_get_request_info: "share/get_request_info",
        api_create_request: "share/create_request",
        api_update_request_by_owner: "share/update_request_by_owner",
        api_update_request_by_borrower: "share/update_request_by_borrower",

        api_search_title: "search/book_by_title",
        api_search_title_total: "search/book_by_title_total",
        api_search_author: "search/book_by_author",
        api_search_author_total: "search/book_by_author_total",
        api_search_user: "search/user",
        api_search_user_total: "search/user_total",
        api_get_book_info: "book/get_book_info",
        api_get_review_for_book: "book/get_edition_evaluation",
        api_get_edition_sharing_user: "book/get_edition_sharing_user",
        api_get_book_sharing_total: "book/get_book_sharing_total",
        api_get_book_same_category: "search/book_same_category",
        api_selfadd_instance: "book/selfadd_instance",
        api_selfget_instance_info: "book/selfget_instance_info",
        api_selfget_reading_stt: "book/selfget_reading_stt",

        api_get_username_image: "common/get_username_image",
        api_selfget_book_evaluation: "book/selfget_book_evaluation",

        api_get_book_searched_keyword : "search/get_book_searched_keyword",
        api_get_author_searched_keyword : "search/get_author_searched_keyword",
        api_get_user_searched_keyword : "search/get_user_searched_keyword",

        api_get_bookstop_info: "bookstop/get_bookstop_info",
        api_get_bookstop_image_total: "bookstop/get_bookstop_image_total",
        api_get_bookstop_images: "bookstop/get_bookstop_images",
        api_get_bookstop_total: "bookstop/get_bookstop_total",
        api_get_bookstop_list: "bookstop/get_bookstop_list",
        api_get_review_list: "book/get_review_list",

        //get API, config include params and headers
        callGetApi: function (url, config) {
          var promiseObj = new Promise(
            (onSuccess, onError) => {
              axios.get(url, config)
                .then(response => onSuccess(response))
                .catch(error => onError(error));
            }
          );
          return promiseObj;
        },

        callGetApiWithAuthorization: function (url, config) {
          axios.defaults.headers.common['Authorization'] = localStorage.getItem(CONST.LOGIN_TOKEN);
          var promiseObj = new Promise(
            (onSuccess, onError) => {
              axios.get(url, config)
                .then(response => onSuccess(response))
                .catch(error => onError(error));
            }
          );
          return promiseObj;
        },

        //post API, data = body, config = headers
        callPostApi: function (url, data, config) {
          var qs = require('qs');
          var promiseObj = new Promise(
            (onSuccess, onError) => {
              axios.post(url, qs.stringify(data), config)
                .then(response => onSuccess(response))
                .catch(error => onError(error));
            }
          );
          return promiseObj;
        },

        callPostApiWithAuthorization: function (url, data, config) {
          if (null == config) {
            config = {};
          }
          config.Authorization = localStorage.getItem(CONST.LOGIN_TOKEN);
          var qs = require('qs');
          var promiseObj = new Promise(
            (onSuccess, onError) => {
              axios.post(url, qs.stringify(data), config)
                .then(response => onSuccess(response))
                .catch(error => onError(error));
            }
          );
          return promiseObj;
        }
      },

    }
  },
  methods: {
    handleErrorFunction(error) {
      if (this.isShowLoading) {
        this.isShowLoading = false;
      }
      if (this.isShowLoading_1) {
        this.isShowLoading_1 = false;
      }
      if (this.isShowLoading_2) {
        this.isShowLoading_2 = false;
      }
      if (this.isShowLoading_3) {
        this.isShowLoading_3 = false;
      }
      if (this.isSystemExecuting) {
        this.isSystemExecuting = false;
      }
      this.errorStatus = error.response.status;
      this.apiErrorDialogContent = error.response.data.message;
      this.isApiserviceErrorDialogOpen = true;
    },
    closeLoadingDialog() {
      this.isSystemExecuting = false;
    }
  }

}



