<template>
    <div class="user-profile-detail-form box-shadow-bold">
        <!-- navigation bar (include sharingBook, notSharingBook and lostBook) -->
        <ul class="nav nav-tabs tab-header">
            <li class="book-shelf-na-tab">&nbsp</li>
            <li class="book-shelf-tab" :class="{'active': $route.query.nav=='sharing'}">
                <router-link v-if="loginUserId" :to="{ name: label.NAME_USER_VISITOR_PAGE, params: {id: loginUserId, tab: 'book-shelf'}, query: {nav: 'sharing', page: 1}}" data-toggle="tab">{{label.sharingBook}}</router-link>
            </li>
            <li class="book-shelf-tab" :class="{'active': $route.query.nav=='notsharing'}">
                <router-link v-if="loginUserId" :to="{ name: label.NAME_USER_VISITOR_PAGE, params: {id: loginUserId, tab: 'book-shelf'}, query: {nav: 'notsharing', page: 1}}" data-toggle="tab">{{label.notSharingBook}}</router-link>
            </li>
            <li class="book-shelf-tab" :class="{'active': $route.query.nav=='lost'}">
                <router-link v-if="loginUserId" :to="{ name: label.NAME_USER_VISITOR_PAGE, params: {id: loginUserId, tab: 'book-shelf'}, query: {nav: 'lost', page: 1}}" data-toggle="tab">{{label.lostBook}}</router-link>
            </li>
            <li class="book-shelf-na-tab">&nbsp</li>
        </ul>
        <!-- tab content display the list of book -->
        <div class="tab-content">
            <!-- sharing tab -->
            <div id="sharing-book-form" class="book-in-each tab-pane fade" :class="{'in active': $route.query.nav=='sharing'}">
                <div v-if="isShowLoading_1" style="margin:10px;">
                    <img src="../../../../assets/images/loading.gif" class="loading-img-medium">
                </div>
                <div v-if="!isShowLoading_1&&sharingTotal > 0" class="center-block">
                    <div class="row" v-for="splitedBook in sharingBooks">
                        <div class="col-xs-3 nopadding" v-for="book in splitedBook">
                            <book-in-book-shelf :book = "book" :refresh="refresh"></book-in-book-shelf>
                        </div>
                    </div>
                    <div v-if="sharingTotal <= per_page" style="height:15px;"></div>
                    <div v-if="sharingTotal > per_page" class="text-center">
                        <uib-pagination
                            :boundary-links="true"
                            :force-ellipses="false"
                            :boundary-link-numbers="true"
                            previous-text="<" 
                            next-text=">" 
                            first-text="<<"
                            last-text=">>"
                            v-model="sharingPagination" 
                            :items-per-page="per_page" 
                            :total-items="sharingTotal" 
                            :max-size="4" 
                            :rotate="true">
                        </uib-pagination>
                    </div>
                </div>
                <div v-if="!isShowLoading_1&&sharingTotal == 0" style="margin-left:10px;">{{sharingText}}</div>
            </div>

            <!-- not sharing tab -->
            <div id="not-sharing-book-form" class="book-in-each tab-pane fade" :class="{'in active': $route.query.nav=='notsharing'}">
                <div v-if="isShowLoading_2" style="margin:10px;">
                    <img src="../../../../assets/images/loading.gif" class="loading-img-medium">
                </div>
                <div v-if="!isShowLoading_2&&notSharingTotal > 0">
                    <div class="row" v-for="splitedBook in notSharingBooks">
                        <div class="col-xs-3 nopadding" v-for="book in splitedBook">
                            <book-in-book-shelf :book = "book" :refresh="refresh"></book-in-book-shelf>
                        </div>
                    </div>
                    <div v-if="notSharingTotal <= per_page" style="height:15px;"></div>
                    <div v-if="notSharingTotal > per_page" class="text-center">
                        <uib-pagination
                            :boundary-links="true"
                            :force-ellipses="false"
                            :boundary-link-numbers="true"
                            previous-text="<" 
                            next-text=">" 
                            first-text="<<"
                            last-text=">>"
                            v-model="notSharingPagination" 
                            :items-per-page="per_page" 
                            :total-items="notSharingTotal" 
                            :max-size="4" 
                            :rotate="true">
                        </uib-pagination>
                    </div>
                </div>
                <div v-if="!isShowLoading_2&&notSharingTotal == 0" style="margin-left:10px;">{{notSharingText}}</div>
            </div>

            <!-- lost tab -->
            <div id="lost-book-form" class="book-in-each tab-pane fade" :class="{'in active': $route.query.nav=='lost'}">
                <div v-if="isShowLoading_3" style="margin:10px;">
                    <img src="../../../../assets/images/loading.gif" class="loading-img-medium">
                </div>
                <div v-if="!isShowLoading_3&&lostTotal > 0">
                    <div class="row" v-for="splitedBook in lostBooks">
                        <div class="col-xs-3 nopadding" v-for="book in splitedBook">
                            <book-in-book-shelf :book = "book" :refresh="refresh"></book-in-book-shelf>
                        </div>
                    </div>
                    <div v-if="lostTotal <= per_page" style="height:15px;"></div>
                    <div v-if="lostTotal > per_page" class="text-center">
                        <uib-pagination
                            :boundary-links="true"
                            :force-ellipses="false"
                            :boundary-link-numbers="true"
                            previous-text="<" 
                            next-text=">" 
                            first-text="<<"
                            last-text=">>"
                            v-model="lostPagination" 
                            :items-per-page="per_page" 
                            :total-items="lostTotal" 
                            :max-size="4" 
                            :rotate="true">
                        </uib-pagination>
                    </div>
                </div>
                <div v-if="!isShowLoading_3&&lostTotal == 0" style="margin-left:10px;">{{lostText}}</div>
            </div>
        </div>
    </div>
</template>

<script>
import BookInBookShelf from './BookInBookShelf.vue';
import BookService from '../../../../services/BookService.js';
import Label from '../../../../common/Label.js';
import * as CONST from '../../../../common/const.js';

export default {
    components: { BookInBookShelf },
    mixins: [BookService, Label],
    mounted() {
        if(this.loginToken) {
            this.refresh();
        }
    },
    watch: {
        loginToken: function(newVal, oldVal){
            if(newVal) {
                this.refresh();
            }
        },
        '$route': function(){
            this.refresh();
        },
        sharingPagination: function(){
            var page = this.sharingPagination.currentPage;
            var _self = this;
            this.$router.push({ name: _self.$route.name, params: {tab: _self.$route.params.tab}, query: {nav: 'sharing', page: page}});
        },
        notSharingPagination: function(){
            var page = this.notSharingPagination.currentPage;
            var _self = this;
            this.$router.push({ name: _self.$route.name, params: {tab: _self.$route.params.tab}, query: {nav: 'notsharing', page: page}});
        },
        lostPagination: function(){
            var page = this.lostPagination.currentPage;
            var _self = this;
            this.$router.push({ name: _self.$route.name, params: {tab: _self.$route.params.tab}, query: {nav: 'lost', page: page}});
        }
    },
    methods: {
        getSharingBooks(){
            this.getBooks(true, false, false, this.sharingPagination.currentPage);
        },
        getNotSharingBooks(){
            this.getBooks(false, true, false, this.notSharingPagination.currentPage);
        },
        getLostBooks(){
            this.getBooks(false, false, true, this.lostPagination.currentPage);
        },
        refresh(){
            var _self = this;
            if(this.$route.query.nav==null){
                this.$router.replace({ name: _self.$route.name, params: {tab: _self.$route.params.tab}, query: {nav: 'sharing', page: _self.$route.query.page}});
            }
            if(this.$route.query.page==null){
                this.$router.replace({ name: _self.$route.name, params: {tab: _self.$route.params.tab}, query: {nav: _self.$route.query.nav, page: '1'}});
            }
            this.getBooksTotal();
            if (this.$route.query.nav == 'sharing') {
                this.sharingPagination = {currentPage: Number(this.$route.query.page)};
                this.sharingBooks = [];
                this.getSharingBooks();
            }
            if (this.$route.query.nav == 'notsharing') {
                this.notSharingPagination = {currentPage: Number(this.$route.query.page)};
                this.notSharingBooks = [];
                this.getNotSharingBooks();
            }
            if (this.$route.query.nav == 'lost') {
                this.lostPagination = {currentPage: Number(this.$route.query.page)};
                this.lostBooks = [];
                this.getLostBooks();
            }
        },
        getBooksTotal() {
            var _self = this;
            var lcs = CONST.USER_BOOK_SHELF_TOTAL;
            _self.$getItem(lcs).then(cacheData => {
                if (cacheData) {
                    if (_self.$route.query.nav == 'sharing') {
                        _self.sharingTotal = cacheData.sharingTotal;
                        if (_self.sharingTotal == 0) {
                            _self.sharingText = _self.label.sharingBookNA;
                        }
                    }
                    if (_self.$route.query.nav == 'notsharing') {
                        _self.notSharingTotal = cacheData.notSharingTotal;
                        if (_self.notSharingTotal == 0) {
                            _self.notSharingText = _self.label.notSharingBookNA;
                        }
                    }
                    if (_self.$route.query.nav == 'lost') {
                        _self.lostTotal = cacheData.lostTotal;
                        if (_self.lostTotal == 0) {
                            _self.lostText = _self.label.lostBookNA;
                        }
                    }
                }
            });
            var config = {
                headers: {
                    'Authorization': _self.loginToken
                }
            };
            _self.api.callGetApi(_self.api.api_selfget_book_instance_total, config)
                .then(response => {
                    if (_self.$route.query.nav == 'sharing') {
                        _self.sharingTotal = response.data.data.sharingTotal;
                        if (_self.sharingTotal == 0) {
                            _self.sharingText = _self.label.sharingBookNA;
                        }
                    }

                    if (_self.$route.query.nav == 'notsharing') {
                        _self.notSharingTotal = response.data.data.notSharingTotal;
                        if (_self.notSharingTotal == 0) {
                            _self.notSharingText = _self.label.notSharingBookNA;
                        }
                    }

                    if (_self.$route.query.nav == 'lost') {
                        _self.lostTotal = response.data.data.lostTotal;
                        if (_self.lostTotal == 0) {
                            _self.lostText = _self.label.lostBookNA;
                        }
                    }
                    //save response data to localstorage
                    _self.$setItem(lcs, response.data.data);
                })
                .catch(_self.handleErrorFunction);
        },
        getBooks(sharingFilter, notSharingFilter, lostFilter, page) {
            var lcs = "";
            var _self = this;
            //get data from localforage
            if (sharingFilter) {
                lcs = CONST.USER_BOOK_SHELF_SHARING + "_page_" + page;
                _self.$getItem(lcs).then(cacheData => {
                    if (!cacheData) {
                        _self.isShowLoading_1 = true;
                    }
                    _self.sharingBooks = _.chunk(cacheData, 4);
                })
            } else if (notSharingFilter) {
                lcs = CONST.USER_BOOK_SHELF_NOT_SHARING + "_page_" + page;
                _self.$getItem(lcs).then(cacheData => {
                    if (!cacheData) {
                        _self.isShowLoading_2 = true;
                    }
                    _self.notSharingBooks = _.chunk(cacheData, 4);
                })
            } else if (lostFilter) {
                lcs = CONST.USER_BOOK_SHELF_LOST + "_page_" + page;
                _self.$getItem(lcs).then(cacheData => {
                    if (!cacheData) {
                        _self.isShowLoading_3 = true;
                    }
                    _self.lostBooks = _.chunk(cacheData, 4);
                })
            }
            //get data from Service API
            var config = {
                params: {
                    sharingFilter: sharingFilter,
                    notSharingFilter: notSharingFilter,
                    lostFilter: lostFilter,
                    page: page,
                    per_page: _self.per_page
                },
                headers: {
                    'Authorization': _self.loginToken
                }
            };
            _self.api.callGetApi(_self.api.api_selfget_book_instance, config)
                .then(response => {
                    if (sharingFilter) {
                        _self.sharingBooks = _.chunk(response.data.data.resultInfo, 4);
                        _self.isShowLoading_1 = false;
                    }
                    if (notSharingFilter) {
                        _self.notSharingBooks = _.chunk(response.data.data.resultInfo, 4);
                        _self.isShowLoading_2 = false;
                    }
                    if (lostFilter) {
                        _self.lostBooks = _.chunk(response.data.data.resultInfo, 4);
                        _self.isShowLoading_3 = false;
                    }
                    _self.$setItem(lcs, response.data.data.resultInfo);
                })
                .catch(_self.handleErrorFunction);
        }
    },
    data() {
        return {
            per_page: CONST.USER_BOOK_ITEM_PERPAGE,

            //for sharing tab
            sharingPagination: {},
            sharingTotal: 0,
            sharingText: "",
            sharingBooks: [],

            //for not sharing tab
            notSharingPagination: {},
            notSharingTotal: 0,
            notSharingText: "",
            notSharingBooks: [],

            //for lost tab
            lostPagination: {},
            lostTotal: 0,
            lostText: "",
            lostBooks: []
        }

    }
}
</script>

<style>
</style>



