<template>
    <div class="container-gat reset-padding">
        <bookstop-info :bookstopInfo="bookstopInfo"></bookstop-info>
        <div class="row" style="margin-top:25px;">
            <div class="col-xs-3">
                <bookstop-nav></bookstop-nav>
                <bookstop-most-book style="margin:30px 0 10px 0;"></bookstop-most-book>
            </div>
            <div class="col-xs-9">
                <bookstop-book-list v-if="$route.params.tab=='books'" :bookstopName="bookstopInfo.name"></bookstop-book-list>
                <bookstop-image-list v-if="$route.params.tab=='images'" :bookstopInfo="bookstopInfo"></bookstop-image-list>
                <bookstop-event v-if="$route.params.tab=='events'"></bookstop-event>
            </div>
        </div>
    </div>
</template>

<script >
import BookstopBookList from '../components/BookstopBookList.vue'
import BookstopEvent from '../components/BookstopEvent.vue'
import BookstopImageList from '../components/BookstopImageList.vue'
import BookstopInfo from '../components/BookstopInfo.vue'
import BookstopMostBook from '../components/BookstopMostBook.vue'
import BookstopNav from '../components/BookstopNav.vue'
import BookService from '../../../services/BookService.js'
import Label from '../../../common/Label.js'
import * as CONST from '../../../common/const.js'
export default {
    name: 'bookstop-detail',
    mixins: [ BookService, Label ],
    components: { BookstopBookList, BookstopEvent, BookstopImageList, BookstopInfo, BookstopMostBook, BookstopNav },
    mounted() {
        if (this.$route.params.tab!='books'&&this.$route.params.tab!='images'&&this.$route.params.tab!='events') {
            this.$router.replace({ name: this.$route.name, params: {id: this.$route.params.id, tab: 'books'}});
        }
        this.getBookstopInfo();
    },
    methods: {
        getBookstopInfo() {
            //get data from Service API
            var _self = this;
            var config = {
                params: {
                    bookstopId: _self.$route.params.id
                }
            };
            _self.api.callGetApi(_self.api.api_get_bookstop_info, config)
                .then(response => {
                    _self.bookstopInfo = response.data.data.resultInfo;
                })
                .catch(_self.handleErrorFunction);
        }
    },
    data() {
        return {
            bookstopInfo: {}
        }
    }
}
</script>

<style lang="scss">
@import 'bookstopDetail.scss';
</style>



