<template>
    <gmap-map v-if="isLoaded"
        :center="center"
        :zoom="17"
        @click="changePosition"
        style="width: 100%; height: 100%">
        <gmap-marker
            :key="1"
            :position="positionTmp"
        ></gmap-marker>
    </gmap-map>
</template>

<script>
import * as CONST from '../../../common/const.js';
export default {
    props: ["userInfo"],
    mounted() {
        this.initLocation();
    },
    watch: {
        userInfo: function(){
            this.initLocation();
        }
    },
    methods: {
        changePosition(event) {
            this.positionTmp = {
                lat: event.latLng.lat(),
                lng: event.latLng.lng()
            }
            this.$emit('positionChanged', this.positionTmp);
        },
        //set init center
        initLocation() {
            if(this.userInfo.usuallyLocation[0].latitude && this.userInfo.usuallyLocation[0].longitude) {
                this.center = {
                    lat: this.userInfo.usuallyLocation[0].latitude,
                    lng: this.userInfo.usuallyLocation[0].longitude
                }
                this.isLoaded = true;
                this.positionTmp = this.center;
                this.$emit('positionChanged', this.positionTmp);
            }else{
                this.getLocation().then(this.displayCurrentLocaltion, this.displayError);
            }
        },
        //get current location
        getLocation() {
            var promiseObj = new Promise(function (handleSussessFunction, handleErrorFunction) {
                if (navigator.geolocation) {
                    navigator.geolocation.getCurrentPosition(handleSussessFunction, handleErrorFunction);
                }
            });
            return promiseObj;
        },
        displayCurrentLocaltion(position) {
            if (position) {
                this.center.lat = position.coords.latitude;
                this.center.lng = position.coords.longitude;
            }
            this.isLoaded = true;
            this.positionTmp = this.center;
            this.$emit('positionChanged', this.positionTmp);
        },
        displayError(error) {
            this.isLoaded = true;
            this.positionTmp = this.center;
            this.$emit('positionChanged', this.positionTmp);
            switch (error.code) {
                case error.PERMISSION_DENIED:
                    console.log("User denied the request for Geolocation.");
                    break;
                case error.POSITION_UNAVAILABLE:
                    console.log("Location information is unavailable.");
                    break;
                case error.TIMEOUT:
                    console.log("The request to get user location timed out.");
                    break;
                case error.UNKNOWN_ERROR:
                    console.log("An unknown error occurred.");
                    break;
            }
        }
    },
    data() {
        return {
            positionTmp: {},
            //flag to display gmap
            isLoaded: false,
            //deflault position
            center: CONST.QL1_COORDINATE
        }
    }
}
</script>

<style>
  
</style>



