<template>
    <div>
        <div class="row container-gat box-margin-top search-user-container">
            <div class="search-user-content-left inline-block">
                <app-gat></app-gat>
            </div>
            <div class="search-user-content-right inline-block">
                <div>
                    <a href="\" class="text-color-category medium-text-form-m">{{label.home_page}}</a>
                    <span class="text-color-category medium-text-form-m">
                        <i class="fa fa-angle-double-right fa-lg" aria-hidden="true"></i>
                    </span>
                    <span class="text-color-highlight medium-text-form-m">{{label.txt_result}}</span>
                    <div style="clear: both;"></div>
                </div>
                <div v-if="isShowLoading">
                    <img src="../../../../assets/images/loading.gif" style="width:50px; height: 50px;">
                </div>
                <div class="search-user-content-right-margin-top" v-else>
                    <span class="text-color-highlight super-biger-text-form-m text-style-bold">{{total}}</span>
                    <span class="medium-text-form-m"> {{label.txt_result_for}} </span>
                    <span class="text-color-highlight super-biger-text-form-m text-style-bold">'{{search_text}}'</span>
                </div>
                <div class="search-user-content-right-container" v-if="false == isShowLoading">
                    <div class="search-user-content-right-content ">
                        <router-link :title="user.name" :to="{ name: user.userTypeFlag==USER_TYPE_FLAG?label.NAME_USER_VISITOR_PAGE:label.NAME_BOOKSTOP_DETAIL_PAGE, params: {id: user.userId, tab: user.userTypeFlag==USER_TYPE_FLAG?'book-shelf':'books'}}" class="search-user-result" v-for="(user, index) in users" :key="user.userId">
                            <search-user-item :name="user.name" :imageId="user.imageId" :address="user.address" :readCount="user.readCount" :sharingCount="user.sharingCount"></search-user-item>
                        </router-link>
                        <div style="clear: both;"></div>
                    </div>
                    <div class="text-center">
                        <uib-pagination :boundary-links="true" :force-ellipses="false" :boundary-link-numbers="true" previous-text="<" next-text=">" first-text="<<" last-text=">>" v-model="pagination" :items-per-page="config.params.per_page" :total-items="total" :max-size="4" :rotate="true" @change="getUsers">
                        </uib-pagination>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import AppGat from '../../../common/appGat/AppGat.vue';
import SearchUserItem from '../components/search_user_item/SearchUserItem.vue';
import BookService from '../../../../services/BookService.js';
import CommonService from '../../../../services/CommonService.js';
import Label from '../../../../common/Label.js';
import * as CONST from '../../../../common/const.js';

export default {
    name: 'search-user',
    components: { AppGat, SearchUserItem },
    mixins: [CommonService, BookService, Label],
    data() {
        return {
            users: [],
            USER_TYPE_FLAG: CONST.USER_TYPE_FLAG,
            search_text: '',
            config: {
                params: {
                    per_page: 18,
                    page: 1,
                    name: ''
                }
            },
            total: 0,
            pagination: { currentPage: 1 },
        }
    },
    mounted() {
        this.initData();
    },
    methods: {
        initData() {
            this.isShowLoading = true;
            this.search_text = this.$route.query.str;
            this.resetData();
            this.getTotal();
            this.callAPISearch();
        },
        resetData() {
            this.config = {
                params: {
                    per_page: 18,
                    page: 1
                }
            };
            this.total = 0;
        },
        setTotal(response) {
            if (200 == response.status) {
                var responseData = response.data;
                this.total = responseData.data.totalResult;
                this.isShowLoading = false;
            }
        },
        getTotal() {
            // Get total
            var data = {
                name: this.search_text
            };
            this.api.callPostApi(this.api.api_search_user_total, data, {}).then(this.setTotal, this.handleErrorFunction);
        },
        setListUsers(response) {
            if (200 == response.status) {
                var responseData = response.data;
                this.users = responseData.data.resultInfo;
            }
        },
        callAPISearch() {
            //Call api to search by title
            this.config.params.name = this.search_text;
            this.api.callGetApi(this.api.api_search_user, this.config).then(this.setListUsers, this.handleErrorFunction);
        },
        getUsers() {
            this.config.params.page = this.pagination.currentPage;
            this.callAPISearch();
        }
    },
    watch: {
        '$route': function() {
            this.initData();
        }
    }
}
</script>
,
<style lang="scss">
@import '../../../../scss/common_styles.scss';
@import 'searchUser.scss';
</style>



