<template>
    <modal :name="dialogName" :clickToClose="false" :width="480" :height="590">
        <div class="dialog-title biger-text-form-m">
            <button class="close" @click="reject">&times;</button>
            <div>{{label.borrow_dialog_title}}</div>
        </div>

        <div style="margin: 15px;height:52px;">
            <!--user-icon-->
            <div class="display-left target-user-border"> 
                <user-icon
                    :userIconStyle="userIconStyle"
                    :imageId="targetUser.imageId">
                </user-icon>
            </div>
            <div class="display-left" style="margin-left: 15px;width:380px">
                <!--user-name-->
                <div class="half-row-top">
                    <span class="medium-text-form-m text-limit text-muted title-name-color">{{targetUser.name}}</span>
                </div>
                <!--address-->
                <div class="half-row-bottom text-limit small-text-form-r">
                    {{targetUser.address}}
                </div>
            </div>
        </div>

        <div class="clear-left" style="margin: 25px 15px;height: 140px;">
            <div class="display-left" style="width:100px;height:140px">
                <img :title="book.title" :src="getUrlImage(book.imageId)" class="image-fill img-book" :alt="book.title">
            </div>

            <div class="display-left" style="margin-left:15px;width:335px">
                <!-- book title -->
                <div>
                    <span class="biger-text-form-m text-limit text-muted title-name-color">{{book.title}}</span>
                </div>
                <!-- author name -->
                <div class="medium-text-form-r text-limit text-muted text-color-author" style="margin-top:6px;">
                    {{book.author}}
                </div>
                <!-- rate avg -->
                <div style="height:35px;">
                    <div class="display-left" style="margin-top:10px;">
                        <star-rating 
                            :rating="book.rateAvg" 
                            :read-only="true" 
                            :increment="0.1" 
                            :show-rating="false" 
                            inactive-color="white" 
                            border-color="#f4c312" 
                            :border-width="1"
                            :star-size="16">
                        </star-rating>
                    </div>
                    <span class="display-left medium-text-form-r text-color-author" 
                        style="margin-left:20px;margin-top:12px;">
                        {{this.avgRound(book.rateAvg)}}
                    </span>
                </div>
            </div>
        </div>

        <div style="margin: 15px;">
            <div>{{label.message_to_owner}}</div>
            <div style="margin-top: 10px;">
                <textarea style="background-color:#eff9ff" :id="'message-form-' + book.instanceId" class="form-control review-form" :placeholder="label.borrow_message_placeholder" rows="4" v-model="message"></textarea>
            </div>
        </div>

        <div style="margin: 15px;">
            <div class="form-group">
                <label style="margin-bottom:10px;" for="sel1">{{label.expectation_borrow}}</label>
                <select class="form-control" id="sel1" v-model="expectation">
                    <option value=0>3 {{label.day}}</option>
                    <option value=1>1 {{label.week}}</option>
                    <option value=2>2 {{label.week}}</option>
                    <option value=3>3 {{label.week}}</option>
                    <option value=4>1 {{label.month}}</option>
                </select>
            </div>
        </div>

        <!-- <div v-html="content" class="confirm-dialog-content"></div> -->
        <div style="margin:30px 15px;">
            <button v-on:click="accept" class="btn btn-gat btn-circle display-left" style="width:100%;">{{label.requestTime}}</button>
        </div>
    </modal>
</template>

<script>
import UserIcon from '../../common/userIcon/UserIcon.vue'
import StarRating from 'vue-star-rating'
import Label from '../../../common/Label.js'
import BookService from '../../../services/BookService.js'
import CommonService from '../../../services/CommonService.js'
import * as CONST from '../../../common/const.js'

//firebase db (tabel users) refer
let userRef = window.firebaseDB.ref('users');
//firebase db (tabel messages) refer
let messagesRef = window.firebaseDB.ref('messages');

export default {
    props: ["dialogName", "isOpen", "book"],
    components: { UserIcon, StarRating },
    mixins: [BookService, CommonService, Label],
    mounted() {
        
    },
    computed: {
        //get message child node from login user id and target user id
        childNode: function() {
            let loginUserId = localStorage.getItem(CONST.USER_ID);
            let targetUserId = this.book.userId;
            return loginUserId*1 > targetUserId*1? targetUserId+":"+loginUserId:loginUserId+":"+targetUserId;
        }
    },
    watch: {
        isOpen: function(){
            if(this.isOpen){
                this.$modal.show(this.dialogName);
                this.getTargetUser();
            }else{
                this.$modal.hide(this.dialogName);
            }
        }
    },
    methods: {
        accept() {
            this.sendBorrowRequest();
            this.$emit('dialogClosed');
            this.$emit('requestSent');
        },
        reject() {
            this.$emit('dialogClosed');
        },
        //round float to 1 decima place
        avgRound(val){
            return val?  val.toFixed(1):null;           
        },
        getTargetUser() {
            var _self = this;
            _self.$getItem(CONST.VISITOR_USER).then(vu => {
                if (!vu) {
                    vu = [];
                }
                var targetUser = vu.filter(function(i) {
                    return i.userId == _self.book.userId;
                });
                if (targetUser.length > 0) {
                    _self.targetUser = {
                        name: targetUser[0].name,
                        imageId: targetUser[0].imageId,
                        address: targetUser[0].address
                    }
                } else {
                    _self.getUserDetail();
                }
            });
        },
        getUserDetail() {
            var _self = this;
            var config = {
                params: {
                    userId: _self.book.userId
                }
            };
            _self.api.callGetApi(_self.api.api_get_username_image, config)
                .then(response => {
                        _self.targetUser = {
                            name: response.data.data.resultInfo.name,
                            imageId: response.data.data.resultInfo.imageId,
                            address: response.data.data.resultInfo.address
                        }
                        //update visitor list in localforage
                        _self.$getItem(CONST.VISITOR_USER).then(visitorUser => 
                            {
                                if (!visitorUser) {
                                    visitorUser = [];
                                }
                                var newVisitorUser = visitorUser.filter(function(i) {
                                    return i.userId != response.data.data.resultInfo.userId;
                                });
                                newVisitorUser.push(response.data.data.resultInfo);
                                _self.$setItem(CONST.VISITOR_USER, newVisitorUser);
                            }
                        );
                    })
                .catch(_self.handleErrorFunction);
        },
        sendBorrowRequest() {
            //insert firebase db
            if (this.message) {
                //insert into users table
                userRef.child(localStorage.getItem(CONST.USER_ID)).child(this.childNode).set(true);
                userRef.child(this.book.userId).child(this.childNode).set(true);
                //insert into messages table
                messagesRef.child(this.childNode).push({
                    isRead: false,
                    message: this.message,
                    userId: localStorage.getItem(CONST.USER_ID),
                    timeStamp: new Date().getTime()
                });
                //reset input to blank
                this.message = "";
            }

            //call create request API
            var params = {
                bookId: this.book.bookId,
                editionId: this.book.editionId,
                ownerId: this.book.userId,
                expectation: this.expectation
            };
            var headers = {
                'Authorization': this.loginToken
            }
            this.api.callPostApi(this.api.api_create_request, params, {headers})
                .then(response => {
                    //update requesting status to 1
                    this.book.requestingStatus = 1;
                    //update request id
                    this.book.recordId = response.data.data.resultInfo.recordId;
                    //update request status
                    this.book.recordStatus = response.data.data.resultInfo.recordStatus;
                    this.$emit('requestComplete');
                })
                .catch(error => {
                    this.$emit('dialogClosed');
                    this.$emit('requestFailed', error.response.data.message);
                });
        }
    },
    data() {
        return {
            expectation: "1",
            message: "",
            targetUser: {},
            //style of user icon
            userIconStyle: {
                'background-color': 'white',
                'border-radius': '22px',
                'width': '44px',
                'height': '44px',
                'margin': '2px auto'
            }
        }
    }
}
</script>

<style>
.close {
    position: absolute;
    top: 13px;
    right: 15px;
}
</style>



