<template>
    <div>
        <error-dialog :errorDialogName="'share-book-near-error-dialog'" :isErrorOpen="isShowErrorDialog" :errorMessage="label.msg_no_get_current_position" :errorStatus="errorStatus"></error-dialog>
        <div class="row container-gat box-margin-top user-near-you-container">
            <div class="user-near-you-content-left inline-block">
                <app-gat></app-gat>
            </div>
            <div class="user-near-you-content-right inline-block">
                <div>
                    <a href="\" class="text-color-category medium-text-form-m">{{label.home_page}}</a>
                    <span class="text-color-category medium-text-form-m">
                        <i class="fa fa-angle-double-right fa-lg" aria-hidden="true"></i>
                    </span>
                    <span class="text-color-highlight medium-text-form-m">{{label.sharing_user}}</span>
                    <div style="clear: both;"></div>
                </div>
                <div v-if="isShowLoading">
                    <img src="../../../assets/images/loading.gif" style="width:50px; height: 50px;">
                </div>
                <div class="user-near-you-content-right-container">
                    <div class="user-near-you-maps">
                        <div class="my-location-box" v-show="isReadyGoogle">
                            <button class="my-location-btn" title="Your Location" v-on:click="clickMyLocationButton">
                                <div class="my-location-img" id="my_location_img">
                                </div>
                            </button>
                        </div>
                        <gmap-map :center="center" @center_changed="updateCenter" @bounds_changed="boundsChanged" @dragstart="dragStart" @dragend="dragEnd" :options="options" :zoom="15" class="user-near-you-maps">
                            <gmap-marker :position="currentLocation" :icon="getIconCurrentLocation()" :clickable="true" :draggable="false" @click="clickCurrentLocationOnMap">
                            </gmap-marker>

                            <gmap-marker :key="index" v-for="(user, index) in users" :options="{styles: this.styles}" :icon="getIconUser()" :position="getPosition(index)" :clickable="true" :draggable="false" @click="center=getPosition(index)">
                            </gmap-marker>
                        </gmap-map>
                    </div>
                    <div class="user-near-you-content-right-content ">
                        <router-link :title="user.name" :to="{ name: user.userTypeFlag==USER_TYPE_FLAG?label.NAME_USER_VISITOR_PAGE:label.NAME_BOOKSTOP_DETAIL_PAGE, params: {id: user.userId, tab: user.userTypeFlag==USER_TYPE_FLAG?'book-shelf':'books'}}"
                            class="user-near-you-result" v-for="user in users" :key="user.userId">
                            <user-near-you-item :user="user" :currentLocation="currentLocation"></user-near-you-item>
                        </router-link>
                        <div style="clear: both;"></div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
import AppGat from '../../common/appGat/AppGat.vue';
import UserNearYouItem from '../components/user_near_you_item/UserNearYouItem.vue';

import BookService from '../../../services/BookService.js';
import CommonService from '../../../services/CommonService.js';
import Label from '../../../common/Label.js';
import * as CONST from '../../../common/const.js';

export default {
    name: 'user-near-you',
    components: { AppGat, UserNearYouItem },
    mixins: [CommonService, BookService, Label],
    data() {
        return {
            users: [],
            USER_TYPE_FLAG: CONST.USER_TYPE_FLAG,
            center: CONST.QUOCLO_LOCATION,
            currentLocation: CONST.QUOCLO_LOCATION,
            markers: [],
            userIconStyle: {
                'background-color': 'white',
                'border-radius': '25px',
                'width': '50px',
                'height': '50px',
                'margin-top': '2px'
            },
            map: null,
            isDrag: false,
            styles: {
                maxZoom: 20,
                minZoom: 15,
                mapTypeId: 'hybrid'
            },
            handleSuccess: this.setListUser,
            options: {
                disableDefaultUI: false,
                scrollwheel: false,
                streetViewControl: false
            },
            animationInterval: null,
            isShowErrorDialog: false,
            isReadyGoogle: false,
        }
    },
    mounted() {
        //After 6s, if can not load SDK google, set default location
        var self = this;
        setTimeout(function() {
        if (null == window.isRuningLoadSDKGoogle && window.isReadyGoogle) {
            self.isReadyGoogle = window.isReadyGoogle;
            // If current location is null, set default location
            if (self.isNull(window.currentLocation)) {
            window.currentLocation = CONST.QUOCLO_LOCATION;
            }
            self.setCurrentLocation(window.currentLocation);
        }
        }, 6000);
    },
    methods: {
        setListUser(response) {
            if (200 == response.status) {
                var responseData = response.data;
                this.users = responseData.data.resultInfo;
            }
        },
        getCurrentLocation() {
            var self = this;
            // Get current location
            this.getLocation().then(this.displayCurrentLocaltion, function(error) {
                self.displayError(error);
                if (self.isNotNull(self.animationInterval)) {
                    clearInterval(self.animationInterval);
                    $('#my_location_img').css('background-position', '0px 0px');
                }
            });
        },
        addToListUser(response) {
            if (200 == response.status) {
                var responseData = response.data;
                this.users = this.users.concat(responseData.resultInfo);
            }
        },
        getPosition(index) {
            return { lat: this.users[index].latitude, lng: this.users[index].longitude };
        },
        dragStart() {
            this.isDrag = true;
        },
        dragEnd() {
            this.isDrag = false;
            $('#my_location_img').css('background-position', '0px 0px');
            this.updateListUser();
        },
        boundsChanged(map) {
            this.map = map;
            if (this.isDrag == false) {
                this.updateListUser();
            }
        },
        clickMyLocationButton() {
            var imgX = '0';
            this.animationInterval = setInterval(function() {
                if (imgX == '-18') imgX = '0';
                else imgX = '-18';
                $('#my_location_img').css('background-position', imgX + 'px 0px');
            }, 500);
            this.center = {
                lat: this.currentLocation.lat,
                lng: this.currentLocation.lng
            }
            if (navigator.geolocation) {
                this.getCurrentLocation();
            }
            else {
                console.log("Geolocation is not supported by this browser.");
                clearInterval(this.animationInterval);
                $('#my_location_img').css('background-position', '0px 0px');
            }
        },
        updateCenter(center) {
            this.center = {
                lat: center.lat(),
                lng: center.lng()
            }
        },
        clickCurrentLocationOnMap() {
            this.center.lat = this.currentLocation.lat;
            this.center.lng = this.currentLocation.lng;
        },
        updateListUser() {
            this.getUser(10);
        },
        getUser(per_page) {
            var map = this.map;
            if (this.isNotNull(map) && this.center.lat > 0 && this.center.lng > 0) {
                var config = {
                    params: {
                        per_page: per_page,
                        page: 1,
                        currentLat: this.center.lat,
                        currentLong: this.center.lng,
                        neLat: map.f.f,
                        neLong: map.b.f,
                        wsLat: map.f.b,
                        wsLong: map.b.b
                    }
                }
                this.api.callGetApi(this.api.api_near_user, config).then(this.handleSuccess, this.displayError);
            }
        },
        showMoreUser() {
            var per_page = this.users.length;
            per_page += 10;
            this.getUser(per_page);
        },
        goToCenterInMap(position) {
            return position;
        },
        getIconUser() {
            if(this.isReadyGoogle){
                var icon = {
                    url: this.getLinkImageLocation('other_user'),
                    scaledSize: new google.maps.Size(40, 40)
                }
                return icon;
            }
            return '';
        },
        getIconCurrentLocation() {
            if(this.isReadyGoogle){
                var icon = {
                    url: this.getLinkImageLocation('current_user'),
                    scaledSize: new google.maps.Size(50, 50)
                }
                return icon;
            } 

            return '';
        },
        displayCurrentLocaltion(position) {
            var currentLocation = this.currentLocation;
            currentLocation.lat = position.coords.latitude;
            currentLocation.lng = position.coords.longitude;
            this.setCurrentLocation(currentLocation);
        },
        setCurrentLocation(currentLocation){
            if (this.isNotNull(currentLocation)) {
                this.currentLocation.lat = currentLocation.lat;
                this.currentLocation.lng = currentLocation.lng;

                this.center.lat = currentLocation.lat;
                this.center.lng = currentLocation.lng;
                if (this.isNotNull(this.animationInterval)) {
                    clearInterval(this.animationInterval);
                    $('#my_location_img').css('background-position', '-144px 0px');
                }
            }
        }
    },
}
</script>
,
<style lang="scss">
@import '../../../scss/common_styles.scss';
@import 'userNearYou.scss';
</style>



