<template>
    <div class="forgot-form box-shadow">
        <loading-dialog :loadingDialogName="'input-email-loading'" :isLoadingOpen="isSystemExecuting"></loading-dialog>
        <div class="forgot-title biger-text-form-m">
            {{label.forgot_pass_1}}&nbsp( 1 )
        </div>
        <div class="align-center" style="margin-top:30px;">
            {{label.forgot_notice_1}}
        </div>
        <div style="margin-top:30px;">
            <input v-model="confirm_email" type="text" class="form-control gat-form" :placeholder="label.register_email_address">
        </div>
        <div style="margin-top:34px;">
            <button v-on:click="verifyEmail" type="button" class="btn btn-gat btn-circle btn-forgot">
                {{label.send}}
            </button>
        </div>
    </div>
</template>

<script>
import Label from '../../../../common/Label.js'
import LoadingDialog from '../../../common/dialog/LoadingDialog.vue'
import BookService from '../../../../services/BookService.js'
import * as CONST from '../../../../common/const.js'
export default {
    mixins: [ Label, BookService ],
    components: { LoadingDialog },
    methods: {
        verifyEmail() {
            this.isSystemExecuting = true;
            var data = {
                email: this.confirm_email
            };
            this.api.callPostApi(this.api.api_request_reset_password, data, {})
                .then(response => {
                    this.$emit("setTokenReset", response.data.data.tokenResetPassword);
                    this.$router.push({ name: CONST.NAME_FORGOT_PASS_PAGE, params: {tab: 'input-verify'}});
                    this.isSystemExecuting = false;
                })
                .catch(this.handleErrorFunction);
        }
    },
    data() {
        return {
            confirm_email: ""
        }
    }
}
</script>

<style>

</style>



