<template>
    <div class="bookstop-nav-form">
        <div class="row reset-margin most-book-title">
            <div class="col-xs-8 align-center medium-text-form-m">{{label.most_book}}</div>
            <div class="col-xs-4 align-center paging-button-group">
                <div v-on:click="back" class="inline-block paging-button" :class="{'active':currentPage!=1}">
                    <span class="fa fa-angle-left fa-angle-left-custom"></span>
                </div>
                <div v-on:click="next" class="inline-block paging-button" :class="{'active':currentPage!=maxPage}" style="margin-left:2px">
                    <span class="fa fa-angle-right fa-angle-right-custom"></span>
                </div>
            </div>
        </div>
        <div v-for="(splitedBook,outerIndex) in mostBooks" :key="outerIndex">
            <div v-if="currentPage == outerIndex + 1"  class="">
                <div v-for="(book,innerIndex) in splitedBook" :key="innerIndex">
                    <book-in-bookstop :book="book" :inMain="false" class="mostbook-layout"></book-in-bookstop>
                    <div v-if="innerIndex!=maxPage-1" class="mostbook-split-border"></div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import BookInBookstop from '../components/BookInBookstop.vue'
import BookService from '../../../services/BookService.js'
import Label from '../../../common/Label.js'
import * as CONST from '../../../common/const.js'
export default {
    mixins: [ BookService, Label ],
    components: { BookInBookstop },
    mounted() {
        this.getMostBooks();
    },
    methods: {
        getMostBooks() {
            //get data from Service API
            var _self = this;
            var config = {
                params: {
                    ownerId: _self.$route.params.id,
                    page: 1,
                    per_page: 9,
                    userId: _self.loginUserId
                }
            };
            _self.api.callGetApi(_self.api.api_get_user_sharing_editions, config)
                .then(response => {
                    _self.mostBooks = _.chunk(response.data.data.resultInfo, 3);
                })
                .catch(_self.handleErrorFunction);
        },
        next() {
            if (this.currentPage < this.maxPage) {
                this.currentPage += 1;
            }
        },
        back() {
            if (this.currentPage > 1) {
                this.currentPage -= 1;
            }
        }
    },
    data() {
        return {
            mostBooks: [],
            currentPage: 1,
            maxPage: 3
        }
    }

}
</script>

<style>

</style>



