<template>
    <div class="social-login-box">
        <loading-dialog :loadingDialogName="'social-login-loading'" :isLoadingOpen="isSystemExecuting"></loading-dialog>
        <error-dialog
            :errorDialogName="'social-login-fail'"
            :isErrorOpen="isApiserviceErrorDialogOpen"
            :errorMessage="apiErrorDialogContent"
            :errorStatus="errorStatus"
            @errorDialogClosed="isApiserviceErrorDialogOpen = false"></error-dialog>
        <div class="gat-icon-container">
            <img :src="require('../../../../assets/images/icon_gatBook_white.png')" class="gat-icon">
        </div>
        <div class="info-container">
            <span>{{ label.txt_signin_info }}</span>
        </div>
        <div class="social-button-group">
            <div>
                <fb-signin-button 
                    type="button" 
                    :params="fbSignInParams"
                    @success="onFbSignInSuccess"
                    @error="onFbSignInError"
                    class="btn btn-gat btn-circle btn-white-border btn-social">
                    <div class="row" style="padding-top:2.5px;">
                        <div class="col-xs-5">
                            <span class="fa fa-facebook" style="font-size:18px"></span>
                        </div>
                        <div class="col-xs-7 nopadding">
                            <span>{{label.facebook}}</span>
                        </div>
                    </div>
                </fb-signin-button>
            </div>
            <div style="margin-top:25px;">
                <a v-bind:href="twitter_authenticate">
                    <button 
                        type="button" 
                        class="btn btn-gat btn-circle btn-white-border btn-social">
                        <div class="row">
                            <div class="col-xs-5">
                                <span class="fa fa-twitter" style="font-size:18px"></span>
                            </div>
                            <div class="col-xs-7 nopadding">
                                <span>{{label.twitter}}</span>
                            </div>
                        </div>
                    </button>
                </a>
            </div>
            <div style="margin-top:25px;">
                <g-signin-button 
                    type="button" 
                    :params="googleSignInParams"
                    @success="onGgSignInSuccess"
                    @error="onGgSignInError"
                    class="btn btn-gat btn-circle btn-white-border btn-social">
                    <div class="row" style="padding-top:2.5px;">
                        <div class="col-xs-5">
                            <span class="fa fa-google-plus" style="font-size:18px"></span>
                        </div>
                        <div class="col-xs-7 nopadding">
                            <span>{{label.google}}</span>
                        </div>
                    </div>
                </g-signin-button>
            </div>
        </div>

    </div>
</template>

<script>
import Label from '../../../../common/Label.js'
import LoadingDialog from '../../../common/dialog/LoadingDialog.vue'
import BookService from '../../../../services/BookService.js'
import * as CONST from '../../../../common/const.js'
//do stuff
export default {
    mixins: [ BookService, Label ],
    components: { LoadingDialog },
    mounted() {
        //get param oauth_token & oauth_verifier from current url
        var oauth_token = this.$route.query.oauth_token;
        var oauth_verifier = this.$route.query.oauth_verifier;
        if(oauth_token && oauth_verifier){
            //if oauth_token & oauth_verifier are existed, check user is loged in or not
            var token = localStorage.getItem(CONST.LOGIN_TOKEN);
            if (!token) {
                //if not login, call api_get_twitter_user
                var config = {
                    params: {
                        oauthToken: oauth_token,
                        oauthVerifier: oauth_verifier
                    }
                }
                this.api.callGetApi(this.api.api_get_twitter_user, config).then(this.twLogin, this.handleErrorFunction);
            } else {
                //if loged in, change to user profile page
                this.$router.push({ name: CONST.NAME_USER_VISITOR_PAGE, params: {id: localStorage.getItem(CONST.USER_ID), tab: 'info-setting'}, query: {oauth_token: oauth_token, oauth_verifier: oauth_verifier}});
            }
            
        }else{
            //esle, do get oauth_token
            this.api.callGetApi(this.api.api_get_twitter_oauth_token, {})
            .then(response => {this.twitter_authenticate = CONST.TWITTER_OAUTH_URL + "?" + response.data.data.resultInfo}, this.handleErrorFunction);
        }
    },
    methods: {
        //do signin GaT system by facebook
        onFbSignInSuccess (response) {
            FB.api('/me', dude => {
                console.log(dude);
                this.isSystemExecuting = true;
                this.signinParams = {
                    socialID: dude.id,                  //facebook id
                    socialType: CONST.FB_TYPE,          //1
                    name: dude.name,                    //facebook name
                    uuid: window.uuid
                };
                this.api.callPostApi(this.api.api_login_by_social, this.signinParams, {})
                    .then(this.initData)                //after login successful
                    .catch(this.socialSignUp);          //if login false, do signup
            })
        },
        //facebook login error
        onFbSignInError (error) {
            console.log('Fb error: ', error)
        },
        //do signin GaT system by twitter
        twLogin(response){
            this.isSystemExecuting = true;
            const queryString = require('query-string');
            var parsed = queryString.parse(response.data.data.resultInfo);
            this.signinParams = {
                socialID: parsed.user_id,               //twitter id
                socialType: CONST.TW_TYPE,              //3
                name: parsed.screen_name,               //twitter name
                uuid: window.uuid
            };
            this.api.callPostApi(this.api.api_login_by_social, this.signinParams, {})
                .then(this.initData)                    //after login successful
                .catch(this.socialSignUp);              //if login false, do signup
        },
        //do signin GaT system by google
        onGgSignInSuccess (googleUser) {
            this.isSystemExecuting = true;
            var profile = googleUser.getBasicProfile();
            this.signinParams = {
                socialID: profile.Eea,                  //google id
                socialType: CONST.GG_TYPE,              //2
                name: profile.ig,                       //google name
                email: profile.U3,                      //gmail
                uuid: window.uuid

            };
            this.api.callPostApi(this.api.api_login_by_social, this.signinParams, {})
                .then(this.initData)                    //after login successful
                .catch(this.socialSignUp);              //if login false, do signup
        },
        //google login error
        onGgSignInError (error) {
            console.log('Gg error: ', error);
        },
        //do signup GaT system by social account
        socialSignUp(error){
            if (CONST.UNAUTHORIZED_STT == error.response.status){
                this.api.callPostApi(this.api.api_register_by_social, this.signinParams, {})
                .then(this.initData)
                .catch(this.handleErrorFunction);
            } else {
                console.log(error.response.data);
            }
        },
        //init data when login success
        initData(response) {
            var _self = this;
            //login to firebase by general account
            firebase.auth().signInWithEmailAndPassword(CONST.GENERAL_FIREBASE_EMAIL, CONST.GENERAL_FIREBASE_PASSWORD).catch(function(error) {
                // Show error log
                console.log(error.code);
                console.log(error.message);
            });
            if (response.data.data.loginToken) {
                localStorage.setItem(CONST.LOGIN_TOKEN, response.data.data.loginToken);
            }
            var config = {
                headers: {
                    'Authorization': response.data.data.loginToken
                }
            };
            _self.api.callGetApi(_self.api.api_get_user_private_info, config)
                .then(response => {
                        localStorage.setItem(CONST.USER_ID, response.data.data.resultInfo.userId);
                        if(response.data.data.resultInfo.usuallyLocation.length==0){
                            response.data.data.resultInfo.usuallyLocation[0] = {};
                        }
                        _self.$setItem(CONST.LOGIN_USER, response.data.data.resultInfo);
                        _self.$router.push({ name: "home"});
                        _self.isSystemExecuting = false;
                    })
                .catch(_self.handleErrorFunction);
            _self.$emit('firebaseRegister');
        }

    },
    data() {
        return {
            twitter_authenticate: '',
            fbSignInParams: {
                scope: '',
                return_scopes: true
            },
            googleSignInParams: {
                client_id: CONST.GOOGLE_CLIENT_ID
            },
            signinParams: {}
        }
    }
}
</script>

<style>

</style>



