<template>
    <modal :name="loadingDialogName" :clickToClose="false" :width="360" :height="100" class="dialog-eclipse">
        <div class="row">
            <div class="col-xs-3">
                <img src="../../../assets/images/sys_loading.gif" style="margin:15px;width:auto;height:70px">
            </div>
            <div class="col-xs-9" style="padding-top:30px" v-html="label.sys_executing">
                
            </div>
        </div>
    </modal>
</template>

<script>
import Label from '../../../common/Label.js'
export default {
    props: ["isLoadingOpen", "loadingDialogName"],
    mixins: [Label],
    watch: {
        isLoadingOpen: function(){
            if(this.isLoadingOpen){
                this.$modal.show(this.loadingDialogName);
            }else{
                this.$modal.hide(this.loadingDialogName);
            }
        }
    },
}
</script>

<style>

</style>



