<template>
    <div class="box-block">
        <div class="book-detail-list-user-review-icon">
            <user-icon :userIconStyle="userIconStyle" :imageId="imageId"></user-icon>
        </div>
        <div class="book-detail-list-user-review-info">
            <p class="biger-text-form-m text-color-default book-detail-txt-margin-bottom">{{name}}</p>
            <star-rating :rating="value" :read-only="true" :increment="0.01" :show-rating="false" inactive-color="white" border-color="#f4c312" :border-width="1" :star-size="15">
            </star-rating>
            <span  v-if="isShowLimitReview" class="medium-text-form-r text-color-author book-detail-txt-margin-top">{{getContentLimitLength(review, maxLengthOfReview)}}</span>
            <p class="medium-text-form-r text-color-author " v-else>
                <template v-for="line in review.split('\n')" v-if="'' != line">{{line}}<br></template>
            </p>
            <span v-if="isShowLimitReview" class="medium-text-form-r text-color-highlight book-detail-descript-txt-show-more" @click="showMoreReview">{{label.txt_show_more}}</span>
        </div>
        <div class="book-detail-list-user-review-action">
            <p class="small-text-form-r text-color-author">{{getDateReview(evaluationTime)}}</p>
            <button class="btn-no-border book-detail-list-user-review-btn-edit" @click="editReview" v-if="isShowEditReview">
                <i class="fa fa-pencil" aria-hidden="true"></i>
            </button>
        </div>
        <div style="clear: both;"></div>
    </div>
</template>

<script>
import * as moment from 'moment';
import UserIcon from '../../../common/userIcon/UserIcon';
import StarRating from 'vue-star-rating';
import CommonService from '../../../../services/CommonService.js';
import Label from '../../../../common/Label.js';

export default {
    name: 'book-detail-user-review-item',
    components: { StarRating, UserIcon },
    mixins: [CommonService, Label],
    data() {
        return {
            userIconStyle: {
                'background-color': 'white',
                'border-radius': '18px',
                'width': '50px',
                'height': '50px',
                'float': 'left',
            },
            isShowLimitReview: false,
            maxLengthOfReview: 185
        };
    },
    mounted(){
        if(this.isNotNull(this.review) && this.review.length >= this.maxLengthOfReview){
            this.isShowLimitReview = true;
        }
    },
    methods: {
        getDateReview(time) {
            var date = moment(time);
            return date.format("DD MMM");
        },
        editReview() {
            this.$emit('editReview');
        },
        showMoreReview() {
            this.isShowLimitReview = false;
        }
    },
    props: {
        imageId: {
            require: true,
            default: ''
        },
        name: {
            require: true,
            default: ''
        },
        value: {
            require: true,
            default: 0
        },
        review: {
            require: true,
            default: ''
        },
        evaluationTime: {
            require: true,
            default: ''
        },
        isShowEditReview: {
            required: false,
            default: false
        }
    },
}
</script>

<style lang="scss">
@import '../../../../scss/common_styles.scss';
@import 'listUserReviewItem.scss';
</style> 


