<template>
    <div class="book-detail-content-user-review-form">
        <div class="book-detail-list-user-review-icon">
            <user-icon :userIconStyle="userIconStyle" :imageId="imageId"></user-icon>
        </div>
        <div class="book-detail-list-user-review-form-info">
            <textarea id="editReview" class="form-control book-detail-list-user-review-form-input" :rows="maxRowReview" v-model="review"></textarea>
            <div class="row book-detail-list-user-review-form-action">
                <div class="col-xs-6 ">
                    <div class="book-detail-review-rate-box">
                        <div class="medium-text-form-m book-detail-list-user-review-txt-rate">
                            {{label.txt_rate_start}}
                        </div>
                        <div class="book-detail-list-user-review-rate-start ">
                            <star-rating @rating-selected="setRateBook" :rating="rate" :increment="rateIncrement" :show-rating="false" inactive-color="white" border-color="#f4c312" :border-width="1" :star-size="14">
                            </star-rating>
                        </div>
                    </div>
                </div>
                <div class="col-xs-6 reset-padding" v-if="isUpdate">
                    <button class="btn-no-border btn-gat-cancel book-detail-list-user-review-cancel" @click="$emit('cancel')">
                        {{label.txt_cancel_update_review}}
                    </button>
                    <button class="btn-no-border book-detail-list-user-review-create" @click="addReview">
                        {{label.txt_update_review}}
                    </button>
                </div>
                <div class="col-xs-6 reset-padding" v-else>
                    <button class="btn-no-border book-detail-list-user-review-create" @click="addReview">
                        {{label.txt_create_review}}
                    </button>
                </div>
            </div>
        </div>
    </div>
</template>


<script>

import UserIcon from '../../../common/userIcon/UserIcon';
import CommonService from '../../../../services/CommonService.js';
import BookService from '../../../../services/BookService.js';
import StarRating from 'vue-star-rating';

import Label from '../../../../common/Label.js';
import * as CONST from '../../../../common/const.js';
import * as moment from 'moment';

export default {
    name: 'list-user-create-review',
    components: { UserIcon, StarRating },
    mixins: [CommonService, StarRating, Label],
    data() {
        return {
            userIconStyle: {
                'background-color': 'white',
                'border-radius': '18px',
                'width': '50px',
                'height': '50px',
                'float': 'left',
            },
            idOfReviewInput: 'editReview',
            rate: 0,
            review: '',
            rateIncrement: 1,
        };
    },
    mounted() {
        if (this.isNotNull(this.selfReview)) {
            this.rate = this.selfReview.value;
            this.review = this.selfReview.review;
        }
        // If user does not login, can't not rate
        if (!localStorage.getItem(CONST.LOGIN_TOKEN)) {
            this.rateIncrement = 0;
        }
    },
    methods: {
        setRateBook(newRate) {
            // If user login, set rate of book
            if (localStorage.getItem(CONST.LOGIN_TOKEN)) {
                this.rate = newRate;
            }else{
                // Show dialog request login
                this.$emit('showRequestLoginDialog');
            }
        },
        addReview() {
            this.selfReview.value = this.rate;
            this.selfReview.review = this.review.trim();
            var date = moment();
            this.selfReview.evaluationTime = date.format("DD MMM");
            this.$emit('addReview');
        }
    },
    props: {
        imageId: {
            required: true
        },
        selfReview: {
            required: true
        },
        isUpdate: {
            required: true,
            default: false
        }
    },
    watch: {
        'review': function() {
            this.typingReview(this.idOfReviewInput);
        }
    }
}
</script>


<style lang="scss">
@import '../../../../scss/common_styles.scss';
@import '../../../../scss/text_styles.scss';
@import 'ListUserCreateReview.scss';
</style>




