<template>
    <div class="tab-heading-background fixed-tab-header">
        <div class="container-gat" style="text-align:center">
            <ul class="nav nav-tabs-header">
                <li class="tab-header-tab medium-large-text-form-r" :class="{'active':$route.name=='home'}">
                    <router-link :to="{ name: 'home'}" data-toggle="tab">{{label.home_page}}</router-link>
                </li>
                <li class="tab-header-tab medium-large-text-form-r" :class="{'active':$route.name==label.NAME_SHARE_BOOKS_PAGE}">
                    <router-link :to="{ name: label.NAME_SHARE_BOOKS_PAGE}" data-toggle="tab">{{label.book_category}}</router-link>
                </li>
                <li class="tab-header-tab medium-large-text-form-r" :class="{'active':$route.name==label.NAME_LIST_BOOKSTOPS_PAGE||$route.name==label.NAME_BOOKSTOP_DETAIL_PAGE}">
                    <router-link :to="{ name: label.NAME_LIST_BOOKSTOPS_PAGE}" data-toggle="tab">{{label.book_stop}}</router-link>
                </li>
                <li class="tab-header-tab medium-large-text-form-r" :class="{'active':$route.name==label.NAME_NEAR_BY_USER}">
                    <router-link :to="{ name: label.NAME_NEAR_BY_USER}" data-toggle="tab">{{label.sharing_user}}</router-link>
                </li>
                <!-- <li class="tab-header-tab medium-large-text-form-r">
                    <a data-toggle="tab">{{label.event}}</a>
                </li>
                <li class="tab-header-tab medium-large-text-form-r">
                    <a data-toggle="tab">{{label.gat_app}}</a>
                </li> -->
            </ul>
        </div>
    </div>
</template>

<script>
import Label from '../../../common/Label.js'
export default {
    mixins: [ Label ],
}
</script>

<style>
@import './tabHeader.scss';
</style>



