<template>
    <div>
        <div class="search-user-item-icon">
            <user-icon :userIconStyle="userIconStyle" :imageId="imageId"></user-icon>
        </div>
        <div class="search-user-item-content">
            <p class="super-biger-text-form-m">{{name}}</p>
            <p class="medium-text-form-m text-color-address text-limit search-user-address-text">{{address}}</p>
            <div class="btn-radius inline-block box-color-number-share-book">
                <p class="reset-margin small-text-form-r text-color-btn">{{sharingCount}}</p>
            </div>
            <div class="inline-block">
                <p class="reset-margin small-text-form-r text-color-highlight-number">{{label.sharingBook}}</p>
            </div>
        </div>
    </div>
</template>
<script>
import UserIcon from '../../../../common/userIcon/UserIcon.vue';
import Label from '../../../../../common/Label.js';
import CommonService from '../../../../../services/CommonService.js';

export default {
    name: 'search-user-item',
    mixins: [Label, CommonService],
    components: { UserIcon },
    data() {
        return {
            userIconStyle: {
                'background-color': 'white',
                'border-radius': '25px',
                'width': '50px',
                'height': '50px',
                'margin-top': '2px'
            },
        }
    },
    props: {
        imageId: {
            required: true
        },
        name: {
            required: true
        },

        address: {
            required: true
        },

        readCount: {
            required: true
        },

        sharingCount: {
            required: true
        }


    }
}
</script>
<style lang="scss">
@import '../../../../../scss/common_styles.scss';
@import 'searchUserItem.scss';
</style>



