<template>
    <div class="search-history-box" v-if="isShow">
        <div style="height: 40px;"></div>
        <p class="medium-text-form-m text-color-default">{{label.txt_search_history}}</p>
        <hr class="line-break reset-margin">
        <div class="search-history-content" v-if="isNotNull(label.search_by) && label.search_by[0] == searchType">
            <div @click="$emit('search', searchType, item.keyword)" v-for="(item, index) in searchHistory.titleSearchHistory" v-if="index < 5" :key="index">
                <header-search-history-item :isShowLineBreak="isShowLineBreak(index)" :searchHistoryItem="item.keyword">
                </header-search-history-item>
            </div>
        </div>
        <div class="search-history-content" v-if="isNotNull(label.search_by) && label.search_by[1] == searchType">
            <div @click="$emit('search', searchType, item.keyword)" v-for="(item, index) in searchHistory.authorSearchHistory" v-if="index < 5" :key="index">
                <header-search-history-item :isType="1" :isShowLineBreak="isShowLineBreak(index)" :searchHistoryItem="item.keyword">
                </header-search-history-item>
            </div>
        </div>
        <div class="search-history-content" v-if="isNotNull(label.search_by) && label.search_by[2] == searchType">
            <div @click="$emit('search', searchType, item.keyword)" v-for="(item, index) in searchHistory.userSearchHistory" v-if="index < 5" :key="index">
                <header-search-history-item :isType="2" :isShowLineBreak="isShowLineBreak(index)" :searchHistoryItem="item.keyword">
                </header-search-history-item>
            </div>
        </div>
    </div>
</template>

<script>
import HeaderSearchHistoryItem from './HeaderSearchHistoryItem.vue';
import Label from '../../../common/Label.js';
import CommonService from '../../../services/CommonService.js';

export default {
    name: "header-search-history",
    components: { HeaderSearchHistoryItem },
    mixins: [Label, CommonService],
    computed: {
        isShow() {
            if (this.isNull(this.label.search_by)) {
                return false;
            }
            switch (this.searchType) {
                case this.label.search_by[0]:
                    return this.isNotNull(this.searchHistory.titleSearchHistory) && this.searchHistory.titleSearchHistory.length > 0;
                case this.label.search_by[1]:
                    return this.isNotNull(this.searchHistory.authorSearchHistory) && this.searchHistory.authorSearchHistory.length > 0;
                    break;
                case this.label.search_by[2]:
                    return this.isNotNull(this.searchHistory.userSearchHistory) && this.searchHistory.userSearchHistory.length > 0;
                    break;
                default:
                    break;
            }
        },
    },
    methods: {
        isShowLineBreak(index) {
            if (this.isNull(this.label.search_by)) {
                return false;
            }
            switch (this.searchType) {
                case this.label.search_by[0]:
                    return this.searchHistory.titleSearchHistory.length >= 5 ? index < 4 : index < this.searchHistory.titleSearchHistory.length - 1;
                case this.label.search_by[1]:
                    return this.searchHistory.authorSearchHistory.length >= 5 ? index < 4 : index < this.searchHistory.authorSearchHistory.length - 1;
                case this.label.search_by[2]:
                    return this.searchHistory.userSearchHistory.length >= 5 ? index < 4 : index < this.searchHistory.userSearchHistory.length - 1;
                default:
                    return this.searchHistory.titleSearchHistory.length >= 5 ? index < 4 : index < this.searchHistory.titleSearchHistory.length - 1;
            }
        }
    },
    props: {
        searchType: {
            required: true
        },
        searchHistory: {
            required: true
        }
    }
}
</script>

<style lang="scss">
@import '../../../scss/common_styles.scss';

.search-history-box {
    position: absolute;
    top: 15px;
    left: 150px;
    background-color: white;
    border: 1px solid #c0c0c0;
    border-radius: 5px;
    width: 450px;
    z-index: 9;
    padding-left: 10px;
    padding-right: 10px;
}

.search-history-content {
    position: relative;
    width: 100%;
    max-height: 265px;
}
</style>



