<template>
    <div>
        <div class="user-image-circle" :class="{'image-container':changable}" v-bind:style="userIconStyle">
            <img v-bind:src="userImageLink" class="img-avatar img-circle" :class="{'on-hover-effect':changable}">
            <div v-if="changable" class="image-middle">
                <span v-on:click="openBrowser" class="fa fa-camera fa-2x mouse-pointer" style="color:#4196c2"></span>
            </div>
        </div>
    </div>
</template>

<script>
import * as CONST from '../../../common/const.js';
export default {
    name: 'user-icon',
    props: ["imageId", "userIconStyle", "changable"],
    computed: {
        userImageLink: function() {
            return this.imageId? CONST.BASE_URL + 'common/get_image/' + this.imageId : CONST.BASE_URL + 'common/get_image/' + CONST.USER_DEFAULT;
        }
    },
    methods: {
        openBrowser() {
            this.$emit('openChangeImageDialog', true);
        }
    },
    data() {
        return {
            
        }
    }
}
</script>

<style>
.image-container {
    position: relative;
}

.on-hover-effect {
    opacity: 1;
    display: block;
    width: 100%;
    height: auto;
    transition: .5s ease;
    backface-visibility: hidden;
}

.image-middle {
    transition: .5s ease;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%)
}

.image-container:hover .on-hover-effect{
    opacity: 0.3;
}

.image-container:hover .image-middle {
    opacity: 1;
}

</style>



