<template>
    <div class="book-detail-content-user-review">
        <p class="super-biger-text-form-m">{{label.txt_review}}</p>
        <div v-if="isShowLoading">
            <img src="../../../../assets/images/loading.gif" style="width:50px; height: 50px;">
        </div>
        <div class="book-detail-content-user-review-content" v-else>
            <list-user-create-review :imageId="getImageUrlOnAddReview" :selfReview="selfReview" :isUpdate="isUpdate" @showRequestLoginDialog="$emit('showConfirmDialog');"
            @addReview='addReview' @cancel="isUpdate = false; isReviewed = true;" v-if="false == isReviewed"></list-user-create-review>
            <hr class="line-break-color reset-padding reset-margin" v-if="false == isReviewed">
            <div class="book-detail-content-user-review-box">
                <div class="row book-detail-content-user-review-box-item" v-if="isReviewed">
                    <list-user-review-item @editReview="editReview" :imageId="loginUser.imageId" :name="loginUser.name" :review="selfReview.review" :value="selfReview.value" :evaluationTime="selfReview.evaluationTime" :isShowEditReview="true"></list-user-review-item>
                    <hr class="line-break-color reset-padding reset-margin">
                </div>
                <div class="row book-detail-content-user-review-box-item" v-for="(user, index) in listUser" :key="user.userId" v-if="isDisplayUserReview(user.userId)">
                    <list-user-review-item :imageId="user.imageId" :name="user.name" :value="user.value" :review="user.review" :evaluationTime="user.evaluationTime"></list-user-review-item>
                    <hr class="line-break-color reset-padding reset-margin">
                </div>
                <div class="book-detail-list-user-review-box-show-more" v-if="isShowMore">
                    <button class="btn-no-border book-detail-list-user-review-btn-show-more" @click="showMoreReview">
                        {{label.txt_show_more_review}}
                    </button>
                </div>
                <div style="clear: both;"></div>
            </div>
        </div>
        <div style="clear: both;"></div>
    </div>
</template>

<script>
import * as CONST from '../../../../common/const.js';
import CommonService from '../../../../services/CommonService.js';
import BookService from '../../../../services/BookService.js'; 

import ListUserReviewItem from '../listUserReviewItem/ListUserReviewItem.vue';
import ListUserCreateReview from '../listUserCreateReview/ListUserCreateReview.vue';
import Label from '../../../../common/Label.js';


export default {
    name: 'book-detail-list-user-review',
    components: {ListUserReviewItem, ListUserCreateReview },
    mixins: [CommonService, BookService, Label],

    data() {
        return {
            listUser: [],
            isReviewed: false,
            config_get_list_user_review: {
                params: {
                    editionId: this.editionId,
                    per_page: 5,
                    page: 1
                }
            },
            config_get_self_review: {
                params: {
                    editionId: this.editionId,
                }
            },
            selfReview: { value: 0, review: '' },
            cloneSelfReview: {},
            page: 1,
            isShowLoading: false,
            isShowMore: true,
            handleSuccess: this.setListUserReview,
            loginUser: null,
            isUpdate: false,
        }
    },
    mounted() {
        this.initData();
    },
    computed: {
        getImageUrlOnAddReview() {
            if (this.isNotNull(this.loginUser)) {
                return this.loginUser.imageId;
            }
            return '';
        },
    },
    methods: {
        initData() {
            this.isShowLoading = true;
            if (this.isNotNull(this.loginToken)) {
                this.getLoginUser();
                this.getReviewOfLoginUser();
            } else {
                this.getListUserReview();
            }
        },
        showMoreUserReview(response) {
            if (200 == response.status) {
                var responseData = response.data.data.resultInfo;
                this.listUser = this.listUser.concat(responseData);
                if (responseData.length < this.config_get_list_user_review.params.per_page) {
                    this.isShowMore = false;
                }
                this.handleSuccess = this.setListUserReview;
            }
        },
        setListUserReview(response) {
            if (200 == response.status) {
                var responseData = response.data.data.resultInfo;
                this.listUser = responseData;
                if (responseData.length < this.config_get_list_user_review.params.per_page) {
                    this.isShowMore = false;
                }
            }
            this.isShowLoading = false;
        },
        getListUserReview() {
            if (this.isNotNull(this.editionId)) {
                this.api.callGetApi(this.api.api_get_review_for_book, this.config_get_list_user_review).then(this.handleSuccess, this.handleErrorFunction);
            }
        },
        showMoreReview() {
            this.config_get_list_user_review.params.page += 1;
            this.handleSuccess = this.showMoreUserReview;
            this.getListUserReview();
        },
        getLoginUser() {
            this.$getItem(CONST.LOGIN_USER).then(loginUser => {
                this.loginUser = this.isNotNull(loginUser) ? loginUser : null;
            });
        },
        addReview() {
            if (this.isNotNull(this.loginUserId)) {
                if (this.isNotNull(this.bookId) && this.isNotNull(this.editionId)) {
                    this.$emit("showLoadingDialog");
                    var params = {
                        evaluationId: this.selfReview.evaluationId,
                        readingId: this.readingObject.readingId,
                        bookId: this.bookId,
                        editionId: this.editionId,
                        value: this.selfReview.value,
                        review: this.selfReview.review
                    }
                    this.api.callPostApiWithAuthorization(this.api.api_selfupdate_book_evaluation, params)
                        .then(this.updateReviewSuccess, this.handleErrorUpdatereview);
                }
            } else {
                this.$emit('showConfirmDialog');
            }
        },

        editReview() {
            // Create clone self review
            this.cloneSelfReview.value = this.selfReview.value;
            this.cloneSelfReview.review = this.selfReview.review;
            this.isUpdate = true;
            this.isReviewed = false;
        },

        handleErrorUpdatereview(error) {
            // If update reivew
            if(this.isUpdate){
                // Restore self review
                this.selfReview.value = this.cloneSelfReview.value;
                this.selfReview.review = this.cloneSelfReview.review;
                this.selfReview.evaluationTime = this.cloneSelfReview.evaluationTime;
                this.isReviewed = true;
                this.isUpdate = false;
            }

            // Close system dialog
            this.$emit("hideLoadingDialog");
            
            // Show error dialog
            this.apiErrorDialogContent = error.response.data.message;
            this.$emit('showErrorDialog', this.apiErrorDialogContent);
        },

        updateReviewSuccess(response) {
            if (200 == response.status) {
                if (this.isUpdate) {
                    this.isUpdate = false;
                    this.isReviewed = true;
                } else {
                    this.getReviewOfLoginUser();
                }
            }
            this.$emit("hideLoadingDialog");
        },
        getReviewOfLoginUser() {
            if (this.isNotNull(this.editionId)) {
                this.api.callGetApiWithAuthorization(this.api.api_selfget_book_evaluation, this.config_get_self_review)
                    .then(this.setReviewOfUserLogin, this.handleErrorFunction);
            }
        },
        setReviewOfUserLogin(response) {
            if (200 == response.status) {
                var tempSelfReview = response.data.data.resultInfo;
                if (this.isNotNull(tempSelfReview) && this.isNotNull(tempSelfReview.evaluationId)) {
                    this.selfReview = tempSelfReview;
                    this.isReviewed = true;
                }

                this.$emit("hideLoadingDialog");
            }
            this.getListUserReview();
        },
        isDisplayUserReview(userId) {
            if (this.isNotNull(this.loginUser) && userId == this.loginUser.userId) {
                return false;
            }
            return true;
        }
    },
    props: {
        bookId: {
            require: true
        },
        editionId: {
            require: true
        },
        readingObject: {
            required: true
        }
    }
}

</script>

<style lang="scss">
@import '../../../../scss/common_styles.scss';
@import '../../../../scss/text_styles.scss';
@import 'ListUserReviewInDetailPage.scss';
</style>




