<template>
    <div class="book-detail-content-right-info">
        <add-instance @change-status="changeStatus" :isShow="isShowModal" :book="book" @addBook="callApiToAddBook" :instanceInfo="instanceInfo"></add-instance>
        <div class="book-detail-content-right-info-content">
            <div class="book-detail-sharing-social">
                <span><social-sharing :url="currentUrl" inline-template>
                    <network network="facebook">
                        <img src="../../../../assets/images/fb_sharing.png" class="img-circle book-detail-sharing-social-icon">
                    </network>
                </social-sharing></span>
                <span><social-sharing :url="currentUrl" inline-template>
                    <network network="twitter">
                        <img src="../../../../assets/images/tweeter_sharing.png" class="img-circle book-detail-sharing-social-icon">
                    </network>
                </social-sharing></span>
            </div>
            <div class="book-detail-right-info-box-image">
                <img :src="getUrlImage(book.imageId)" class="image-fill book-detail-right-info-box-image" :alt="book.title">
                <div style="clear: both;"></div>
            </div>
            <div class="book-detail-right-info-box-content">
                <p class="super-super-biger-text-form-m text-color-default book-detail-title">{{getContentLimitLength(book.title, 70)}}</p>
                <p class="super-biger-text-form-m text-color-author">{{getAuthor(book.author)}}</p>
                <div class="book-detail-box-rate-star box-block">
                    <span class="book-detail-box-star">
                        <star-rating :rating="book.rateAvg" :read-only="true" :increment="0.01" :show-rating="false" inactive-color="white" border-color="#f4c312" :border-width="1" :star-size="18">
                        </star-rating>
                    </span>
                    <span class="super-biger-text-form-m text-color-author">{{book.rateAvg}}</span>
                </div>
                <p class="super-biger-text-form-m text-color-highlight">{{label.txt_publisher + " " + book.publisher}}</p>
                <div>
                    <div class="book-detail-box-add-book btn-custom" @click="addBookToLibrary">
                        <span class="book-detail-btn-add-book box-circle">
                            <i class="fa fa-plus" aria-hidden="true"></i>
                        </span>
                        <span class="biger-text-form-r text-color-btn book-detail-txt-add-book">{{label.txt_add_book}}</span>
                    </div>
                    <div class="book-detail-box-reading-status" @click="clickBtnActionWithBook()">
                        <img class=" image-fill img-book " src="../../../../assets/images/icon_bookmark_blue.png" v-if="isDisplayChecked"></img>
                        <img class=" image-fill img-book " src="../../../../assets/images/icon_bookmark_white.png" v-else></img>
                        <div style="clear: both;"></div>
                    </div>
                    <div style="clear: both;"></div>
                    <!-- <div class="btn book-detail-box-dropdown btn-custom">
                        <div id="book-detail-dropdown-btn" class="book-detail-dropdown-btn dropdown-toggle" @click="clickBtnActionWithBook()">
                            <span id="book-detail-dropdown-btn" class="book-detail-checked" v-show="isDisplayChecked">
                                <i id="book-detail-dropdown-btn" class="fa fa-check " aria-hidden="true"></i>
                            </span>
                            <span id="book-detail-dropdown-btn" class="biger-text-form-r text-color-btn book-detail-txt-action-with-box">{{getActionWithBook}}</span>
                            <span id="book-detail-dropdown-btn" class="book-detail-angle-down">
                                <i id="book-detail-dropdown-btn" class="fa fa-angle-down fa-lg" aria-hidden="true"></i>
                            </span>
                        </div>
                        <div class="book-detail-extension" v-if="isShowExtension">
                            <div v-for="(action, index) in getActionArray" :key="index" class="book-detail-list-action-box" 
                                :class="{'book-detail-list-action-box-border' : (index < label.txt_action_array.length - 2),
                                'book-detail-list-action-box-first-item' : index == 0}" @click="selectActionWithBook(action.status)">
                                <p class="biger-text-form-r" style="color:red;" v-if="label.txt_action_array[3].text == action.text">{{action.text}}</p>
                                <p class="biger-text-form-r text-color-highlight" v-else>{{action.text}}</p>
                            </div>
                        </div>
                    </div> -->
                </div>
            </div>
        </div>
        <hr class="line-break-category reset-padding reset-margin">
        <div class="book-detail-content-right-list-share">
            <list-user-share-book @showConfirmDialog="$emit('showConfirmDialog');" @showErrorDialog="$emit('showErrorDialog', $event)"
             @showLoadingDialog="$emit('showLoadingDialog');" @hideLoadingDialog="$emit('hideLoadingDialog');"
             :listUserShareObject="listUserShareObject" :book="bookObject" :loginUser="loginUser"></list-user-share-book>
        </div>
        <div style="clear: both;"></div>
    </div>
</template>

<script>
import * as CONST from '../../../../common/const.js';
import UserIcon from '../../../common/userIcon/UserIcon';
import CommonService from '../../../../services/CommonService.js';
import BookService from '../../../../services/BookService.js';
import ListUserShareBook from '../listShareBook/ListUserShareBookInDetailPage.vue';
import AddInstance from '../addInstance/AddInstance.vue';
import StarRating from 'vue-star-rating';
import Label from '../../../../common/Label.js';

export default {
    name: 'book-detail-info',
    components: { StarRating, UserIcon, ListUserShareBook, AddInstance },
    mixins: [CommonService, BookService, Label],

    data() {
        return {
            isShowAction: false,
            categoryIndex: this.category,
            book: { imageId: null, bookId: null, editionId: null },
            isShowExtension: false,
            isShowModal: false,
            currentUrl: null,
            config_add_book: {
                readingId: this.readingObject.readingId,
                bookId: 0,
                editionId: 0,
                sharingStatus: 0,
                numberOfBook: 0
            },
        }
    },
    mounted() {
        this.initData();
    },
    computed: {
        isDisplayChecked() {
            if (this.isNull(this.readingObject.readingStatus) || this.readingObject.readingStatus < 0) {
                return false;
            }
            return true;
        },
        getActionWithBook() {
            if (this.isDisplayChecked) {
                for (var count = 0; count < this.label.txt_action_array.length; count++) {
                    if (this.label.txt_action_array[count].status == this.readingObject.readingStatus) {
                        return this.label.txt_action_array[count].text;
                    }
                }
            }
            return this.label.txt_action_array[0].text;
        },
        getActionArray() {
            var status = 2;
            if (this.isNotNull(this.readingObject.readingStatus) && this.readingObject.readingStatus >= 0) {
                status = this.readingObject.readingStatus;
            }
            var result = [];
            for (var count = 0; count < this.label.txt_action_array.length; count++) {
                if (status != this.label.txt_action_array[count].status) {
                    result[result.length] = this.label.txt_action_array[count];
                }
            }
            return result;
        },

    },
    methods: {
        initData() {
            this.currentUrl = window.location.href;
            // this.currentUrl = "https://gatbook-v1-7449e.firebaseapp.com/";
            if (this.isNotNull(this.bookObject.editionId)) {
                this.book = this.bookObject;
                this.config_add_book.bookId = this.book.bookId;
                this.config_add_book.editionId = this.book.editionId;
            }
            var _self = this;
            $(window).click(function(event) {
                if (event.target.id != "book-detail-dropdown-btn") {
                    _self.isShowExtension = false;
                }
            });
        },
        addBookToLibrary() {
            if (this.isNotNull(this.loginUserId)) {
                this.isShowModal = true;
            } else {
                this.$emit('showConfirmDialog');
            }
        },
        callApiToAddBook(sharingStatus, numberOfBook) {
            this.config_add_book.sharingStatus = sharingStatus;
            this.config_add_book.numberOfBook = numberOfBook;
            this.$emit("showLoadingDialog");
            
            var _self = this;
            this.api.callPostApiWithAuthorization(this.api.api_selfadd_instance, this.config_add_book)
                .then(this.handleSuccessAddBook, this.handleErrorFunction);
        },
        handleSuccessAddBook(response) {
            this.isShowModal = false;
            this.$emit("hideLoadingDialog");
            if (200 == response.status) {
                // If change status diffirent with current status, call getInstanceInfoBook 
                if((this.config_add_book.sharingStatus == 0 && (this.isNull(this.instanceInfo.notSharingTotal) || this.instanceInfo.notSharingTotal <= 0))
                || (this.config_add_book.sharingStatus == 1 && (this.isNull(this.instanceInfo.sharingTotal) || this.instanceInfo.sharingTotal <= 0))){
                    this.$emit("getInstanceInfoBook");
                    this.$emit("getBookshareTotal");
                    this.$emit("getListUserShare");
                } 
            }
        },
        clickBtnActionWithBook() {
            if (this.isNotNull(this.loginUserId)) {
                // if (this.isDisplayChecked) {
                //     this.isShowExtension = true;
                // } else {
                //     this.isShowExtension = false;
                //     this.updateStatusReadingBook(2);
                // }
                if (this.isDisplayChecked) {
                    this.readingObject.readingStatus = -1;
                } else{
                    this.readingObject.readingStatus = 2;
                }
                this.updateStatusReadingBook(this.readingObject.readingStatus);
            } else {
                this.$emit('showConfirmDialog');
            }
        },
        // selectActionWithBook(status) {
        //     this.updateStatusReadingBook(status);
        // },
        updateStatusReadingBook(readingStatus) {
            // -1: khng g ht
            // 0: read -  c
            // 1: reading - ang c
            // 2: to read - mun c
            var params = {
                readingId: this.readingObject.readingId,
                bookId: this.book.bookId,
                editionId: this.book.editionId,
                readingStatus: readingStatus
            };
            this.api.callPostApiWithAuthorization(this.api.api_selfupdate_reading_stt, params)
                .then(this.setReadingStatus, this.errorCallApiUpdateReadingSTT);
        },
        changeStatus(status) {
            this.isShowModal = false;
        },
        setReadingStatus(response){
            this.$emit('setReadingStatus', response);
        },
        errorCallApiUpdateReadingSTT(error) {
            this.$emit('showErrorDialog', error.response.data.message);
            if (this.isDisplayChecked) {
                this.readingObject.readingStatus = 2;
            } else{
                this.readingObject.readingStatus = -1;
            }
        }
    },
    watch:{
        bookObject: function () {
            this.initData();
        }
    },
    props: {
        bookObject: {
            required: true
        },
        readingObject: {
            required: true
        },
        instanceInfo: {
            required: true
        },
        listUserShareObject: {
            required: true
        },
        loginUser: {
            required: true
        },
    }

}
</script>


<style lang="scss">
@import '../../../../scss/text_styles.scss';
@import '../../../../scss/common_styles.scss';
@import 'BookDetailInfo.scss';
</style>





