<template>
    <div class="box-book-grid">
        <figure>
        <div class="box-image">
            <a v-if="label.PATH_BOOK_DETAIL_PAGE" :href="label.PATH_BOOK_DETAIL_PAGE.replace(':id',book.editionId)">
                <img :title="book.title" :src="getUrlImage(book.editionImageId)" class="image-fill img-book" :alt="book.title">
            </a>
        </div>
        <div class="title-info">
            <a v-if="label.PATH_BOOK_DETAIL_PAGE" :title="book.title" :href="label.PATH_BOOK_DETAIL_PAGE.replace(':id',book.editionId)">
                <span class="medium-text-form-m text-limit text-muted title-name-color">{{book.title}}</span>
            </a>
        </div>
        <div class="author-info">
            <span v-if="book.author" class="small-text-form-r text-limit text-color-author">{{book.author}}</span>
            <span v-if="!book.author" class="small-text-form-r text-limit text-color-author">&nbsp</span>
        </div>
        <div v-if="book.borrowFromUserId" class="sharing-info">
            <div class="display-left">
                <span class="small-text-form-r center-vertical-text">{{label.borrowFrom}}</span>
            </div>
            <div class="display-right">
                <router-link v-if="targetUserRouter" :title="book.borrowFromUserName" :to="targetUserRouter">
                    <div class="user-border">
                        <user-icon :userIconStyle="userIconStyle" :imageId="book.borrowFromUserImage"></user-icon>
                    </div>
                </router-link>
            </div>
        </div>
        </figure>
    </div>
</template>

<script>
import CommonService from '../../../../services/CommonService.js';
import BookService from '../../../../services/BookService.js';
import UserIcon from '../../../common/userIcon/UserIcon.vue';
import Label from '../../../../common/Label.js';
import * as CONST from '../../../../common/const.js'

export default {
    name: 'book-in-read-history',
    components: { UserIcon },
    mixins: [CommonService, BookService, Label],
    mounted() {

    },
    computed: {
        targetUserRouter: function() {
            if (this.book.borrowFromUserTypeFlag) {
                if (this.book.borrowFromUserTypeFlag == CONST.USER_TYPE_FLAG) {
                    return { name: this.label.NAME_USER_VISITOR_PAGE, params: {id: this.book.borrowFromUserId, tab: 'book-shelf'}};
                } else if (this.book.borrowFromUserTypeFlag == CONST.BOOKSTOP_TYPE_FLAG) {
                    return { name: this.label.NAME_BOOKSTOP_DETAIL_PAGE, params: {id: this.book.borrowFromUserId, tab: 'books'}};
                } else {
                    return null;
                }
            } 
        }
    },
    methods: {
        
    },
    data() {
        return {
            userIconStyle: {
                'background-color': 'white',
                'border-radius': '16px',
                'width': '32px',
                'height': '32px',
                'margin': '0 auto'
            }
        }
    },
    props: {
        book: {
            required: true
        }
    }
}
</script>

<style>

</style>



