<template>
    <div class="message-list-form">
        <div class="block-title biger-text-form-m" style="border-bottom: 1px solid white;">
            {{label.messageListTitle}}
        </div>
        <vue-perfect-scrollbar class="message-target-list" >
            <message-row v-for="(item, index) in displayList" :key="index" :item="item"></message-row>
            <infinite-loading :on-infinite="onInfinite" ref="infiniteLoading"></infinite-loading>
        </vue-perfect-scrollbar>
    </div>
</template>

<script>
import InfiniteLoading from 'vue-infinite-loading'
import VuePerfectScrollbar from 'vue-perfect-scrollbar'
import MessageRow from '../components/MessageRow.vue'
import Label from '../../../../common/Label.js'
import * as CONST from '../../../../common/const.js'

//firebase db (tabel users) refer
//let userRef = window.firebaseDB.ref('users');
//firebase db (tabel messages) refer
let messagesRef = window.firebaseDB.ref('messages');

export default {
    name: 'message-list',
    mixins: [ Label ],
    components: { InfiniteLoading, VuePerfectScrollbar, MessageRow },
    
    mounted() {
        let _self = this;
        let loginUserId = localStorage.getItem(CONST.USER_ID);
        //get users key
        messagesRef.on('child_added', function (data) {
            //get userids in conversation
            let uic =  data.key.split(':');
            if(uic[0] == loginUserId || uic[1] == loginUserId){
                messagesRef.child(data.key).orderByChild('timeStamp').limitToLast(1).on('child_added', function (subData) {
                    //get target user
                    var targetId = loginUserId == uic[0]? uic[1]:uic[0];
                    //check each child node
                    var child = {
                        targetId: targetId,
                        isRead: subData.val().isRead,
                        message: subData.val().userId==loginUserId? _self.label.you + ": " + subData.val().message : subData.val().message,
                        timeStamp: subData.val().timeStamp,
                        userId: subData.val().userId
                    }
                    //exist flag
                    var isExist = false;
                    //position of existed item
                    var position = 0;
                    _.each (_self.messagedList, function(item, index) {
                        if (item.targetId == child.targetId) {
                            isExist = true;
                            position = index;
                            return;
                        }
                    });
                    //if exist, replace item at [position] by child if child.timeStamp is bigger; else, push child node into list
                    if (isExist) {
                        if (_self.messagedList[position].timeStamp < child.timeStamp) {
                            _self.messagedList.splice(position,1);
                            _self.messagedList.unshift(child);
                        }
                    } else {
                        _self.messagedList.unshift(child);
                    }
                });
            }
        });
    },

    watch: {
        messagedList: function() {
            var x = this.displayList.length;
            this.displayList = [].concat(this.messagedList.slice(0, x));
        }
    },
    
    data() {
        return {
            //contain message history of you and your friend
            messagedList: [],
            //loaded target array
            displayList: [],
            //default page
            start: 0,
        };
    },

    methods: {
        onInfinite() {
            setTimeout(() => {
                if (this.messagedList[this.start]) {
                    if (this.start == 0) {
                        //sort the list by timeStamp desc
                        this.messagedList.sort(function(a,b) {return (a.timeStamp < b.timeStamp) ? 1 : ((b.timeStamp < a.timeStamp) ? -1 : 0);} );
                        //get max item can display on screen
                        let x = ((screen.height - 140)/60).toFixed(0)*1 + 1;
                        //load fastestable x first items
                        this.displayList = [].concat(this.messagedList.slice(0, x));
                        this.start+=this.displayList.length;
                    } else {
                        //load each next item
                        this.displayList.push(this.messagedList[this.start]);
                        this.start+=1;
                    }
                }
                this.$refs.infiniteLoading.$emit('$InfiniteLoading:loaded');
            }, 300);
        }
    },
}
</script>

<style>
.infinite-status-prompt{
    display: none;
}
</style>



