<template>
    <div class="box-shadow-bold">
        <div class="row nopadding bookstop-search-form">
            <div class="col-xs-9 medium-text-form-r">
                <input type="text" class="form-control search-item" v-model="searchText" :placeholder="label.search_place_holder">
            </div>
            <div class="col-xs-3">
                <button v-on:click="doSearch" class="btn btn-gat search-item" style="width:150px">
                    {{label.search}}
                </button>
            </div>
        </div>
        <div class="bookstop-booklist">
            <div v-if="isShowLoading" style="margin:10px;">
                <img src="../../../assets/images/loading.gif" class="loading-img-medium">
            </div>
            <div v-if="!isShowLoading">
                <div style="margin-bottom:10px; margin-left:10px;" v-html="sharingResult"></div>
                <div class="row" v-for="(splitedBook,outerIndex) in bookList" :key="outerIndex">
                    <div class="col-xs-3 nopadding" v-for="(book,innerIndex) in splitedBook" :key="innerIndex">
                        <book-in-bookstop :book="book" :inMain="true"></book-in-bookstop>
                    </div>
                </div>
                <div v-if="sharingTotal <= per_page" style="height:15px;"></div>
                <div v-if="sharingTotal > per_page" class="text-center">
                    <uib-pagination
                        :boundary-links="true"
                        :force-ellipses="false"
                        :boundary-link-numbers="true"
                        previous-text="<" 
                        next-text=">" 
                        first-text="<<"
                        last-text=">>"
                        v-model="pagination" 
                        :items-per-page="per_page" 
                        :total-items="sharingTotal" 
                        :max-size="4" 
                        :rotate="true">
                    </uib-pagination>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import BookInBookstop from '../components/BookInBookstop.vue'
import CommonService from '../../../services/CommonService.js'
import BookService from '../../../services/BookService.js'
import * as CONST from '../../../common/const.js'
import Label from '../../../common/Label.js'
export default {
    props: ["bookstopName"],
    components: { BookInBookstop },
    mixins: [ Label, CommonService, BookService],
    mounted() {
        this.refresh();
    },
    watch: {
        '$route': function(){
            this.refresh();
        },
        pagination: function(){
            var page = this.pagination.currentPage;
            var _self = this;
            this.$router.push({ name: _self.$route.name, params: {tab: _self.$route.params.tab}, query: {page: page, searchKey: this.searchText}});
        }
    },
    computed: {
        sharingResult() {
            if (this.bookstopName) {
                if (this.sharingTotal!=null) {
                    return this.label.totalResult(this.sharingTotal);
                }
            }
        }
    },
    methods: {
        refresh(){
            var _self = this;
            if(this.$route.query.page==null){
                this.$router.replace({ name: _self.$route.name, params: {tab: _self.$route.params.tab}, query: {page: '1', searchKey: this.searchText}});
            }
            this.pagination = {currentPage: Number(this.$route.query.page)};
            this.getBookTotal();
            this.getBookList();
        },
        doSearch() {
            this.$router.replace({query: {page: '1', searchKey: this.searchText}});
            this.refresh();
        },
        getBookTotal() {
            //get data from Service API
            var _self = this;
            var config = {
                params: {
                    ownerId: _self.$route.params.id,
                    searchKey: _self.searchText,
                }
            };
            _self.api.callGetApi(_self.api.api_get_user_sharing_editions_total, config)
                .then(response => {
                    _self.sharingTotal = response.data.data.sharingTotal;
                })
                .catch(_self.handleErrorFunction);
        },
        getBookList() {
            //get data from Service API
            var _self = this;
            _self.bookList = [];
            _self.isShowLoading = true;
            var config = {
                params: {
                    ownerId: _self.$route.params.id,
                    searchKey: _self.searchText,
                    userId: _self.loginUserId,
                    page: _self.pagination.currentPage,
                    per_page: _self.per_page
                }
            };
            _self.api.callGetApi(_self.api.api_get_user_sharing_editions, config)
                .then(response => {
                    _self.bookList = _.chunk(response.data.data.resultInfo, 4);
                    _self.isShowLoading = false;
                })
                .catch(_self.handleErrorFunction);
        }
    },
    data() {
        return {
            searchText: "",
            per_page: CONST.USER_BOOK_ITEM_PERPAGE,
            pagination: {},
            sharingTotal: null,
            bookList: []
        }
    }
}
</script>

<style>

</style>



