<template>
    <div class="user-profile-detail-form box-shadow-bold">
        <!-- loading dialog -->
        <loading-dialog :loadingDialogName="'update-request-loading-'+result.recordId" :isLoadingOpen="isSystemExecuting"></loading-dialog>
        <!-- confirm dialog -->
        <confirm-dialog
            :dialogName="'updateRequest-confirm-'+result.recordId"
            :isOpen="isDialogOpen"
            :content="confirmDialogContent"
            :execute="updateBorrowRequest"
            @dialogClosed="isDialogOpen = false"></confirm-dialog>
        <message-dialog
            :dialogName="'send-message-'+targetUser.userId"
            :isOpen="isMessageDialogOpen"
            :user="targetUser"
            @dialogClosed="isMessageDialogOpen = false"></message-dialog>
        <!--page title-->
        <div class="row page-detail-header nopadding">
            <div class="col-xs-2" style="text-align:left">
                <img v-on:click="goRequestList" src="../../../../assets/images/left-arrow.png" class="mouse-pointer" style="width:20px;height:auto;">
            </div>
            <div v-if="label.recordStatus(result.recordStatus)" class="col-xs-8 medium-text-form-m">
                {{label.recordStatus(result.recordStatus)}}
            </div>
        </div>
        <div v-if="isShowLoading" class="block-full-screen-content">
            <img src="../../../../assets/images/loading.gif" class="loading-img-medium" style="margin:30px">
        </div>
        <!--page content-->
        <div v-else style="margin:30px 30px 0 30px;">
            <!--request content-->
            <div v-if="requestDetail.target==TARGET_TO_YOU" class="medium-text-form-r align-center text-limit" 
                v-html="label.receiveRequestDetail(result.editionInfo.editionId, result.editionInfo.title, targetUser.userId, targetUser.name, targetUser.userTypeFlag)">
            </div>
            <div v-if="requestDetail.target==TARGET_FROM_YOU" class="medium-text-form-r align-center text-limit" 
                v-html="label.sendRequestDetail(result.editionInfo.editionId, result.editionInfo.title)">
            </div>
            <div class="align-center" style = "margin:20px;">
                <!-- send request progress -->
                <div class="progress-wraper inline-block">
                    <div class="success-part inline-block">
                        <span class="fa fa-check" style="padding:12px;color:white;transform:scale(1.6,1.6);"></span>
                    </div>
                    <div class="progress-des success-progress-des medium-text-form-m">
                        {{label.requestTime}}
                    </div>
                </div>
                <!-- accept request progress -->
                <div v-if="result.recordStatus==BR_STT_WAIT_CONFIRM||result.recordStatus==BR_STT_ON_HOLD" class="progress-wraper inline-block">
                    <div class="progress-stick hold-on-stick inline-block"></div>
                    <div class="hold-on-part inline-block">
                        <div class="hold-on-inner-part biger-text-form-m">2</div>
                    </div>
                    <div class="progress-des hold-on-progress-des medium-text-form-m">
                        {{label.approveTime}}
                    </div>
                </div>
                <!-- accept request progress -->
                <div v-if="result.recordStatus==BR_STT_CONTACTING||result.recordStatus==BR_STT_BORROWING||result.recordStatus==BR_STT_COMPLETED||result.recordStatus==BR_STT_UNRETURN" class="progress-wraper inline-block">
                    <div class="progress-stick success-stick inline-block"></div>
                    <div class="success-part inline-block">
                        <span class="fa fa-check" style="padding:12px;color:white;transform:scale(1.6,1.6);"></span>
                    </div>
                    <div class="progress-des success-progress-des medium-text-form-m">
                        {{label.approveTime}}
                    </div>
                </div>
                <!-- approve request progress -->
                <div v-if="result.recordStatus==BR_STT_WAIT_CONFIRM||result.recordStatus==BR_STT_ON_HOLD||result.recordStatus==BR_STT_CONTACTING" class="progress-wraper inline-block">
                    <div class="progress-stick hold-on-stick inline-block"></div>
                    <div class="hold-on-part inline-block">
                        <div class="hold-on-inner-part biger-text-form-m">3</div>
                    </div>
                    <div class="progress-des hold-on-progress-des medium-text-form-m">
                        {{label.borrowTime}}
                    </div>
                </div>
                <div v-if="result.recordStatus==BR_STT_BORROWING||result.recordStatus==BR_STT_COMPLETED||result.recordStatus==BR_STT_UNRETURN" class="progress-wraper inline-block">
                    <div class="progress-stick success-stick inline-block"></div>
                    <div class="success-part inline-block">
                        <span class="fa fa-check" style="padding:12px;color:white;transform:scale(1.6,1.6);"></span>
                    </div>
                    <div class="progress-des success-progress-des medium-text-form-m">
                        {{label.borrowTime}}
                    </div>
                </div>
                <!-- complete request progress -->
                <div v-if="result.recordStatus==BR_STT_WAIT_CONFIRM||result.recordStatus==BR_STT_ON_HOLD||result.recordStatus==BR_STT_CONTACTING||result.recordStatus==BR_STT_BORROWING" class="progress-wraper inline-block">
                    <div class="progress-stick hold-on-stick inline-block"></div>
                    <div class="hold-on-part inline-block">
                        <div class="hold-on-inner-part biger-text-form-m">4</div>
                    </div>
                    <div class="progress-des hold-on-progress-des medium-text-form-m">
                        {{label.completeTime}}
                    </div>
                </div>
                <div v-if="result.recordStatus==BR_STT_COMPLETED" class="progress-wraper inline-block">
                    <div class="progress-stick success-stick inline-block"></div>
                    <div class="success-part inline-block">
                        <span class="fa fa-check" style="padding:12px;color:white;transform:scale(1.6,1.6);"></span>
                    </div>
                    <div class="progress-des success-progress-des medium-text-form-m">
                        {{label.completeTime}}
                    </div>
                </div>
                <!-- cancel request progress -->
                <div v-if="result.recordStatus==BR_STT_CANCELED" class="progress-wraper inline-block">
                    <div class="progress-stick fail-stick inline-block"></div>
                    <div class="fail-part inline-block">
                        <span class="fa fa-times" style="padding:12px;color:white;transform:scale(1.6,1.6);"></span>
                    </div>
                    <div class="progress-des fail-progress-des medium-text-form-m">
                        {{label.cancelTime}}
                    </div>
                </div>
                <!-- reject request progress -->
                <div v-if="result.recordStatus==BR_STT_REJECTED" class="progress-wraper inline-block">
                    <div class="progress-stick fail-stick inline-block"></div>
                    <div class="fail-part inline-block">
                        <span class="fa fa-times" style="padding:12px;color:white;transform:scale(1.6,1.6);"></span>
                    </div>
                    <div class="progress-des fail-progress-des medium-text-form-m">
                        {{label.rejectTime}}
                    </div>
                </div>
                <!-- unreturn request progress -->
                <div v-if="result.recordStatus==BR_STT_UNRETURN" class="progress-wraper inline-block">
                    <div class="progress-stick fail-stick inline-block"></div>
                    <div class="fail-part inline-block">
                        <span class="fa fa-times" style="padding:12px;color:white;transform:scale(1.6,1.6);"></span>
                    </div>
                    <div class="progress-des fail-progress-des medium-text-form-m">
                        {{label.lostTime}}
                    </div>
                </div>
            </div>
            <div class="target-user-info">
                <!--user-icon-->
                <router-link v-if="targetUserRouter" :to="targetUserRouter">
                    <div class="display-left target-user-border">
                        <user-icon
                            :userIconStyle="userIconStyle"
                            :imageId="targetUser.imageId">
                        </user-icon>
                    </div>
                </router-link>
                <div class="display-left" style="margin-left: 15px; width: 550px;">
                    <!--user-name-->
                    <div class="half-row-top">
                        <span class="inline-block">
                            <router-link
                                v-if="targetUserRouter"
                                style="text-decoration:none;" class="medium-text-form-m text-limit text-muted title-name-color" 
                                :to="targetUserRouter">
                                {{targetUser.name}}
                            </router-link>
                        </span>
                    </div>
                    <!--address-->
                    <div class="half-row-bottom text-limit small-text-form-r">
                        {{targetUser.address}}
                    </div>
                </div>
                <div class="display-right" style="margin-top:8px;">
                    <button v-on:click="openMessageDialog" class="btn btn-gat btn-circle" style="width:150px;">
                        <img src="../../../../assets/images/message.png" style="width:20px;height:20px;">
                        <span style="margin-left:20px;">{{label.sendMessage}}</span>
                    </button>
                </div>
            </div>
            <div class="target-book-info">
                <div class="display-left book-request-image">
                    <a v-if="result.editionInfo.editionId&&label.PATH_BOOK_DETAIL_PAGE" :href="label.PATH_BOOK_DETAIL_PAGE.replace(':id',result.editionInfo.editionId)">
                        <img :title="result.editionInfo.title" :src="getUrlImage(result.editionInfo.imageId)" class="image-fill img-book" :alt="result.editionInfo.title">
                    </a>
                </div>
                <div class="display-left book-request-detail">
                    <!-- book title -->
                    <div>
                        <span class="inline-block">
                            <router-link v-if="result.editionInfo.editionId" style="text-decoration:none;" class="biger-text-form-m text-limit text-muted title-name-color"
                                :to="{name: label.NAME_BOOK_DETAIL_PAGE, params: {id: result.editionInfo.editionId}}">
                                {{result.editionInfo.title}}
                            </router-link>
                        </span>
                    </div>
                    <!-- author name -->
                    <div class="medium-text-form-r text-limit text-muted text-color-author" style="margin-top:6px;">
                        {{result.editionInfo.author}}
                    </div>
                    <!-- rate avg -->
                    <div style="height:35px;">
                        <div class="display-left" style="margin-top:10px;">
                            <star-rating 
                                :rating="result.editionInfo.rateAvg" 
                                :read-only="true" 
                                :increment="0.1" 
                                :show-rating="false" 
                                inactive-color="white" 
                                border-color="#f4c312" 
                                :border-width="1"
                                :star-size="16">
                            </star-rating>
                        </div>
                        <span class="display-left medium-text-form-r text-color-author" 
                            style="margin-left:20px;margin-top:12px;">
                            {{this.avgRound(result.editionInfo.rateAvg)}}
                        </span>
                    </div>
                    <!-- request history -->
                    <div style="margin-top:10px;">
                        <div v-for="(item, index) in requestHistory" :key="index" v-if="item[1]" class="row nopadding-hoz request-history">
                            <div class="col-xs-3 nopadding-hoz">{{item[0]}} :</div>
                            <div class="col-xs-9 nopadding-hoz">{{label.getDateTime(item[1])}}</div>
                        </div>
                    </div>
                    <!-- status -->
                    <div class="row nopadding-hoz" style="margin-top:10px;">
                        <div class="col-xs-3 nopadding-hoz">{{label.state}} :</div>
                        <div class="col-xs-9 nopadding-hoz">{{label.recordStatus(result.recordStatus)}}</div>
                    </div>
                    <!-- update request button -->
                    <div class="update-request-group-btn">
                        <div
                            v-if="requestDetail.target==TARGET_TO_YOU&&result.recordStatus==BR_STT_WAIT_CONFIRM"
                            class="display-left btn-update-request-wrapper">
                            <button v-on:click="updateRequestConfirm(BR_STT_CONTACTING)" class="btn btn-gat btn-circle btn-update-request">
                                {{label.contactButton}}
                            </button>
                        </div>
                        <div
                            v-if="requestDetail.target==TARGET_TO_YOU&&result.recordStatus==BR_STT_CONTACTING"
                            class="display-left btn-update-request-wrapper">
                            <button v-on:click="updateRequestConfirm(BR_STT_BORROWING)" class="btn btn-gat btn-circle btn-update-request">
                                {{label.borrowButton}}
                            </button>
                        </div>
                        <div
                            v-if="requestDetail.target==TARGET_TO_YOU&&result.recordStatus==BR_STT_BORROWING"
                            class="display-left btn-update-request-wrapper">
                            <button v-on:click="updateRequestConfirm(BR_STT_COMPLETED)" class="btn btn-gat btn-circle btn-update-request">
                                {{label.completeButton}}
                            </button>
                        </div>
                        <div
                            v-if="requestDetail.target==TARGET_TO_YOU&&(result.recordStatus==BR_STT_WAIT_CONFIRM||result.recordStatus==BR_STT_CONTACTING)"
                            class="display-left btn-update-request-wrapper">
                            <button v-on:click="updateRequestConfirm(BR_STT_REJECTED)" class="btn btn-gat-cancel btn-circle btn-update-request">
                                {{label.rejectButton}}
                            </button>
                        </div>
                        <div
                            v-if="requestDetail.target==TARGET_FROM_YOU&&
                                (result.recordStatus==BR_STT_WAIT_CONFIRM||result.recordStatus==BR_STT_ON_HOLD||result.recordStatus==BR_STT_CONTACTING)"
                            class="display-left btn-update-request-wrapper">
                            <button v-on:click="updateRequestConfirm(BR_STT_CANCELED)" class="btn btn-gat-cancel btn-circle btn-update-request">
                                {{label.cancelButton}}
                            </button>
                        </div>
                        <div
                            v-if="requestDetail.target==TARGET_TO_YOU&&result.recordStatus==BR_STT_BORROWING"
                            class="display-left btn-update-request-wrapper">
                            <button v-on:click="updateRequestConfirm(BR_STT_UNRETURN)" class="btn btn-gat-cancel btn-circle btn-update-request">
                                {{label.lostButton}}
                            </button>
                        </div>
                    </div>
                </div>
            </div>
            <div style="height:calc(100vh - 586px); min-height:40px;"></div>
        </div>
    </div>
</template>

<script>
import BookService from '../../../../services/BookService.js'
import CommonService from '../../../../services/CommonService.js'
import UserIcon from '../../../common/userIcon/UserIcon.vue'
import ConfirmDialog from '../../../common/dialog/ConfirmDialog.vue'
import StarRating from 'vue-star-rating'
import Label from '../../../../common/Label.js'
import * as CONST from '../../../../common/const.js'
import LoadingDialog from '../../../common/dialog/LoadingDialog.vue'
import MessageDialog from '../../../common/dialog/MessageDialog.vue'

export default {
    mixins: [ BookService, CommonService, Label ],
    components: { UserIcon, StarRating, ConfirmDialog, LoadingDialog, MessageDialog },
    props: ["getRequestSummary"],
    mounted() {
        this.getRequestDetail(true);
    },
    watch: {
        '$route': function () {
            this.getRequestDetail(true);
        }
    },
    computed: {
        //get target user info from getRequestDetail result
        targetUser(){
            if (this.requestDetail.target==CONST.TARGET_TO_YOU) {
                return this.result.borrowerInfo;
            } else if (this.requestDetail.target==CONST.TARGET_FROM_YOU) {
                return this.result.ownerInfo;
            } else {
                return {};
            }
        },
        //get request history info from getRequestDetail result
        requestHistory(){
            return [
                [this.label.requestTime, this.result.requestTime],
                [this.label.approveTime, this.result.approveTime],
                [this.label.borrowTime, this.result.borrowTime],
                [this.label.completeTime, this.result.completeTime],
                [this.label.rejectTime, this.result.rejectTime],
                [this.label.cancelTime, this.result.cancelTime],
                [this.label.lostTime, this.result.lostTime]
                ];
        },
        targetUserRouter() {
            if (this.targetUser) {
                if (this.targetUser.userTypeFlag == CONST.USER_TYPE_FLAG) {
                    return { name: this.label.NAME_USER_VISITOR_PAGE, params: {id: this.targetUser.userId, tab: 'book-shelf'}};
                } else if (this.targetUser.userTypeFlag == CONST.BOOKSTOP_TYPE_FLAG) {
                    return { name: this.label.NAME_BOOKSTOP_DETAIL_PAGE, params: {id: this.targetUser.userId, tab: 'books'}};
                } else {
                    return null;
                }
            } 
        }
    },
    methods: {
        //round float to 1 decima place
        avgRound(val){
            return val?  val.toFixed(1):null;           
        },
        //back to request list
        goRequestList(){
            if(this.requestDetail.target!=null && this.requestDetail.type!=null) {
                this.$router.push({ name: CONST.NAME_USER_REQUEST_PAGE, query: {target: this.requestDetail.target, type: this.requestDetail.type}});
            }
        },
        //get request detail by id
        getRequestDetail(getCache){
            if (getCache) {
                var lcs = CONST.USER_REQUEST_DETAIL + "_id_" + this.$route.params.id;
                this.$getItem(lcs).then(cacheData => {
                    if (!cacheData) {
                        //set loading flag to true
                        this.isShowLoading = true;
                    } else {
                        this.result = cacheData;
                        //defined request target and request type
                        this.requestDetail = {
                            target: 0,
                            type: 0
                        };
                        if (this.result.recordType==1){
                            this.requestDetail.target = CONST.TARGET_TO_YOU;
                        } else if (this.result.recordType==2){
                            this.requestDetail.target = CONST.TARGET_FROM_YOU;
                        }

                        if (this.result.recordStatus==CONST.BR_STT_WAIT_CONFIRM){
                            this.requestDetail.type = 0;
                        } else if(this.result.recordStatus==CONST.BR_STT_CONTACTING){
                            this.requestDetail.type = 1;
                        }else if(this.result.recordStatus==CONST.BR_STT_BORROWING){
                            this.requestDetail.type = 2;
                        }else{
                            this.requestDetail.type = 3;
                        }
                        //pass requestDetail to layout, to active correct tab in request navigation block
                        this.$emit('dataLoaded', this.requestDetail);
                    }
                })
            } else {
                //set loading flag to true
                this.isShowLoading = true;
            }
            
            var config = {
                params: {
                    recordId: this.$route.params.id
                },
                headers: {
                    'Authorization': this.loginToken
                }
            };
            this.api.callGetApi(this.api.api_get_request_info, config)
                .then(response => {
                    this.result = response.data.data.resultInfo;
                    //defined request target and request type
                    this.requestDetail = {
                        target: 0,
                        type: 0
                    };
                    if (this.result.recordType==1){
                        this.requestDetail.target = CONST.TARGET_TO_YOU;
                    } else if (this.result.recordType==2){
                        this.requestDetail.target = CONST.TARGET_FROM_YOU;
                    }

                    if (this.result.recordStatus==CONST.BR_STT_WAIT_CONFIRM){
                        this.requestDetail.type = 0;
                    } else if(this.result.recordStatus==CONST.BR_STT_CONTACTING){
                        this.requestDetail.type = 1;
                    }else if(this.result.recordStatus==CONST.BR_STT_BORROWING){
                        this.requestDetail.type = 2;
                    }else{
                        this.requestDetail.type = 3;
                    }
                    this.isShowLoading = false;
                    //pass requestDetail to layout, to active correct tab in request navigation block
                    this.$emit('dataLoaded', this.requestDetail);
                    this.$setItem(lcs, response.data.data.resultInfo);
                })
                .catch(this.handleErrorFunction);
        },
        //open confirm dialog
        updateRequestConfirm(newStt){
            this.newStatus = newStt;
            this.isDialogOpen = true;
            this.confirmDialogContent = this.label.updateRequestSttDialogContent(newStt, this.targetUser.name, this.result.editionInfo.title);
        },
        //
        updateBorrowRequest(){
            this.isSystemExecuting = true;
            var apiCalled = this.requestDetail.target==0? this.api.api_update_request_by_owner:this.api.api_update_request_by_borrower;
            var params = {
                recordId: this.result.recordId,
                currentStatus: this.result.recordStatus,
                newStatus: this.newStatus
            };
            var headers = {
                'Authorization': this.loginToken
            }
            this.api.callPostApi(apiCalled, params, {headers})
                .then(response => {
                    this.getRequestDetail(false);
                    this.getRequestSummary();
                    this.isSystemExecuting = false;
                    this.$emit('getRequestChange');
                })
                .catch(this.handleErrorFunction);
        },
        openMessageDialog() {
            this.isMessageDialogOpen = true;
        }
    },
    data() {
        return {
            //new status to update
            newStatus: null,
            //content of confirm dialog
            confirmDialogContent: "",
            isDialogOpen: false,
            isMessageDialogOpen: false,

            BR_STT_WAIT_CONFIRM: CONST.BR_STT_WAIT_CONFIRM,
            BR_STT_ON_HOLD: CONST.BR_STT_ON_HOLD,
            BR_STT_CONTACTING: CONST.BR_STT_CONTACTING,
            BR_STT_BORROWING: CONST.BR_STT_BORROWING,
            BR_STT_COMPLETED: CONST.BR_STT_COMPLETED,
            BR_STT_REJECTED: CONST.BR_STT_REJECTED,
            BR_STT_CANCELED: CONST.BR_STT_CANCELED,
            BR_STT_UNRETURN: CONST.BR_STT_UNRETURN,

            //target of request
            TARGET_TO_YOU: CONST.TARGET_TO_YOU,                 //0
            TARGET_FROM_YOU: CONST.TARGET_FROM_YOU,             //1

            result: {
                editionInfo:{}
            },
            requestDetail: {
                target: null,
                type: null
            },
            //style of user icon
            userIconStyle: {
                'background-color': 'white',
                'border-radius': '22px',
                'width': '44px',
                'height': '44px',
                'margin': '2px auto'
            }
        }
    }
}
</script>

<style>

</style>



