<template>
    <div>
    </div>
</template>

<script>
import * as CONST from '../../../common/const.js';
import CommonService from '../../../services/CommonService.js';

export default {
    name: 'meta-info',
    mixins: [CommonService],
    props: {
        title: {
            required: false,
            default: CONST.META_TITLE_VN,
        },
        ogTitle: {
            required: false,
            default: CONST.META_TITLE_VN,
        },
        ogUrl: {
            required: false,
            default: CONST.META_URL_TARGET,
        },
        ogImage: {
            required: false,
            default: CONST.META_URL_TARGET,
        },
        ogDescription: {
            required: false,
            default: CONST.META_TITLE_VN,
        },
    },
    data() {
        return {

        }
    },
    metaInfo() {
        return {
            title: this.title,
            titleTemplate: '%s',
            meta: [
                { vmid: 'og:title', property: 'og:title', content: this.ogTitle },
                { vmid: 'og:url', property: 'og:url', content: this.ogUrl },
                { vmid: 'og:image', property: 'og:image', content: this.ogImage },
                { vmid: 'og:description', property: 'og:description', content: this.ogDescription }
            ],
            link: [
                { rel: 'icon', href: this.getLinkImageLocation('icon_gatBook'), sizes: "32x32", type: "images/png" }
            ],
            changed(newInfo, addedTags, removedTags) {
                // console.log('newInfo');
                // console.log(newInfo);
                // console.log('addedTags');
                // console.log(addedTags);
                // console.log('removedTags');
                // console.log(removedTags);
            },
        }
    }
}
</script>


