<template>
    <div class="user-profile-detail-form user-info-content box-shadow-bold">
        <loading-dialog :loadingDialogName="'update-userinfo-loading'" :isLoadingOpen="isSystemExecuting"></loading-dialog>
        <error-dialog
            :errorDialogName="'update-userinfo-fail'"
            :isErrorOpen="isApiserviceErrorDialogOpen"
            :errorMessage="apiErrorDialogContent"
            :errorStatus="errorStatus"
            @errorDialogClosed="isApiserviceErrorDialogOpen = false"></error-dialog>

        <div class="biger-text-form-m">
            {{label.userInfo}}
        </div>

        <!-- user name -->
        <div class="small-text-form-r text-color-author" style="margin-top:25px;">
            {{label.displayName}}
        </div>
        <div v-if="!changingName" v-on:mouseenter="displayEditName(true)" v-on:mouseleave="displayEditName(false)" class="row user-info-each">
            <div class="col-xs-6 medium-text-form-r text-limit">
                {{userInfo.name}}
            </div>
            <div class="col-xs-6 medium-text-form-r">
                <button v-if="editName" v-on:click="setChangingName(true)" class="btn btn-gat btn-circle button-min-width">
                    <span class="glyphicon glyphicon-pencil"></span>
                </button>
            </div>
        </div>
        <div v-if="changingName" class="row user-info-each">
            <div class="col-xs-6 medium-text-form-r">
                <input type="text" class="form-control center-input" v-model="nameTmp" :placeholder="label.displayName">
            </div>
            <div class="col-xs-6">
                <button v-on:click="updateUserName" class="btn btn-gat btn-circle" style="width:70px">
                    {{label.save}}
                </button>
                <button v-on:click="setChangingName(false)" class="btn btn-gat-cancel btn-circle" style="width:70px">
                    {{label.cancel}}
                </button>
            </div>
        </div>

        <!-- address -->
        <div class="small-text-form-r text-color-author" style="margin-top:25px;">
            {{label.address}}
        </div>
        <div v-if="!changingAddress" v-on:mouseenter="displayEditAddress(true)" v-on:mouseleave="displayEditAddress(false)" class="row user-info-each">
            <div v-if="currentAddress" class="col-xs-6 medium-text-form-r text-limit">
                {{currentAddress}}
            </div>
            <div v-if="!currentAddress" class="col-xs-6 medium-text-form-r text-color-author text-limit">
                {{label.addressNA}}
            </div>
            <div class="col-xs-6 medium-text-form-r">
                <button v-if="editAddress" v-on:click="setChangingAddress(true)" class="btn btn-gat btn-circle button-min-width">
                    <span class="glyphicon glyphicon-pencil"></span>
                </button>
            </div>
        </div>
        <div v-if="changingAddress" class="row user-info-each">
            <div class="col-xs-6 medium-text-form-r">
                <input type="text" class="form-control center-input" v-model="addressTmp" :placeholder="label.address">
            </div>
            <div class="col-xs-6">
                <button v-on:click="updateUserLocation" class="btn btn-gat btn-circle" style="width:70px">
                    {{label.save}}
                </button>
                <button v-on:click="setChangingAddress(false)" class="btn btn-gat-cancel btn-circle" style="width:70px">
                    {{label.cancel}}
                </button>
            </div>
        </div>
        <div v-if="changingAddress" class="gmap-area">
            <vue-gmap :userInfo="userInfo" @positionChanged="positionTmp = $event"></vue-gmap>
        </div>

        <!--break line-->
        <hr class="split-content">
        
        <!--social connect-->
        <div class="biger-text-form-m">
            {{label.socialConnect}}
        </div>
        <!--facebook connected-->
        <div v-if="!userInfo.faceBookId" style="margin-top:25px;">
            <span class="fa fa-facebook-square" style="color:#c8c8c8"></span>
            <span class="small-text-form-r text-color-author">{{label.facebook}}</span>
        </div>
        <div v-if="!userInfo.faceBookId" v-on:mouseenter="displayConnectFb(true)" v-on:mouseleave="displayConnectFb(false)" class="row user-info-each">
            <div class="col-xs-6 medium-text-form-r text-limit" style="color:#c8c8c8">
                {{label.notConnectFb}}
            </div>
            <div class="col-xs-6 medium-text-form-r">
                <fb-signin-button
                    :params="fbSignInParams"
                    @success="linkFacebook"
                    @error="linkFacebookError"
                    class="btn btn-gat btn-circle button-social-min-width"
                    v-bind:style="{ display: connectFb? 'block':'none' }">
                    <span class="fa fa-link"></span>
                </fb-signin-button>
            </div>
        </div>
        <!--facebook un-connected-->
        <div v-if="userInfo.faceBookId" style="margin-top:25px;">
            <span class="fa fa-facebook-square" style="color:#4196c2"></span>
            <span class="small-text-form-r  text-color-author">{{label.facebook}}</span>
        </div>
        <div v-if="userInfo.faceBookId" v-on:mouseenter="displayConnectFb(true)" v-on:mouseleave="displayConnectFb(false)" class="row user-info-each">
            <div class="col-xs-6 medium-text-form-r text-limit">
                {{userInfo.faceBookName}}
            </div>
            <div class="col-xs-6 medium-text-form-r">
                <button v-if="connectFb" v-on:click="unlinkFacebook" class="btn btn-gat btn-circle button-min-width">
                    <span class="fa fa-chain-broken"></span>
                </button>
            </div>
        </div>

        <!--twitter connected-->
        <div v-if="!userInfo.twitterId" style="margin-top:25px;">
            <span class="fa fa-twitter-square" style="color:#c8c8c8"></span>
            <span class="small-text-form-r text-color-author">{{label.twitter}}</span>
        </div>
        <div v-if="!userInfo.twitterId" v-on:mouseenter="displayConnectTw(true)" v-on:mouseleave="displayConnectTw(false)" class="row user-info-each">
            <div class="col-xs-6 medium-text-form-r text-limit" style="color:#c8c8c8">
                {{label.notConnectTw}}
            </div>
            <div class="col-xs-6 medium-text-form-r">
                <a v-bind:href="twitter_authenticate">
                    <button
                        type="button" 
                        class="btn btn-gat btn-circle button-min-width"
                        v-bind:style="{ display: connectTw? 'block':'none' }">
                        <span class="fa fa-link"></span>
                    </button>
                </a>
            </div>
        </div>
        <!--twitter un-connected-->
        <div v-if="userInfo.twitterId" style="margin-top:25px;">
            <span class="fa fa-twitter-square" style="color:#00aeef"></span>
            <span class="small-text-form-r text-color-author">{{label.twitter}}</span>
        </div>
        <div v-if="userInfo.twitterId" v-on:mouseenter="displayConnectTw(true)" v-on:mouseleave="displayConnectTw(false)" class="row user-info-each">
            <div class="col-xs-6 medium-text-form-r text-limit">
                {{userInfo.twitterName}}
            </div>
            <div class="col-xs-6 medium-text-form-r">
                <button v-if="connectTw" v-on:click="unlinkTwitter" class="btn btn-gat btn-circle button-min-width">
                    <span class="fa fa-chain-broken"></span>
                </button>
            </div>
        </div>

        <!--google connected-->
        <div v-if="!userInfo.googleId" style="margin-top:25px;">
            <span class="fa fa-google-plus-square" style="color:#c8c8c8"></span>
            <span class="small-text-form-r text-color-author">{{label.google}}</span>
        </div>
        <div v-if="!userInfo.googleId" v-on:mouseenter="displayConnectGg(true)" v-on:mouseleave="displayConnectGg(false)" class="row user-info-each">
            <div class="col-xs-6 medium-text-form-r text-limit" style="color:#c8c8c8">
                {{label.notConnectGg}}
            </div>
            <div class="col-xs-6 medium-text-form-r">
                <g-signin-button
                    type="button" 
                    :params="googleSignInParams"
                    @success="linkGoogle"
                    @error="linkGoogleError"
                    class="btn btn-gat btn-circle button-social-min-width"
                    v-bind:style="{ display: connectGg? 'block':'none' }">
                    <span class="fa fa-link"></span>
                </g-signin-button>
            </div>
        </div>
        <!--google un-connected-->
        <div v-if="userInfo.googleId" style="margin-top:25px;">
            <span class="fa fa-google-plus-square" style="color:#ed1c24"></span>
            <span class="small-text-form-r  text-color-author">{{label.google}}</span>
        </div>
        <div v-if="userInfo.googleId" v-on:mouseenter="displayConnectGg(true)" v-on:mouseleave="displayConnectGg(false)" class="row user-info-each">
            <div class="col-xs-6 medium-text-form-r text-limit">
                {{userInfo.googleName}}
            </div>
            <div class="col-xs-6 medium-text-form-r">
                <button v-if="connectGg" v-on:click="unlinkGoogle" class="btn btn-gat btn-circle button-min-width">
                    <span class="fa fa-chain-broken"></span>
                </button>
            </div>
        </div>

        <!--break line-->
        <hr class="split-content">

        <div class="biger-text-form-m">
            {{label.mail_pass}}
        </div>

        <div class="small-text-form-r text-color-author" style="margin-top:25px;">
            {{label.emailAddress}}
        </div>
        <div v-on:mouseenter="displayEditEmail(true)" v-on:mouseleave="displayEditEmail(false)" class="row">
            <div v-if="!changingEmail">
                <div v-if="userInfo.passwordFlag==NON_PASSWORD_FLG" class="col-xs-6 medium-text-form-r text-color-author user-info-each">
                    {{label.emailNA}}
                </div>
                <div v-if="userInfo.passwordFlag==HAS_PASSWORD_FLG" class="col-xs-6 medium-text-form-r user-info-each">
                    {{userInfo.email}}
                </div>
                <div class="col-xs-6 medium-text-form-r user-info-each">
                    <button v-if="editEmail" v-on:click="setChangingEmail(true)" class="btn btn-gat btn-circle button-min-width">
                        <span class="glyphicon glyphicon-pencil"></span>
                    </button>
                </div>
            </div>
            <div v-if="changingEmail">
                <div class="col-xs-6 medium-text-form-r">
                    <div class="email-pass-row">
                        <div class="input-group" style="padding-top:6px;">
                            <span class="input-group-addon"><i class="fa fa-envelope"></i></span>
                            <input type="text" class="form-control center-input" :placeholder="label.emailAddress" v-model="emailTmp">
                        </div>
                    </div>
                    <div class="email-pass-row">
                        <div class="input-group" style="padding-top:6px;">
                            <span class="input-group-addon"><i class="glyphicon glyphicon-lock"></i></span>
                            <input type="password" class="form-control center-input" :placeholder="label.password" v-model="passwordTmp">
                        </div>
                    </div>
                    <div class="email-pass-row">
                        <button v-on:click="addEmailPassword" class="btn btn-gat btn-circle" style="width:70px">
                            {{label.save}}
                        </button>
                        <button v-on:click="setChangingEmail(false)" class="btn btn-gat-cancel btn-circle" style="width:70px">
                            {{label.cancel}}
                        </button>
                    </div>
                </div>
            </div>
        </div>

        <div v-if="userInfo.passwordFlag==HAS_PASSWORD_FLG" class="" style="margin-top:25px;">
            <button v-on:click="setChangingPass(true)" type="button" class="btn btn-link" style="padding:0">{{label.passChange}}</button>
            <div v-if="changingPass" class="row">
                <div class="col-xs-6 medium-text-form-r">
                    <div class="email-pass-row">
                        <input type="password" class="form-control center-input" :placeholder="label.currentPass" v-model="currentPassTmp">
                    </div>
                    <div class="email-pass-row">
                        <input type="password" class="form-control center-input" :placeholder="label.newPass" v-model="newPassTmp">
                    </div>
                    <div class="email-pass-row">
                        <button v-on:click="changePassword" class="btn btn-gat btn-circle" style="width:70px">
                            {{label.save}}
                        </button>
                        <button v-on:click="setChangingPass(false)" class="btn btn-gat-cancel btn-circle" style="width:70px">
                            {{label.cancel}}
                        </button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import LoadingDialog from '../../../common/dialog/LoadingDialog.vue'
import BookService from '../../../../services/BookService.js';
import VueGmap from '../../../common/vueGmap/VueGmap.vue';
import Label from '../../../../common/Label.js';
import * as CONST from '../../../../common/const.js';
export default {
    props: [ "userInfo" ],
    mixins: [ BookService, Label ],
    components: { VueGmap, LoadingDialog },
    computed: {
        currentAddress: function() {
            return this.userInfo.usuallyLocation? this.userInfo.usuallyLocation[0].address:"";
        },
        NON_PASSWORD_FLG: function() {
            return CONST.NON_PASSWORD_FLG;
        },
        HAS_PASSWORD_FLG: function() {
            return CONST.HAS_PASSWORD_FLG;
        }
    },
    mounted() {
        this.nameTmp = this.userInfo? this.userInfo.name : "";
        this.addressTmp = this.userInfo.usuallyLocation? this.userInfo.usuallyLocation[0].address : "";

        //for twitter
        //get param oauth_token & oauth_verifier from current url
        var oauth_token = this.$route.query.oauth_token;
        var oauth_verifier = this.$route.query.oauth_verifier;

        if(oauth_token && oauth_verifier){
            //if oauth_token & oauth_verifier are existed, call api_get_twitter_user
            var config = {
                params: {
                    oauthToken: oauth_token,
                    oauthVerifier: oauth_verifier
                }
            }
            this.api.callGetApi(this.api.api_get_twitter_user, config).then(this.linkTwitter, this.handleErrorFunction);
        }else{
            //esle, do get oauth_token
            this.api.callGetApi(this.api.api_get_twitter_oauth_token, {})
            .then(response => {this.twitter_authenticate = CONST.TWITTER_OAUTH_URL + "?" + response.data.data.resultInfo}, this.handleErrorFunction);
        }
    },
    watch: {
        userInfo: function(){
            this.nameTmp = this.userInfo? this.userInfo.name : "";
            this.addressTmp = this.userInfo.usuallyLocation? this.userInfo.usuallyLocation[0].address : "";
        }
    },
    methods: {
        //mouse in/out edit name button
        displayEditName(mode) {
            this.editName = mode;
        },
        //mouse in/out edit address button
        displayEditAddress(mode) {
            this.editAddress = mode;
        },
        //click edit name button
        setChangingName(mode){
            this.changingName = mode;
            if (!mode) {
                this.nameTmp = this.userInfo? this.userInfo.name : "";
            }
        },
        //click edit address button
        setChangingAddress(mode){
            this.changingAddress = mode;
            if (!mode) {
                this.addressTmp = this.userInfo.usuallyLocation? this.userInfo.usuallyLocation[0].address : "";
            }
        },
        //click save name button
        updateUserName(){
            this.isSystemExecuting = true;
            var params = {
                name: this.nameTmp,
                changeImageFlag: false
            };
            var headers = {
                'Authorization': this.loginToken
            }
            this.api.callPostApi(this.api.api_update_user_info, params, {headers})
                .then(this.updateUserNameSuccess, this.handleErrorFunction);
        },
        updateUserNameSuccess(response) {
            this.isSystemExecuting = false;
            this.userInfo.name = this.nameTmp;
            this.changingName = false;
            //update localforage       
            this.$getItem(CONST.LOGIN_USER).then(loginUser => 
                {
                    loginUser.name = this.nameTmp;
                    this.$setItem(CONST.LOGIN_USER, loginUser);
                    this.$emit('infoChanged', loginUser);
                }
            );
        },
        //click save address button
        updateUserLocation(){
            this.isSystemExecuting = true;
            var params = {
                address: this.addressTmp,
                latitude: this.positionTmp.lat,
                longitude: this.positionTmp.lng
            };
            var headers = {
                'Authorization': this.loginToken
            }
            this.api.callPostApi(this.api.api_update_usually_location, params, {headers})
                .then(this.updateUserLocationSuccess, this.handleErrorFunction);
        },
        updateUserLocationSuccess(response) {
            this.isSystemExecuting = false;
            this.userInfo.usuallyLocation[0].address = this.addressTmp;
            this.userInfo.usuallyLocation[0].latitude = this.positionTmp.lat;
            this.userInfo.usuallyLocation[0].longitude = this.positionTmp.lng;
            this.changingAddress = false;
            //update localforage  
            this.$getItem(CONST.LOGIN_USER).then(loginUser => 
                {
                    loginUser.usuallyLocation[0] = this.userInfo.usuallyLocation[0];
                    this.$setItem(CONST.LOGIN_USER, loginUser);
                    this.$emit('infoChanged', loginUser);
                }
            );
        },

        //display connect facebook button
        displayConnectFb(mode) {
            this.connectFb = mode;
        },
        //link to facebook
        linkFacebook() {
            FB.api('/me', dude => {
                this.isSystemExecuting = true;
                this.socialConnectParams = {
                    socialID: dude.id,                  //facebook id
                    socialName : dude.name,             //facebook name
                    socialType : CONST.FB_TYPE          //1
                };
                var headers = {
                    'Authorization': this.loginToken
                }
                this.api.callPostApi(this.api.api_link_social_acc, this.socialConnectParams, {headers})
                    .then(this.linkFacebookSuccess, this.handleErrorFunction);
            })
        },
        //cannot connect to facebook
        linkFacebookError (error) {
            console.log('Fb error: ', error)
        },
        //after call linkfacebook api success
        linkFacebookSuccess (response) {
            this.isSystemExecuting = false;
            this.userInfo.faceBookId = this.socialConnectParams.socialID;
            this.userInfo.faceBookName = this.socialConnectParams.socialName;

            //update localforage  
            this.$getItem(CONST.LOGIN_USER).then(loginUser => 
                {
                    loginUser.faceBookId = this.userInfo.faceBookId;
                    loginUser.faceBookName = this.userInfo.faceBookName;
                    this.$setItem(CONST.LOGIN_USER, loginUser);
                }
            );
        },
        //unlink facebook
        unlinkFacebook() {
            this.isSystemExecuting = true;
            var params = {
                socialType : CONST.FB_TYPE          //1
            };
            var headers = {
                'Authorization': this.loginToken
            }
            this.api.callPostApi(this.api.api_unlink_social_acc, params, {headers})
                .then(this.unlinkFacebookSuccess, this.handleErrorFunction);
        },
        //after call linkfacebook api success
        unlinkFacebookSuccess (response) {
            this.isSystemExecuting = false;
            this.userInfo.faceBookId = null;
            this.userInfo.faceBookName = null;

            //update localforage  
            this.$getItem(CONST.LOGIN_USER).then(loginUser => 
                {
                    loginUser.faceBookId = null;
                    loginUser.faceBookName = null;
                    this.$setItem(CONST.LOGIN_USER, loginUser);
                }
            );
        },

        //display connect google button
        displayConnectGg(mode) {
            this.connectGg = mode;
        },

        //link to google
        linkGoogle(googleUser) {
            this.isSystemExecuting = true;
            var profile = googleUser.getBasicProfile();
            this.socialConnectParams = {
                socialID: profile.Eea,               //google id
                socialName : profile.ig,             //google name
                socialType : CONST.GG_TYPE           //2
            };
            var headers = {
                'Authorization': this.loginToken
            }
            this.api.callPostApi(this.api.api_link_social_acc, this.socialConnectParams, {headers})
                .then(this.linkGoogleSuccess, this.handleErrorFunction);
        },
        //cannot connect to google
        linkGoogleError (error) {
            console.log('Gg error: ', error)
        },
        //after call linkgoogle api success
        linkGoogleSuccess (response) {
            this.isSystemExecuting = false;
            this.userInfo.googleId = this.socialConnectParams.socialID;
            this.userInfo.googleName = this.socialConnectParams.socialName;

            //update localforage  
            this.$getItem(CONST.LOGIN_USER).then(loginUser => 
                {
                    loginUser.googleId = this.userInfo.googleId;
                    loginUser.googleName = this.userInfo.googleName;
                    this.$setItem(CONST.LOGIN_USER, loginUser);
                }
            );
        },

        //unlink google
        unlinkGoogle() {
            this.isSystemExecuting = true;
            var params = {
                socialType : CONST.GG_TYPE          //2
            };
            var headers = {
                'Authorization': this.loginToken
            }
            this.api.callPostApi(this.api.api_unlink_social_acc, params, {headers})
                .then(this.unlinkGoogleSuccess, this.handleErrorFunction);
        },

        //after call unlinkfacebook api success
        unlinkGoogleSuccess (response) {
            this.isSystemExecuting = false;
            this.userInfo.googleId = null;
            this.userInfo.googleName = null;

            //update localforage  
            this.$getItem(CONST.LOGIN_USER).then(loginUser => 
                {
                    loginUser.googleId = null;
                    loginUser.googleName = null;
                    this.$setItem(CONST.LOGIN_USER, loginUser);
                }
            );
        },

        //display connect google button
        displayConnectTw(mode) {
            this.connectTw = mode;
        },

        //link account to twitter
        linkTwitter(response) {
            this.isSystemExecuting = true;
            const queryString = require('query-string');
            var parsed = queryString.parse(response.data.data.resultInfo);
            this.socialConnectParams = {
                socialID: parsed.user_id,            //twitter id
                socialName : parsed.screen_name,     //twitter name
                socialType : CONST.TW_TYPE           //3
            };
            var headers = {
                'Authorization': this.loginToken
            }
            this.api.callPostApi(this.api.api_link_social_acc, this.socialConnectParams, {headers})
                .then(this.linkTwitterSuccess, this.linkTwitterFailed);
        },

        //after call linktwitter api success
        linkTwitterSuccess(response) {
            this.isSystemExecuting = false;
            this.$router.replace({ name: CONST.NAME_USER_VISITOR_PAGE, params: {id: localStorage.getItem(CONST.USER_ID), tab: 'info-setting'}});
            this.userInfo.twitterId = this.socialConnectParams.socialID;
            this.userInfo.twitterName = this.socialConnectParams.socialName;

            //update localforage  
            this.$getItem(CONST.LOGIN_USER).then(loginUser => 
                {
                    loginUser.twitterId = this.userInfo.twitterId;
                    loginUser.twitterName = this.userInfo.twitterName;
                    this.$setItem(CONST.LOGIN_USER, loginUser);
                }
            );
        },

        //after call linktwitter api success
        linkTwitterFailed(error) {
            this.isSystemExecuting = false;
            this.$router.replace({ name: CONST.NAME_USER_VISITOR_PAGE, params: {id: localStorage.getItem(CONST.USER_ID), tab: 'info-setting'}});
            this.isApiserviceErrorDialogOpen = true,
            this.apiErrorDialogContent = error.response.data.message;
        },

        //unlink twitter
        unlinkTwitter() {
            this.isSystemExecuting = true;
            var params = {
                socialType : CONST.TW_TYPE          //2
            };
            var headers = {
                'Authorization': this.loginToken
            }
            this.api.callPostApi(this.api.api_unlink_social_acc, params, {headers})
                .then(this.unlinkTwitterSuccess, this.handleErrorFunction);
        },

        //after call unlinktwitter api success
        unlinkTwitterSuccess (response) {
            this.isSystemExecuting = false;
            this.api.callGetApi(this.api.api_get_twitter_oauth_token, {})
            .then(response => {this.twitter_authenticate = CONST.TWITTER_OAUTH_URL + "?" + response.data.data.resultInfo}, this.handleErrorFunction);
            this.userInfo.twitterId = null;
            this.userInfo.twitterName = null;

            //update localforage  
            this.$getItem(CONST.LOGIN_USER).then(loginUser => 
                {
                    loginUser.twitterId = null;
                    loginUser.twitterName = null;
                    this.$setItem(CONST.LOGIN_USER, loginUser);
                }
            );
        },

        //add/update email
        displayEditEmail(mode) {
            //update localforage  
            this.$getItem(CONST.LOGIN_USER).then(loginUser => 
                {
                    if (loginUser.passwordFlag == 0) {
                        this.editEmail = mode;
                    }
                }
            );
        },

        //show/hide add email input
        setChangingEmail(mode){
            this.changingEmail = mode;
            if (!mode) {
                this.emailTmp = "";
                this.passwordTmp = "";
            }
        },

        //show/hide change password input
        setChangingPass(mode) {
            this.changingPass = mode;
            if (!mode) {
                this.currentPassTmp = "";
                this.newPassTmp = "";
            }
        },

        //call add email api
        addEmailPassword() {
            this.isSystemExecuting = true;
            var params = {
                email: this.emailTmp,
                password : this.passwordTmp
            };
            var headers = {
                'Authorization': this.loginToken
            }
            this.api.callPostApi(this.api.api_add_email_pass, params, {headers})
                .then(this.addEmailSuccess, this.handleErrorFunction);
        },

        //after call add email api success
        addEmailSuccess(response) {
            this.isSystemExecuting = false;
            this.userInfo.email = this.emailTmp;
            this.userInfo.passwordFlag = CONST.HAS_PASSWORD_FLG;
            this.setChangingEmail(false);

            //update localforage  
            this.$getItem(CONST.LOGIN_USER).then(loginUser => 
                {
                    loginUser.email = this.userInfo.email;
                    loginUser.passwordFlag = this.userInfo.passwordFlag;
                    this.$setItem(CONST.LOGIN_USER, loginUser);
                }
            );
            this.$setItem(CONST.FIREBASE_PASS, response.data.data.firebasePassword);
        },

        //call change pass api
        //call add email api
        changePassword() {
            this.isSystemExecuting = true;
            var params = {
                oldPassword : this.currentPassTmp,
                newPassword : this.newPassTmp,
                uuid: window.uuid
            };
            var headers = {
                'Authorization': this.loginToken
            }
            this.api.callPostApi(this.api.api_change_password, params, {headers})
                .then(this.changePassSuccess, this.handleErrorFunction);
        },

        changePassSuccess(response) {
            this.isSystemExecuting = false;
            this.setChangingPass(false);
            this.$setItem(CONST.LOGIN_TOKEN, response.data.data.loginToken);
            this.$setItem(CONST.FIREBASE_PASS, response.data.data.firebasePassword);
        }
    },
    data() {
        return {

            //for info setting
            nameTmp: "",
            addressTmp: "",
            positionTmp: {},
            editName: false,
            editAddress: false,
            changingName: false,
            changingAddress: false,

            //for social setting
            socialConnectParams: {},

            connectFb: false,
            fbSignInParams: {
                scope: '',
                return_scopes: true
            },

            connectGg: false,
            googleSignInParams: {
                client_id: CONST.GOOGLE_CLIENT_ID,
                target: '_blank'
            },

            twitter_authenticate: '',
            connectTw: false,

            //email&password
            editEmail: false,
            changingEmail: false,
            emailTmp: "",
            passwordTmp: "",
            changingPass: false,
            currentPassTmp: "",
            newPassTmp: ""
        }
    }
}
</script>

