<template>
    <modal :name="dialogName" :clickToClose="false" :width="480" :height="250" class="dialog-eclipse">
        <div class="dialog-title biger-text-form-m">
            <button class="close" @click="reject">&times;</button>
            <div>{{label.dialogTitle}}</div>
        </div>
        <div v-html="content" class="confirm-dialog-content"></div>
        <div class="confirm-dialog-button" style="left:45px">
            <button v-on:click="accept" class="btn btn-gat btn-circle display-left" style="width:190px;">{{label.confirmButton}}</button>
            <button v-on:click="reject" class="btn btn-gat-cancel btn-circle display-right" style="width:190px;margin-left:10px;">{{label.ignoreButton}}</button>
        </div>
    </modal>
</template>

<script>
import Label from '../../../common/Label.js'
export default {
    props: ["dialogName", "isOpen", "content", "execute"],
    mixins: [Label],
    watch: {
        isOpen: function(){
            if(this.isOpen){
                this.$modal.show(this.dialogName);
            }else{
                this.$modal.hide(this.dialogName);
            }
        }
    },
    methods: {
        accept() {
            this.execute();
            this.$emit('dialogClosed');
        },
        reject() {
            this.$emit('dialogClosed');
        }
    },
    data() {
        return {
            
        }
    }
}
</script>

<style>

</style>



