<template>
    <div class="row container-gat box-margin-top list-book-container ">
        <meta-info :title="title" :ogTitle="title" :ogUrl="url" :ogImage="imageUrl" :ogDescription="description"></meta-info>
         <confirm-dialog
            :dialogName="'book-detail-confirm-dialog-'+ book.editionId"
            :isOpen="isShowConfirmDialog"
            :content="label.msg_request_login"
            :execute="goToLoginPage"
            @dialogClosed="isShowConfirmDialog = false"></confirm-dialog>
        <error-dialog :errorDialogName="'book-detail-error-dialog-'+ book.editionId"
            :isErrorOpen="isApiserviceErrorDialogOpen" 
            :errorMessage="apiErrorDialogContent" 
            :errorStatus="errorStatus"
            @errorDialogClosed="isApiserviceErrorDialogOpen = false"></error-dialog>
        <loading-dialog :loadingDialogName="'book-detail-loading-'+ book.editionId" :isLoadingOpen="isSystemExecuting"></loading-dialog>
        <div class="book-detail-content-left inline-block">
            <app-gat></app-gat>
        </div>
        <div class="book-detail-content-right inline-block">
            <div>
                <a href="\" class="text-color-category medium-text-form-m">{{label.home_page}}</a>
                <span class="text-color-category medium-text-form-m book-detail-margin-right-left">
                    <i class="fa fa-angle-double-right fa-lg" aria-hidden="true"></i>
                </span>
                <span v-if="isShowSearchPage">
                    <a href="#" class="text-color-category medium-text-form-m" @click="goBack">{{label.txt_result_seaching}}</a>
                    <span class="text-color-category medium-text-form-m book-detail-margin-right-left">
                        <i class="fa fa-angle-double-right fa-lg" aria-hidden="true"></i>
                    </span>
                </span>
                <span v-if="isShowShareBook">
                    <a href="#" class="text-color-category medium-text-form-m" @click="goBack">{{label.txt_list_share_book}}</a>
                    <span class="text-color-category medium-text-form-m book-detail-margin-right-left">
                        <i class="fa fa-angle-double-right fa-lg" aria-hidden="true"></i>
                    </span>
                </span>
                <span class="text-color-highlight medium-text-form-m">{{getContentLimitLength(book.title, 95)}}</span>
            </div>
            <div v-if="isShowBookLoading">
                <img src="../../../assets/images/loading.gif" style="width:50px; height: 50px;">
            </div>
            <div class="book-detail-content-right-container" v-else>
                <div class="book-detail-content-right-content ">
                    <book-detail-component @showLoadingDialog="isSystemExecuting = true" @hideLoadingDialog="isSystemExecuting = false" 
                        @showConfirmDialog="isShowConfirmDialog = true" @showErrorDialog="showErrorDialog" 
                        @setReadingStatus="setReadingStatus" @getInstanceInfoBook="getInstanceInfoBook"
                        @getBookshareTotal="getBookshareTotal" @getListUserShare="getListUserShare"
                        :readingObject="readingObject" :bookObject="book" :instanceInfo="instanceInfo"
                        :listUserShareObject="listUserShareObject" :loginUser="loginUser"></book-detail-component>
                    <div class="book-detail-content-right-descript">
                        <p class="super-biger-text-form-m">{{label.txt_descript}}</p>
                        <div class="book-detail-content-right-descript-content medium-text-form-r">
                            <span v-if="isShowLimitDescript">{{getContentLimitLength(book.description, maxLengthOfDescript)}}</span>
                            <span v-else>{{book.description}}</span>
                            <span v-if="isShowLimitDescript" class="medium-text-form-r text-color-highlight book-detail-descript-txt-show-more" @click="showMoreDescript">{{label.txt_show_more}}</span>
                        </div>
                    </div>
                    <hr class="line-break-color reset-padding reset-margin">
                    <list-user-review @showLoadingDialog="isSystemExecuting = true" @hideLoadingDialog="isSystemExecuting = false" @showConfirmDialog="isShowConfirmDialog = true" @showErrorDialog="showErrorDialog" :readingObject="readingObject" :bookId="book.bookId" :editionId="book.editionId"></list-user-review>
                    <div class="book-detail-content-right-related ">
                        <list-related-book :category="book.category" @showErrorDialog="showErrorDialog" ></list-related-book>
                    </div>
                    <div style="clear: both;"></div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import * as CONST from '../../../common/const.js';
import UserIcon from '../../common/userIcon/UserIcon';
import AppGat from '../../common/appGat/AppGat.vue';
import BookService from '../../../services/BookService.js';
import CommonService from '../../../services/CommonService.js';
import BookDetailComponent from '../component/bookDetailInfo/BookDetailInfo.vue';
import ListUserReview from '../component/listUserReview/ListUserReviewInDetailPage.vue';
import ListRelatedBook from '../component/listRelatedBook/ListRelatedBook.vue';
import Label from '../../../common/Label.js';
import MetaInfo from '../../common/metaInfo/MetaInfo.vue';
import ConfirmDialog from '../../common/dialog/ConfirmDialog.vue';
import ErrorDialog from '../../common/dialog/ErrorDialog.vue';
import LoadingDialog from '../../common/dialog/LoadingDialog.vue';

export default {
    name: 'book-detail',
    components: { AppGat, BookDetailComponent, ListUserReview, ListRelatedBook, MetaInfo, ConfirmDialog, ErrorDialog, LoadingDialog },
    mixins: [CommonService, BookService, Label],
    data() {
        return {
            isShowLimitDescript: false,
            isShowSearchPage: false,
            isShowShareBook: false,
            config: {
                params: {
                    editionId: 0
                }
            },
            config_get_list_user: {
                params: {
                    editionId: 0,
                    userId: 0,
                    per_page: 10,
                    page: 1,
                    latitude: null,
                    longitude: null,
                }
            },
            maxLengthOfDescript: 1200,
            title: 'GAT - GIVE AND TAKE THE BOOK',
            description: '...',
            imageUrl: this.api_image,
            url: CONST.BASE_URL,
            book: { imageId: null },
            readingObject: {},
            instanceInfo: {
                lostTotal: 0,
                notSharingTotal: 0,
                borrowingTotal: 0,
                sharingTotal: 0
            },
            listUserShareObject: { totalUserShare: {}, listUserShare: [] },
            callApiBookInfo: true,
            callApiSelfGetReadingStt: true,
            callApiSelfGetInstanceInfo: true,
            isShowConfirmDialog: false,
            isReadyGoogle: false,
            loginUser: {usuallyLocation:[{latitude: null, longitude: null}]},
        }
    },
    mounted() {
        this.initData();
    },
    computed: {
        isShowBookLoading() {
            return this.callApiBookInfo || this.callApiSelfGetReadingStt ;
        }
    },
    watch: {
        '$route.params.id': function() {
            this.resetData();
            this.initData();
        },
    },
    methods: {
        initData() {
            this.getLoginUser();
            this.url = window.location.href;
            if (this.isNotNull(window.isSearching) && window.isSearching > 0) {
                this.isShowSearchPage = true;
            } else {
                this.isShowSearchPage = false;
            }
            if (this.isNotNull(window.isShareBook) && window.isShareBook > 0) {
                this.isShowShareBook = true;
            } else {
                this.isShowShareBook = false;
            }
            this.config.params.editionId = this.$route.params.id;
            this.config_get_list_user.params.editionId = this.$route.params.id;
            this.config_get_list_user.params.userId = this.loginUserId;
            //After 6s, if can not load SDK google, set default location
            var self = this;
            setTimeout(function() {
                if(null == window.isRuningLoadSDKGoogle && window.isReadyGoogle) {
                    self.isReadyGoogle = window.isReadyGoogle;
                    // If current location is null, set default location
                    if (self.isNull(window.currentLocation)) {
                    window.currentLocation = CONST.QUOCLO_LOCATION;
                    }
                    self.loginUser.usuallyLocation[0].latitude = window.currentLocation.lat;
                    self.loginUser.usuallyLocation[0].longitude = window.currentLocation.lng;
                }
            }, 6000);

            // Get book info
            this.getBook();
            // Get reading status 
            this.getReadingStatus();
            // Get Instance status 
            this.getInstanceInfoBook();
            // Get total user sharing book
            this.getBookshareTotal();
        },
        setBook(response) {
            if (200 == response.status) {
                var responseData = response.data;
                this.book = responseData.data.resultInfo;
                //Set title of page
                // document.title = this.book.title;
                this.title = this.book.title;
                this.imageUrl = this.getUrlImage(this.book.imageId);
                this.description = this.book.description;
                if (this.isNotNull(this.description) && this.description.length - 3 >= this.maxLengthOfDescript) {
                    this.isShowLimitDescript = true;
                }
                window.callApiBookInfo = true;
            }
            this.callApiBookInfo = false; 
        },
        getBook() {
            window.callApiBookInfo = false;
            this.api.callGetApi(this.api.api_get_book_info, this.config).then(this.setBook, this.errorCallApiBookInfo);
        },
        showMoreDescript() {
            this.isShowLimitDescript = false;
        },
        getReadingStatus() {
            window.callApiSelfGetReadingStt = false;
            if (this.isNotNull(this.config.params.editionId) && this.isNotNull(this.loginToken)) {
                this.api.callGetApiWithAuthorization(this.api.api_selfget_reading_stt, this.config).then(this.setReadingStatus, this.errorCallApiSelfGetReadingStt);
            } else {
                this.callApiSelfGetReadingStt = false;
            }
        },
        setReadingStatus(response) {
            if (200 == response.status) {
                this.readingObject = response.data.data;
                window.callApiSelfGetReadingStt = true;
            }
            this.callApiSelfGetReadingStt = false;
        },
        getInstanceInfoBook() {
            window.callApiSelfGetInstanceInfo = false;
            if (this.isNotNull(this.config.params.editionId) && this.isNotNull(this.loginToken)) {
                this.api.callGetApiWithAuthorization(this.api.api_selfget_instance_info, this.config).then(this.setInstanceInfoBook, this.errorCallApiSelfGetInstanceInfo);
            } else {
                this.callApiSelfGetInstanceInfo = false;
            }
        },
        setInstanceInfoBook(response) {
            if (200 == response.status) {
                this.instanceInfo = response.data.data;
                window.callApiSelfGetInstanceInfo = true;
            }
            this.callApiSelfGetInstanceInfo = false;
        },
        getBookshareTotal() {
            window.callApiTotalUserShare = false;
            if (this.isNotNull(this.config.params.editionId)) {
                this.api.callGetApi(this.api.api_get_book_sharing_total, this.config).then(this.setBookshareTotal, this.errorCallApiTotalUserShare);
            } 
        },
        setBookshareTotal(response) {
            if (200 == response.status) {
                this.listUserShareObject.totalUserShare = response.data.data.resultInfo;
                window.callApiTotalUserShare = true;
            }
        },
        getListUserShare() {
            window.callApiListUserShare = false;
            this.api.callGetApi(this.api.api_get_edition_sharing_user, this.config_get_list_user).then(this.setListUserShare, this.errorCallApiListUserShare);
        },
        setListUserShare(response) {
            if (200 == response.status) {
                this.listUserShareObject.listUserShare = response.data.data.resultInfo;
                window.callApiListUserShare = true;
            } 
        },
        errorCallApiBookInfo(error) {
            this.callApiBookInfo = false;
            window.callApiBookInfo = false;
            this.showErrorDialog(error);
        },
        errorCallApiSelfGetReadingStt(error) {
            this.callApiSelfGetReadingStt = false;
            window.callApiSelfGetReadingStt = false;
            this.showErrorDialog(error);
        },
        errorCallApiSelfGetInstanceInfo(error) {
            this.callApiSelfGetInstanceInfo = false;
            window.callApiSelfGetInstanceInfo = false;
            this.showErrorDialog(error);
        },
        errorCallApiTotalUserShare(error) {
            window.callApiTotalUserShare = false;
            this.showErrorDialog(error);
        },
        errorCallApiListUserShare(error) {
            window.callApiListUserShare = false;
            this.showErrorDialog(error);
        },
        resetData() {
            this.isShowLimitDescript = false;
            this.isShowSearchPage = false;
            this.isShowShareBook = false;
            this.book = { imageId: null };
            this.config = {
                params: {
                    editionId: 0
                }
            };
            this.maxLengthOfDescript = 1200;
            this.title = 'GAT - GIVE AND TAKE THE BOOK';
            this.description = '...';
            this.imageUrl = this.api_image;
            this.url = CONST.BASE_URL;
            this.readingObject = {};
            this.instanceInfo = {
                lostTotal: 0,
                notSharingTotal: 0,
                borrowingTotal: 0,
                sharingTotal: 0
            };
            this.callApiBookInfo = true;
            this.callApiSelfGetReadingStt = true;
            this.callApiSelfGetInstanceInfo = true;

            this.config_get_list_user = {
                params: {
                    editionId: 0,
                    userId: 0,
                    per_page: 10,
                    page: 1,
                    latitude: null,
                    longitude: null,
                }
            };
            this.listUserShareObject = { totalUserShare: {}, listUserShare: [] };
            this.isShowConfirmDialog = false;
            if(null == window.isRuningLoadSDKGoogle && window.isReadyGoogle && this.isNotNull(window.currentLocation)) {
                this.loginUser.usuallyLocation.latitude = window.currentLocation.lat;
                this.loginUser.usuallyLocation.longitude = window.currentLocation.lng;
            }
            this.config_get_list_user = {
                params: {
                    editionId: 0,
                    userId: 0,
                    per_page: 10,
                    page: 1,
                    latitude: null,
                    longitude: null,
                }
            };
        },
        showErrorDialog(error){
            if (error.response.data.message) {
                this.apiErrorDialogContent = error.response.data.message;
            } else{
                this.apiErrorDialogContent = error;
            }
            this.isApiserviceErrorDialogOpen = true;
        },
        getLoginUser(){
            if (this.isNotNull(this.loginToken)) {
                var self = this;
                this.$getItem(CONST.LOGIN_USER).then(response => {
                    self.loginUser = response;
                    if(self.isNull(self.loginUser.usuallyLocation)){
                        self.loginUser.usuallyLocation = [];
                    }
                    if(null == window.isRuningLoadSDKGoogle && window.isReadyGoogle && self.isNotNull(window.currentLocation)) {
                        self.loginUser.usuallyLocation[0].latitude = window.currentLocation.lat;
                        self.loginUser.usuallyLocation[0].longitude = window.currentLocation.lng;
                    }
                    self.config_get_list_user.params.latitude = self.loginUser.usuallyLocation[0].latitude;
                    self.config_get_list_user.params.longitude = self.loginUser.usuallyLocation[0].longitude;
                });
            }
            // get list user sharing book
            this.getListUserShare();
        }
    }
}
</script>

<style lang="scss">
@import '../../../scss/text_styles.scss';
@import '../../../scss/common_styles.scss';
@import 'BookDetail.scss';
</style>



