<template>
    <div class="box-book-grid">
        <loading-dialog :loadingDialogName="'update-request-loading-'+book.userId+'-'+book.editionId" :isLoadingOpen="isSystemExecuting"></loading-dialog>
        <error-dialog
            :errorDialogName="'borrow-fail-'+book.userId+'-'+book.editionId"
            :isErrorOpen="isApiserviceErrorDialogOpen"
            :errorMessage="apiErrorDialogContent"
            :errorStatus="errorStatus"
            @errorDialogClosed="isApiserviceErrorDialogOpen = false"></error-dialog>
        <borrow-dialog
            :dialogName="'borrow-confirm-'+book.userId+'-'+book.editionId"
            :isOpen="isDialogOpen"
            :book="book"
            @requestSent="requestSent"
            @requestComplete="requestComplete"
            @requestFailed="requestFailed"
            @dialogClosed="isDialogOpen = false"></borrow-dialog>
        <div v-if="!inMain" class="row reset-margin">
            <div class="col-xs-5">
                <div class="mostbook-image-form">
                    <a v-if="label.PATH_BOOK_DETAIL_PAGE" :href="label.PATH_BOOK_DETAIL_PAGE.replace(':id',book.editionId)">
                        <img :title="book.title" :src="getUrlImage(book.imageId)" class="mostbook-image" :alt="book.title">
                    </a>
                </div>
            </div>
            <div class="col-xs-7">
                <div class="title-info">
                    <a v-if="label.PATH_BOOK_DETAIL_PAGE" :title="book.title" :href="label.PATH_BOOK_DETAIL_PAGE.replace(':id',book.editionId)">
                        <span class="medium-text-form-m text-limit text-muted title-name-color">{{book.title}}</span>
                    </a>
                </div>
                <div style="margin-top:7px;">
                    <span v-if="book.author" class="small-text-form-r text-limit text-color-author">{{book.author}}</span>
                    <span v-if="!book.author" class="small-text-form-r text-limit text-color-author">&nbsp</span>
                </div>
                <div class="rate-avg" style="margin-top:10px;">
                    <span class="left-side">
                        <star-rating 
                            :rating="book.rateAvg" 
                            :read-only="true" 
                            :increment="0.01" 
                            :show-rating="false" 
                            inactive-color="white" 
                            border-color="#f4c312" 
                            :border-width="1"
                            :star-size="14">
                        </star-rating>
                    </span>
                </div>
                <div v-if="book.sharingStatus!=STT_READ_IN_PLACE&&book.requestingStatus==0" style="margin-top:46px;">
                    <button v-if="book.availableStatus==1" v-on:click="openBorrowConfirm(true)" class="btn btn-gat btn-circle btn-fulfill">{{label.sendBorrow}}</button>
                    <button v-if="book.availableStatus==0" v-on:click="openBorrowConfirm(false)" class="btn btn-gat-wait btn-circle btn-fulfill">{{label.sendOnhold}}</button>
                </div>
                <div v-if="book.sharingStatus!=STT_READ_IN_PLACE&&book.requestingStatus==1" style="margin-top:46px;">
                    <router-link :to="{ name: label.NAME_USER_REQUEST_DETAIL_PAGE, params: {id: book.recordId}}">
                        {{label.recordStatus(book.recordStatus)}}
                    </router-link>
                </div>
                <div v-if="book.sharingStatus==STT_READ_IN_PLACE" style="margin-top:52px;">
                    <img class="inline-block" src="../../../assets/images/book-cafe.png" style="width:20px;height:20px;">
                    <div class="inline-block small-text-form-r">{{label.read_in_place_book}}</div>
                </div>
            </div>
        </div>
        <figure v-else>
            <div class="box-image">
                <a v-if="label.PATH_BOOK_DETAIL_PAGE" :href="label.PATH_BOOK_DETAIL_PAGE.replace(':id',book.editionId)">
                    <img :title="book.title" :src="getUrlImage(book.imageId)" class="image-fill img-book" :alt="book.title">
                </a>
            </div>
            <div class="title-info">
                <a v-if="label.PATH_BOOK_DETAIL_PAGE" :title="book.title" :href="label.PATH_BOOK_DETAIL_PAGE.replace(':id',book.editionId)">
                    <span class="medium-text-form-m text-limit text-muted title-name-color">{{book.title}}</span>
                </a>
            </div>
            <div class="author-info">
                <span v-if="book.author" class="small-text-form-r text-limit text-color-author">{{book.author}}</span>
                <span v-if="!book.author" class="small-text-form-r text-limit text-color-author">&nbsp</span>
            </div>
            <div v-if="book.sharingStatus!=STT_READ_IN_PLACE&&book.requestingStatus==0" style="margin-top:10px;">
                <button v-if="book.availableStatus==1" v-on:click="openBorrowConfirm(true)" class="btn btn-gat btn-circle btn-fulfill">{{label.sendBorrow}}</button>
                <button v-if="book.availableStatus==0" v-on:click="openBorrowConfirm(false)" class="btn btn-gat-wait btn-circle btn-fulfill">{{label.sendOnhold}}</button>
            </div>
            <div v-if="book.sharingStatus!=STT_READ_IN_PLACE&&book.requestingStatus==1" style="margin-top:17px;">
                <router-link :to="{ name: label.NAME_USER_REQUEST_DETAIL_PAGE, params: {id: book.recordId}}">
                    {{label.recordStatus(book.recordStatus)}}
                </router-link>
            </div>
            <div v-if="book.sharingStatus==STT_READ_IN_PLACE" style="margin-top:21px;">
                <img class="inline-block" src="../../../assets/images/book-cafe.png" style="width:20px;height:20px;">
                <div class="inline-block small-text-form-r">{{label.read_in_place_book}}</div>
            </div>
        </figure>
    </div>
</template>

<script>
import BorrowDialog from '../../common/dialog/BorrowDialog.vue'
import LoadingDialog from '../../common/dialog/LoadingDialog.vue'
import CommonService from '../../../services/CommonService.js'
import BookService from '../../../services/BookService.js'
import StarRating from 'vue-star-rating'
import * as CONST from '../../../common/const.js'
import Label from '../../../common/Label.js'
export default {
    props: ["book", "inMain"],
    components: { BorrowDialog, LoadingDialog, StarRating },
    mixins: [ Label, CommonService, BookService],
    methods: {
        openBorrowConfirm(isBorrow) {
            this.isDialogOpen = true;
        },
        requestSent() {
            this.isSystemExecuting = true;
        },
        requestComplete() {
            this.isSystemExecuting = false;
        },
        requestFailed($event) {
            this.isSystemExecuting = false;
            this.apiErrorDialogContent = $event;
            this.isApiserviceErrorDialogOpen = true;
        }
    },
    data() {
        return {
            isDialogOpen: false,
            STT_READ_IN_PLACE: CONST.STT_READ_IN_PLACE
        }
    }
}
</script>

<style>

</style>



