<template>
    <div class="heading-background">
        <loading-dialog :loadingDialogName="'sign-out-loading'" :isLoadingOpen="isSystemExecuting"></loading-dialog>
        <error-dialog
            :errorDialogName="'email-login-fail'"
            :isErrorOpen="isApiserviceErrorDialogOpen"
            :errorMessage="apiErrorDialogContent"
            :errorStatus="errorStatus"
            @errorDialogClosed="isApiserviceErrorDialogOpen = false"></error-dialog>
        <div class="container-gat reset-padding" style="height: 60px;">
            <div class="header-left inline-block">
                <router-link :to="{ name: 'home'}">
                    <img src="../../../assets/images/icon_gatBook_white.png" class="img-responsive header-icon" alt="Gat">
                </router-link>
            </div>

            <div class="header-middle inline-block">
                <div class="input-group box-search">
                    <div class="input-group-btn search-panel">
                        <button id="btn-search-filter" class="btn dropdown-toggle search-background search-filter search-radius-left text-color-highlight" style="width: 150px; z-index: 15; outline: 0; border: 0px;">
                            <span id="btn-search-filter" class="biger-text-form-r text-color-highlight">{{search_type}}</span>
                            <i id="btn-search-filter" class="fa fa-angle-down fa-lg right-side text-color-highlight" style="margin-top: 5px;" aria-hidden="true"></i>
                        </button>
                        <div class="search-filter-list" v-if="isShowExtension">
                            <div style="height: 30px;"></div>
                            <div class="biger-text-form-r search-filter-item"
                                v-for="(item, index) in getArraySearchBy" @click="selectFilter(item)" :key="index">
                                {{item}}
                            </div>
                        </div>
                    </div>
                    <input id="btn-search-history" type="text" v-on:keyup.enter="search(search_type, search_text)" class="form-control search-background" v-model="search_text" name="x" 
                      @click="getSearchHistory" :placeholder="label.txt_enter_key" style="border: 0px; outline: 0; z-index: 15;">
                    <header-search-history @search="search" :searchHistory="searchHistory" :searchType="search_type" v-if="isShowSearchHistory"></header-search-history>
                    <span class="input-group-btn" style="border: 0px; outline: 0; z-index: 15;">
                        <button class="btn search-background search-radius-right" type="button" @click="search(search_type, search_text)" style="border: 0px;outline: 0;">
                            <span class="glyphicon glyphicon-search" style="color:#4196c2;transform:scale(1.5);"></span>
                        </button>
                    </span>
                </div>
            </div>

            <div v-if="!loginMode" class="header-right inline-block">
                <div class="header-user-action">
                    <router-link :to="{ name: label.NAME_LOGIN_PAGE, params: {tab: 'signin'}}">
                        <span class="medium-text-form-r">{{label.txt_login}}</span>
                    </router-link>
                    <div class="inline-block" style="color:white;"> | </div>
                    <router-link :to="{ name: label.NAME_LOGIN_PAGE, params: {tab: 'signup'}}">
                        <span class="medium-text-form-r">{{label.txt_register}}</span>
                    </router-link>
                </div>
                <user-icon :userIconStyle="userIconStyle" :imageId="''"></user-icon>
                <!-- multi language -->
                <div class="language-btn">
                    <div id="btn-language-filter" class="btn dropdown-toggle language-filter" :class="{'color-white':!isShowLanguageFilter, 'color-navi':isShowLanguageFilter}">
                        <span id="btn-language-filter" class="medium-text-form-r">{{language}}</span>
                        <i id="btn-language-filter" class="fa fa-angle-down fa-lg right-side" style="margin-top:5px;" aria-hidden="true"></i>
                    </div>
                    <div class="language-filter-list" v-if="isShowLanguageFilter">
                        <div style="height:15px;"></div>
                        <div class="medium-text-form-r language-filter-item"
                            v-for="(item, index) in getArrayLangFilter" @click="selectLanguage(item)" :key="index">
                            {{item}}
                        </div>
                    </div>
                </div>
            </div>

            <div v-else class="header-right inline-block">
                <div v-on:click="userNavToggle" id="user-navigation-header" class="user-navigation-header"></div>
                <div class="header-split"></div>
                <div v-on:click="userReqToggle" id="user-request-header" class="notice-circle mouse-pointer">
                    <img id="user-request-core" src="../../../assets/images/icon_request.png" alt="user" class="img-rounded icon-center">
                    <div v-if="waitToConfirmCount > 0" class="number-item mini-text-form-r">
                        {{waitToConfirmCount}}
                    </div>
                </div>
                <div v-on:click="userMesToggle" id="user-message-header" class="notice-circle mouse-pointer">
                    <img id="user-message-core" src="../../../assets/images/icon_message.png" alt="user" class="img-rounded  icon-center">
                    <div v-if="unReadMessage > 0" class="number-item mini-text-form-r">
                        {{unReadMessage}}
                    </div>
                </div>
                <div v-on:click="userNotiToggle" id="user-notification-header" class="notice-circle mouse-pointer">
                    <img id="user-notification-core" src="../../../assets/images/icon_notification.png" alt="user" class="img-rounded  icon-center">
                    <div v-if="unReadNotification > 0" class="number-item mini-text-form-r">
                        {{unReadNotification}}
                    </div>
                </div>
                <router-link :to="{ name: label.NAME_USER_VISITOR_PAGE, params: {id: loginUserId,tab: 'book-shelf'}}">
                    <user-icon :userIconStyle="userIconStyle" :imageId="imageId"></user-icon>
                </router-link>
                <!-- multi language -->
                <div class="language-btn">
                    <div id="btn-language-filter" class="btn dropdown-toggle language-filter" :class="{'color-white':!isShowLanguageFilter, 'color-navi':isShowLanguageFilter}">
                        <span id="btn-language-filter" class="medium-text-form-r">{{language}}</span>
                        <i id="btn-language-filter" class="fa fa-angle-down fa-lg right-side" style="margin-top:5px;" aria-hidden="true"></i>
                    </div>
                    <div class="language-filter-list" v-if="isShowLanguageFilter">
                        <div style="height:15px;"></div>
                        <div class="medium-text-form-r language-filter-item"
                            v-for="(item, index) in getArrayLangFilter" @click="selectLanguage(item)" :key="index">
                            {{item}}
                        </div>
                    </div>
                </div>
                <header-user-notification-list v-if="userNotiOpenFlag" :notifyList="notifyList"></header-user-notification-list>
                <header-user-message-list v-if="userMesOpenFlag" :messagedList="messagedList.slice(0,per_page)"></header-user-message-list>
                <header-user-request-list v-if="userReqOpenFlag" :requestList="requestList"></header-user-request-list>
                <header-user-navigation v-if="userNavOpenFlag" :logout="logout"></header-user-navigation>
            </div>
        </div>
    </div>
</template>

<script>
import * as CONST from '../../../common/const.js'
import Label from '../../../common/Label.js'
import BookService from '../../../services/BookService.js'
import CommonService from '../../../services/CommonService.js'
import UserIcon from '../../common/userIcon/UserIcon'
import LoadingDialog from '../dialog/LoadingDialog.vue'
import HeaderUserNotificationList from './HeaderUserNotificationList.vue'
import HeaderUserNavigation from './HeaderUserNavigation.vue'
import HeaderUserRequestList from './HeaderUserRequestList.vue'
import HeaderUserMessageList from './HeaderUserMessageList.vue'
import ConfirmDialog from '../dialog/ConfirmDialog.vue'
import HeaderSearchHistory from './HeaderSearchHistory.vue'

let messaging;
if ('serviceWorker' in navigator) {
    firebase.initializeApp(CONST.FIREBASE_CONFIG);
    messaging = firebase.messaging();
    navigator.serviceWorker.register('../../../static/firebase-messaging-sw.js')
    .then((registration) => {
        messaging.useServiceWorker(registration);
    })
}

export default {
    name: "header-gat",
    props: [ "imageId", "getRequestToggle"],
    mixins: [ Label, BookService, CommonService],
    components: { ConfirmDialog, UserIcon, LoadingDialog, HeaderUserNotificationList, HeaderUserNavigation, HeaderUserRequestList, HeaderUserMessageList, HeaderSearchHistory },

    mounted() {
        this.search_type = this.label.search_by[0];
        this.getSearchHistory();

        this.setMode();
        var _self = this;
        if ('serviceWorker' in navigator) {
            messaging.onMessage(function(payload) {
                _self.getNotificationList();
                _self.getRequestList();
            });
            messaging.onTokenRefresh(function() {
                messaging.getToken()
                .then(function(refreshedToken) {
                    // Indicate that the new Instance ID token has not yet been sent to the
                    // app server.
                    var params = {
                        firebaseToken: refreshedToken,
                        uuid: window.uuid
                    }
                    var headers = {
                        'Authorization': localStorage.getItem(CONST.LOGIN_TOKEN)
                    }
                    _self.api.callPostApi(_self.api.api_firebase_token_register, params, {headers})
                    .then(response => {
                    })
                    .catch(_self.handleErrorFunction);
                    // ...
                })
                .catch(function(err) {
                    console.log('Unable to retrieve refreshed token ', err);
                });
            });
        }
        $(window).click(function(event) {
            if (event.target.id != "user-notification-header" && event.target.id != "user-notification-core") {
                _self.userNotiOpenFlag = false;
            }
            if (event.target.id != "user-message-header" && event.target.id != "user-message-core") {
                _self.userMesOpenFlag = false;
            }
            if (event.target.id != "user-request-header" && event.target.id != "user-request-core") {
                _self.userReqOpenFlag = false;
            }
            if (event.target.id != "user-navigation-header") {
                _self.userNavOpenFlag = false;
            }
            if (event.target.id != "btn-search-filter") {
                _self.isShowExtension = false;
            } else if(event.target.id == "btn-search-filter") {
                _self.isShowExtension = !_self.isShowExtension;
            }
            if (event.target.id != "btn-language-filter") {
                _self.isShowLanguageFilter = false;
            } else if(event.target.id == "btn-language-filter") {
                _self.isShowLanguageFilter = !_self.isShowLanguageFilter;
            }
            if(_self.isNotNull(_self.loginUserId)){
                if(event.target.id != "btn-search-history") {
                    _self.isShowSearchHistory = false;
                } else if(event.target.id == "btn-search-history") {
                    _self.isShowSearchHistory = !_self.isShowSearchHistory;
                }
            }
        });
    },

    computed: {
        unReadMessage: function() {
            var num = 0;
            for (var i=0; i<this.messagedList.length; i++) {
                if (this.messagedList[i].userId != localStorage.getItem(CONST.USER_ID) && !this.messagedList[i].isRead) {
                    num += 1;
                }
            }
            return num>99? 99:num;
        },
        getArraySearchBy(){
            var arr = arr = this.label.search_by.filter(item => item !== this.search_type);
            return arr;
        },
        getArrayLangFilter(){
            var arr = arr = this.language_filter.filter(item => item !== this.language);
            return arr;
        }
        // unReadNotification: function() {
        //     var num = 0;
        //     for (var i=0; i<this.notifyList.length; i++) {
        //         if (!this.notifyList[i].pullFlag) {
        //             num += 1;
        //         }
        //     }
        //     return num>99? 99:num;
        // }
    },

    watch: {
        '$route': function () {
            this.setMode();
            if (this.loginToken) {
                this.getNotificationList();
                this.getRequestList();
            }
        },
        '$route.name': function() {
            var nameOfPage = this.$route.name;
            if (this.label.NAME_SEARCH_BOOK_PAGE != nameOfPage &&
                this.label.NAME_SEARCH_AUTHOR_PAGE != nameOfPage &&
                this.label.NAME_SEARCH_USER_PAGE != nameOfPage) {
                switch (nameOfPage) {
                    case this.label.NAME_HOME_PAGE:
                        this.search_text = window.home;
                        break;

                    case this.label.NAME_USER_REQUEST_PAGE:
                        this.search_text = window.userRequest;
                        break;

                    case this.label.NAME_USER_REQUEST_DETAIL_PAGE:
                        this.search_text = window.userRequestDetail;
                        break;

                    case this.label.NAME_USER_VISITOR_PAGE:
                        this.search_text = window.userVisitor;
                        break;

                    case this.label.NAME_USER_MESSAGE_PAGE:
                        this.search_text = window.userMessage;
                        break;
                        
                    case this.label.NAME_USER_MESSAGE_DETAIL_PAGE:
                        this.search_text = window.userMessageDetail;
                        break;

                    case this.label.NAME_NEAR_BY_USER:
                        this.search_text = window.nearByUser;
                        break;

                    case this.label.NAME_SHARE_BOOKS_PAGE:
                        this.search_text = window.listShareBooks;
                        break;

                    case this.label.NAME_BOOK_DETAIL_PAGE:
                        this.search_text = window.bookDetail;
                        break;

                    case this.label.NAME_EVENT_DETAIL_PAGE:
                        this.search_text = window.eventDetail;
                        break;

                    case this.label.NAME_LIST_BOOKSTOPS_PAGE:
                        this.search_text = window.listBookstops;
                        break;

                    case this.label.NAME_BOOKSTOP_DETAIL_PAGE:
                        this.search_text = window.bookstopDetail;
                        break;
                    default:
                        this.search_text = "";
                        break;
                }
            }
        },
        loginMode: function (to) {
            if (to) {
                this.getMessageList();
                this.getRequestList();
                this.getNotificationList();
                this.firebaseNotificationRegist();
            }
        },
        getRequestToggle: function() {
            this.getNotificationList();
            this.getRequestList();
        }
    },

    data() {
        return {
            requestList: [],
            waitToConfirmCount: 0,
            messagedList: [],
            notifyList: [],
            unReadNotification: 0,
            per_page: 5,
            search_type: '',
            search_text: "",
            loginMode: false,
            userIconStyle: {
                'background-color': 'white',
                'border-radius': '18px',
                'width': '36px',
                'height': '36px',
                'float': 'right'
            },
            userNotiOpenFlag: false,
            userMesOpenFlag: false,
            userReqOpenFlag: false,
            userNavOpenFlag: false,
            isShowExtension: false,
            isShowLanguageFilter: false,
            isShowSearchHistory: false,
            searchHistory: {userSearchHistory: null, titleSearchHistory: null, authorSearchHistory: null}
        }
    },

    methods: {
        userNotiToggle() {
            this.userNotiOpenFlag = !this.userNotiOpenFlag;
        },
        userMesToggle() {
            this.messagedList.sort(function(a,b) {return (a.timeStamp < b.timeStamp) ? 1 : ((b.timeStamp < a.timeStamp) ? -1 : 0);} );           
            this.userMesOpenFlag = !this.userMesOpenFlag;
        },
        userReqToggle() {
            this.userReqOpenFlag = !this.userReqOpenFlag;
        },
        userNavToggle() {
            this.userNavOpenFlag = !this.userNavOpenFlag;
        },
        setMode() {
            this.loginMode = localStorage.getItem(CONST.LOGIN_TOKEN) ? true : false;
        },
        search(type, text) {
            this.isShowSearchHistory = false;
            this.search_text = text;
            this.setContentOfSearching(text);
            var self = this;
            if (text) {
                switch (type) {
                    case this.label.search_by[0]:
                        this.$router.push({ name: CONST.NAME_SEARCH_BOOK_PAGE, query: { str: this.search_text } });
                        if(this.isNotNull(this.searchHistory.titleSearchHistory)){
                            var indexOf = this.searchHistory.titleSearchHistory.findIndex(item => item.keyword==this.search_text);
                            if(indexOf >= 0){
                                this.searchHistory.titleSearchHistory.splice(indexOf, 1);
                            }
                            this.searchHistory.titleSearchHistory.unshift({keyword: this.search_text});
                            if(this.searchHistory.titleSearchHistory.length == 11){
                                this.searchHistory.titleSearchHistory.splice(10, 1);
                            }
                            this.$setItem(CONST.TITLE_SEARCH_HISTORY, this.searchHistory.titleSearchHistory);
                        }
                        break;
                    case this.label.search_by[1]:
                        this.$router.push({ name: CONST.NAME_SEARCH_AUTHOR_PAGE, query: { str: this.search_text } });
                        if(this.isNotNull(this.searchHistory.authorSearchHistory)) {
                            var indexOf = this.searchHistory.authorSearchHistory.findIndex(item => item.keyword==this.search_text);
                            if(indexOf >= 0){
                                this.searchHistory.authorSearchHistory.splice(indexOf, 1);
                            }
                            this.searchHistory.authorSearchHistory.unshift({keyword: this.search_text});
                            if(this.searchHistory.authorSearchHistory.length == 11){
                                this.searchHistory.authorSearchHistory.splice(10, 1);
                            }
                            this.$setItem(CONST.AUTHOR_SEARCH_HISTORY, this.searchHistory.authorSearchHistory);
                        }
                        break;
                    case this.label.search_by[2]:
                        this.$router.push({ name: CONST.NAME_SEARCH_USER_PAGE, query: { str: this.search_text } });
                        if(this.isNotNull(this.searchHistory.userSearchHistory)) {
                            var indexOf = this.searchHistory.userSearchHistory.findIndex(item => item.keyword==this.search_text);
                            if(indexOf >= 0){
                                this.searchHistory.userSearchHistory.splice(indexOf, 1);
                            }
                            this.searchHistory.userSearchHistory.unshift({keyword: this.search_text});
                            if(this.searchHistory.userSearchHistory.length == 11){
                                this.searchHistory.userSearchHistory.splice(10, 1);
                            }
                            this.$setItem(CONST.USER_SEARCH_HISTORY, this.searchHistory.userSearchHistory);
                        }
                        break;
                    default:
                        this.$router.push({ name: CONST.NAME_SEARCH_BOOK_PAGE, query: { str: this.search_text } });
                        if(this.isNotNull(this.searchHistory.titleSearchHistory)){
                            var indexOf = this.searchHistory.titleSearchHistory.findIndex(item => item.keyword==this.search_text);
                            if(indexOf >= 0){
                                this.searchHistory.titleSearchHistory.splice(indexOf, 1);
                            }
                            this.searchHistory.titleSearchHistory.unshift({keyword: this.search_text});
                            if(this.searchHistory.titleSearchHistory.length == 11){
                                this.searchHistory.titleSearchHistory.splice(10, 1);
                            }
                            this.$setItem(CONST.TITLE_SEARCH_HISTORY, this.searchHistory.titleSearchHistory);
                        }
                        break;
                }
            } else {
                this.apiErrorDialogContent = this.label.txt_warning_search_empty;
                this.isApiserviceErrorDialogOpen = true;
            }
        },

        selectFilter(text) {
            this.search_type = text;
        },

        selectLanguage(text) {
            localStorage.setItem("language", text);
            location.reload();
        },

        getMessageList() {
            var _self = this;
            //firebase db (tabel messages) refer
            let messagesRef =  window.firebaseDB.ref('messages');
            let loginUserId = localStorage.getItem(CONST.USER_ID);
            //get users key
            messagesRef.on('child_added', function (data) {
                //get userids in conversation
                let uic =  data.key.split(':');
                if(uic[0] == loginUserId || uic[1] == loginUserId){
                    //on child added event
                    messagesRef.child(data.key).orderByChild('timeStamp').limitToLast(1).on('child_added', function (subData) {
                        //get target user
                        var targetId = loginUserId == uic[0]? uic[1]:uic[0];
                        //check each child node
                        var child = {
                            targetId: targetId,
                            isRead: subData.val().isRead,
                            message: subData.val().userId == loginUserId ? _self.label.you + ": " + subData.val().message : subData.val().message,
                            timeStamp: subData.val().timeStamp,
                            userId: subData.val().userId
                        }
                        //exist flag
                        var isExist = false;
                        //position of existed item
                        var position = 0;
                        _.each (_self.messagedList, function(item, index) {
                            if (item.targetId == child.targetId) {
                                isExist = true;
                                position = index;
                                return;
                            }
                        });
                        //if exist, replace item at [position] by child if child.timeStamp is bigger; else, push child node into list
                        if (isExist) {
                            if (_self.messagedList[position].timeStamp < child.timeStamp) {
                                _self.messagedList.splice(position,1);
                                _self.messagedList.unshift(child);
                            }
                        } else {
                            _self.messagedList.unshift(child);
                        }
                    });

                    //on child changed event
                    messagesRef.child(data.key).orderByChild('timeStamp').limitToLast(1).on('child_changed', function (subData) {
                        _.each (_self.messagedList, function(item, index) {
                            if (item.targetId == subData.val().userId) {
                                item.isRead = true;
                            }
                        });
                    });
                }
            });
        },

        getNotificationList() {
            // let _self = this;
            // _self.notifyList = [];
            // let loginUserId = localStorage.getItem(CONST.USER_ID);
            // //firebase db (tabel notifications) refer
            // let notificationsRef = window.firebaseDB.ref('notifications');
            // notificationsRef.child(loginUserId).on('child_added', function (data) {
            //     var node = data.val();
            //     node.key = data.key;
            //     _self.notifyList.unshift(node);
            // })
            // //on child changed event
            // notificationsRef.child(loginUserId).on('child_changed', function (data) {
            //     _.each (_self.notifyList, function(item, index) {
            //         if (item.key == data.key) {
            //             item.pullFlag = data.val().pullFlag;
            //         }
            //     });
            // });
            var config = {
                params: {
                    countFlag: true,
                    pullFlag: false,
                    per_page: 5
                },
                headers: {
                    'Authorization': this.loginToken
                }
            };
            this.api.callGetApi(this.api.api_get_user_notification_web, config)
                .then(response => {
                    var result = response.data.data;
                    this.notifyList = result.resultInfo;
                    this.unReadNotification = result.notifyTotal > 99? 99:result.notifyTotal;
                })
                .catch(this.handleErrorFunction);
        },

        firebaseOff() {
            // let loginUserId = localStorage.getItem(CONST.USER_ID);
            // let notificationsRef = window.firebaseDB.ref('notifications');
            // notificationsRef.child(loginUserId).off('child_added');

            //firebase db (tabel messages) refer
            let messagesRef =  window.firebaseDB.ref('messages');
            messagesRef.off('child_added');

            //reset messagedList, notifyList, requestList, waitToConfirmCount
            this.messagedList = [];
            this.notifyList = [];
            this.requestList = [];
            this.waitToConfirmCount = 0;
        },

        getRequestList() {
            var config1 = {
                headers: {
                    'Authorization': this.loginToken
                }
            };
            this.api.callGetApi(this.api.api_get_request_summary, config1)
                .then(response => {
                        this.waitToConfirmCount = response.data.data.toYou.waitToConfirm > 99?
                            99 : response.data.data.toYou.waitToConfirm;
                    })
                .catch(this.handleErrorFunction);
            
            var config2 = {
                params: {
                    target: 0,
                    type: 0,
                    page: 1,
                    per_page: this.per_page
                },
                headers: {
                    'Authorization': this.loginToken
                }
            };
            this.api.callGetApi(this.api.api_get_request_list_web, config2)
                .then(response => {
                        this.requestList = response.data.data.resultInfo;
                    })
                .catch(this.handleErrorFunction);
            
        },

        logout() {
            this.isSystemExecuting = true;
            var data = {
                uuid: window.uuid
            };
            var headers = {
                'Authorization': this.loginToken
            }
            this.api.callPostApi(this.api.api_sign_out, data, {headers})
                .then(response => {
                    this.firebaseOff();
                    this.isSystemExecuting = false;
                    localStorage.removeItem(CONST.USER_ID);
                    localStorage.removeItem(CONST.LOGIN_TOKEN);
                    localStorage.removeItem(CONST.FIREBASE_PASS);
                    this.$router.push({name: CONST.NAME_LOGIN_PAGE, params: {tab:"signin"}});
                })
                .catch(error => {
                    this.firebaseOff();
                    this.isSystemExecuting = false;
                    localStorage.removeItem(CONST.USER_ID);
                    localStorage.removeItem(CONST.LOGIN_TOKEN);
                    localStorage.removeItem(CONST.FIREBASE_PASS);
                    this.$router.push({name: CONST.NAME_LOGIN_PAGE, params: {tab:"signin"}});
                });
        },

        firebaseNotificationRegist() {
            let _self = this;
            setTimeout(function(){
                if ('serviceWorker' in navigator) {
                    messaging.requestPermission()
                    .then(function () {
                        return messaging.getToken()
                    })
                    .then(function (token) {
                        var params = {
                            firebaseToken: token,
                            uuid: window.uuid
                        }
                        var headers = {
                            'Authorization': localStorage.getItem(CONST.LOGIN_TOKEN)
                        }
                        _self.api.callPostApi(_self.api.api_firebase_token_register, params, {headers})
                        .then(response => {

                        })
                        .catch(_self.handleErrorFunction);
                    })
                    .catch(function (err) {
                        console.log(err)
                    })
                }
            },1000)

        },
        setContentOfSearching(text) {
            var nameOfPage = this.$route.name;
            if (this.label.NAME_SEARCH_BOOK_PAGE != nameOfPage &&
                this.label.NAME_SEARCH_AUTHOR_PAGE != nameOfPage &&
                this.label.NAME_SEARCH_USER_PAGE != nameOfPage) {
                switch (nameOfPage) {
                    case this.label.NAME_HOME_PAGE:
                        window.home = text;
                        break;
                    case this.label.NAME_USER_REQUEST_PAGE:
                        window.userRequest = text;
                        break;
                    case this.label.NAME_USER_REQUEST_DETAIL_PAGE:
                        window.userRequestDetail = text;
                        break;
                    case this.label.NAME_USER_VISITOR_PAGE:
                        window.userVisitor = text;
                        break;
                    case this.label.NAME_USER_MESSAGE_PAGE:
                        window.userMessage = text;
                        break;
                    case this.label.NAME_USER_MESSAGE_DETAIL_PAGE:
                        window.userMessageDetail = text;
                        break;

                    case this.label.NAME_NEAR_BY_USER:
                        window.nearByUser = text;
                        break;

                    case this.label.NAME_SHARE_BOOKS_PAGE:
                        window.listShareBooks = text;
                        break;

                    case this.label.NAME_BOOK_DETAIL_PAGE:
                        window.bookDetail = text;
                        break;

                    case this.label.NAME_EVENT_DETAIL_PAGE:
                        window.eventDetail = text;
                        break;

                    case this.label.NAME_LIST_BOOKSTOPS_PAGE:
                        window.listBookstops = text;
                        break;

                    case this.label.NAME_BOOKSTOP_DETAIL_PAGE:
                        window.bookstopDetail = text;
                        break;
                }
            }
        },
        getSearchHistory(){
            var self = this;
            if(this.isNotNull(this.loginUserId)){
                if(this.isNull(this.searchHistory.userSearchHistory)){
                    this.$getItem(CONST.USER_SEARCH_HISTORY).then(data => 
                    {
                        if(self.isNull(data)){
                            self.getUserSearchHistory();
                        }else{
                            self.searchHistory.userSearchHistory = data;
                        }
                    });
                }
                if(this.isNull(this.searchHistory.titleSearchHistory)){
                    this.$getItem(CONST.TITLE_SEARCH_HISTORY).then(data => 
                    {
                        if(self.isNull(data)){
                            self.getTitleSearchHistory();
                        }else{
                            self.searchHistory.titleSearchHistory = data;
                        }
                    });
                }
                if(this.isNull(this.searchHistory.authorSearchHistory)){
                    this.$getItem(CONST.AUTHOR_SEARCH_HISTORY).then(data => 
                    {
                        if(self.isNull(data)){
                            self.getAuthorSearchHistory();
                        }else{
                            self.searchHistory.authorSearchHistory = data;
                        }
                    });
                }
            }
        },
        getUserSearchHistory(){
            this.api.callGetApiWithAuthorization(this.api.api_get_user_searched_keyword, null)
                    .then(this.setUserSearchHistory, this.handleErrorFunction);
        },
        getTitleSearchHistory(){
            this.api.callGetApiWithAuthorization(this.api.api_get_book_searched_keyword, null)
                    .then(this.setTitleSearchHistory, this.handleErrorFunction);
        },
        getAuthorSearchHistory(){
            this.api.callGetApiWithAuthorization(this.api.api_get_author_searched_keyword, null)
                    .then(this.setAuthorSearchHistory, this.handleErrorFunction);
        },
        setUserSearchHistory(response){
            if (200 == response.status) {
                this.$setItem(CONST.USER_SEARCH_HISTORY, response.data.data.resultInfo);
                this.searchHistory.userSearchHistory = response.data.data.resultInfo;
            }
        },
        setTitleSearchHistory(response){
            if (200 == response.status) {
                this.$setItem(CONST.TITLE_SEARCH_HISTORY, response.data.data.resultInfo);
                this.searchHistory.titleSearchHistory = response.data.data.resultInfo;
            }
        },
        setAuthorSearchHistory(response){
            if (200 == response.status) {   
                this.$setItem(CONST.AUTHOR_SEARCH_HISTORY, response.data.data.resultInfo);
                this.searchHistory.authorSearchHistory = response.data.data.resultInfo;
            }
        },
    }
}
</script>


<style lang="scss">
@import '../../../scss/common_styles.scss';
@import 'headerGat.scss';
</style>



