<template>
    <div class="bookstop-nav-form medium-text-form-m center-block">
        <div v-on:click="setTabNumber($event, 'books')" class="bookstop-nav-button" v-bind:class="{ 'active': $route.params.tab=='books' }">{{label.book_shelf}}</div>
        <div v-on:click="setTabNumber($event, 'images')" class="bookstop-nav-button" v-bind:class="{ 'active': $route.params.tab=='images' }">{{label.bookstop_space}}</div>
    </div>
</template>

<script>
import Label from '../../../common/Label.js'
import * as CONST from '../../../common/const.js'
export default {
    mixins: [ Label ],
    methods: {
        setTabNumber(ev, index) {
            $('.bookstop-nav-button').removeClass('active');
            $(ev.target).addClass('active');
            if (this.byMyself) {
                this.$router.push({ name: CONST.NAME_BOOKSTOP_DETAIL_PAGE, params: {tab: index}});
            } else {
                this.$router.push({ name: CONST.NAME_BOOKSTOP_DETAIL_PAGE, params: {id: this.$route.params.id, tab: index}});
            }
        },
    }
}
</script>

<style>

</style>



