// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'

import FBSignInButton from 'vue-facebook-signin-button'
import GSignInButton from 'vue-google-signin-button'
import VueLocalStorage from 'vue-localstorage'
import VueLocalForage from 'vue-localforage'
import ToggleButton from 'vue-js-toggle-button'
import vmodal from 'vue-js-modal'
import VueFire from 'vuefire'
import * as VueGoogleMaps from 'vue2-google-maps'
import * as Pagination from "vuejs-uib-pagination"
import * as CONST from './common/const.js'
import SocialSharing from 'vue-social-sharing'
import VueAwesomeSwiper from 'vue-awesome-swiper'

// require styles
import 'swiper/dist/css/swiper.css'
Vue.use(VueAwesomeSwiper, /* { default global options } */)

Vue.config.productionTip = false;
Vue.use(FBSignInButton);
Vue.use(GSignInButton);
Vue.use(VueLocalStorage);
Vue.use(VueLocalForage);
Vue.use(VueGoogleMaps, {
    load: CONST.GMAP_CONFIG
});
Vue.use(VueFire);
Vue.use(Pagination);
Vue.use(ToggleButton);
Vue.use(vmodal);
Vue.use(SocialSharing);

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})

window.isReadyFaceBook = 0;
window.isRuningLoadSDKFacebook = null;
if (0 == window.isReadyFaceBook && null == window.isRuningLoadSDKFacebook) {
  // Waiting to load facebook sdk
  window.isRuningLoadSDKFacebook = setInterval(function () {
    if (window.isReadyFaceBook > 0) {
      clearInterval(window.isRuningLoadSDKFacebook);
    }
  }, 500);
}
window.fbAsyncInit = function () {
  FB.init({
    appId: CONST.FACEBOOK_APP_ID,
    cookie: true,
    xfbml: true,
    version: 'v2.8'
  });
  FB.Event.subscribe('xfbml.render', function(response) {
    window.isReadyFaceBook = 1;
  });
  FB.AppEvents.logPageView();
};

(function (d, s, id) {
  var js, fjs = d.getElementsByTagName(s)[0];
  if (d.getElementById(id)) {
    return;
  }
  js = d.createElement(s);
  js.id = id;
  js.src = "//connect.facebook.net/en_US/sdk.js";
  fjs.parentNode.insertBefore(js, fjs);
}(document, 'script', 'facebook-jssdk'));

window.isReadyGoogle = false;
window.isRuningLoadSDKGoogle = null;
if (false == window.isReadyGoogle && null == window.isRuningLoadSDKGoogle) {
  var time = 1;
  // Load google sdk in 5s
  window.isRuningLoadSDKGoogle = setInterval(function () {
    if (typeof google === 'object' && typeof google.maps === 'object') {
      window.isReadyGoogle = true;
      clearInterval(window.isRuningLoadSDKGoogle);
      window.isRuningLoadSDKGoogle = null;
      var handleSussessFunction = function (position) {
        // Store current location
        window.currentLocation = CONST.QL1_COORDINATE;
        window.currentLocation.lat = position.coords.latitude;
        window.currentLocation.lng = position.coords.longitude;
      };
      var handleErrorFunction = function (error) {
        switch (error.code) {
          case error.PERMISSION_DENIED:
            console.log("User denied the request for Geolocation.");
            break;
          case error.POSITION_UNAVAILABLE:
            console.log("Location information is unavailable.");
            break;
          case error.TIMEOUT:
            console.log("The request to get user location timed out.");
            break;
          case error.UNKNOWN_ERROR:
            console.log("An unknown error occurred.");
            break;
        }
      };
      // Get current location
      var promiseObj = new Promise(function (success, error) {
        if (navigator.geolocation) {
          navigator.geolocation.getCurrentPosition(success, error);
        } else {
          console.log("Geolocation is not supported by this browser.");
        }
      });
      promiseObj.then(handleSussessFunction, handleErrorFunction);
    }
    if (10 == time) {
      clearInterval(window.isRuningLoadSDKGoogle);
      window.isRuningLoadSDKGoogle = null;
    }
    time++;
  }, 500);
}


