<template>
    <div class="">
        <div v-if="isShowLoading">
            <img src="../../../assets/images/loading.gif" class="loading-img-medium">
        </div>
        <div v-if="!isShowLoading" class="">
            <div class="row" v-for="(splitedBookstop,outerIndex) in bookstopList" :key="outerIndex">
                <div class="col-xs-6" v-for="(bookstop,innerIndex) in splitedBookstop" :key="innerIndex">
                    <router-link :to="{name:label.NAME_BOOKSTOP_DETAIL_PAGE, params:{id: bookstop.userId, tab: 'books'}}">
                        <bookstop :bookstop="bookstop"></bookstop>
                    </router-link>
                </div>
            </div>
            <div v-if="bookstopTotal <= per_page" style="height:15px;"></div>
            <div v-if="bookstopTotal > per_page" class="text-center">
                <uib-pagination
                    :boundary-links="true"
                    :force-ellipses="false"
                    :boundary-link-numbers="true"
                    previous-text="<" 
                    next-text=">" 
                    first-text="<<"
                    last-text=">>"
                    v-model="pagination" 
                    :items-per-page="per_page" 
                    :total-items="bookstopTotal" 
                    :max-size="4" 
                    :rotate="true">
                </uib-pagination>
            </div>
        </div>
    </div>
</template>

<script>
import Bookstop from './Bookstop.vue'
import CommonService from '../../../services/CommonService.js'
import BookService from '../../../services/BookService.js'
import * as CONST from '../../../common/const.js'
import Label from '../../../common/Label.js'
export default {
    components: { Bookstop },
    mixins: [ Label, CommonService, BookService ],
    mounted() {
        this.refresh();
    },
    watch: {
        '$route': function(){
            this.refresh();
        },
        pagination: function(){
            var page = this.pagination.currentPage;
            var _self = this;
            this.$router.push({query: {page: page}});
        }
    },
    methods: {
        refresh() {
            var _self = this;
            if(this.$route.query.page==null){
                this.$router.replace({query: {page: '1'}});
            }
            this.pagination = {currentPage: Number(this.$route.query.page)};
            _self.getBookstopTotal();
            _self.getBookstopList();
        },
        getBookstopTotal() {
            //get data from Service API
            var _self = this;
            var config = {
                params: {}
            };
            _self.api.callGetApi(_self.api.api_get_bookstop_total, config)
                .then(response => {
                    _self.bookstopTotal = response.data.data.bookstopTotal;
                })
                .catch(_self.handleErrorFunction);
        },
        getBookstopList() {
            //get data from Service API
            var _self = this;
            _self.bookstopList = [];
            _self.isShowLoading = true;
            var config = {
                params: {
                    page: _self.pagination.currentPage,
                    per_page: _self.per_page
                }
            };
            _self.api.callGetApi(_self.api.api_get_bookstop_list, config)
                .then(response => {
                    _self.bookstopList = _.chunk(response.data.data.resultInfo, 2);
                    _self.isShowLoading = false;
                })
                .catch(_self.handleErrorFunction);
        }
    },
    data() {
        return {
            per_page: CONST.USER_BOOK_ITEM_PERPAGE,
            pagination: {},
            bookstopTotal: null,
            bookstopList: []
        }
    }
}
</script>

<style>

</style>



