<template>
    <div class="block-full-screen-form">
        <!-- title -->
        <div class="notification-title biger-text-form-m">
            {{label.notificationTitle}}
        </div>
        <div class="block-full-screen-content" style="margin-top:45px !important;">
            <notification-item
                v-for="(item, index) in displayList" :key="index"
                :item="item"
                :dateDisplay="index==0 || (index>0&&(new Date(item.beginTime).getDate()!=new Date(displayList[index-1].beginTime).getDate()))"></notification-item>
        </div>
        <infinite-loading :on-infinite="onInfinite" ref="infiniteLoading"></infinite-loading>
    </div>
</template>

<script>
import InfiniteLoading from 'vue-infinite-loading'
import NotificationItem from './NotificationItem.vue'
import Label from '../../../../common/Label.js'
import BookService from '../../../../services/BookService.js'
import * as CONST from '../../../../common/const.js'

//firebase db (tabel notifications) refer
//let notificationsRef = window.firebaseDB.ref('notifications');

export default {
    name: 'notification-list',
    mixins: [ Label, BookService ],
    components: { NotificationItem, InfiniteLoading },
    mounted() {
        this.$getItem(CONST.USER_NOTIFICATION).then(cacheData => {
            if (cacheData) {
                this.displayList = cacheData;
                for(var i=0; i<cacheData.length; i++) {
                    if (!cacheData[i].sourceId || cacheData[i].sourceId == 0) {
                        cacheData[i].sourceImage = CONST.SYSTEM_IMAGE;
                    }
                }
                
            }
        })
        //this.getNotifications();
    },
    // watch: {
    //     notifyList: function() {
    //         var x = this.displayList.length;
    //         this.displayList = [].concat(this.notifyList.slice(0, x));
    //     }
    // },
    methods: {
        // getNotifications() {
        //     let _self = this;
        //     let loginUserId = localStorage.getItem(CONST.USER_ID);
        //     notificationsRef.child(loginUserId).on('child_added', function (data) {
        //         var node = data.val();
        //         node.key = data.key;
        //         _self.notifyList.unshift(node);
        //     })
        // },
        // onInfinite() {
        //     setTimeout(() => {
        //         this.displayList = this.displayList.concat(this.notifyList.slice(this.start,this.start+this.per_page));
        //         this.start = this.displayList.length;
        //         if (this.notifyList.length==0 || (this.displayList.length < this.notifyList.length)) {
        //             this.$refs.infiniteLoading.$emit('$InfiniteLoading:loaded');
        //         } else {
        //             this.$refs.infiniteLoading.$emit('$InfiniteLoading:complete');
        //         }
        //     }, 600);
        // }
        onInfinite() {
            setTimeout(() => {
                var config = {
                    params: {
                        countFlag: false,
                        pullFlag: true,
                        notificationId: this.notificationId,
                        per_page: CONST.PER_PAGE_DEFAULT
                    },
                    headers: {
                        'Authorization': this.loginToken
                    }
                };
                this.api.callGetApi(this.api.api_get_user_notification_web, config)
                    .then(response => {
                        var result = response.data.data.resultInfo;
                        for(var i=0; i<result.length; i++) {
                            if (!result[i].sourceId || result[i].sourceId == 0) {
                                result[i].sourceImage = CONST.SYSTEM_IMAGE;
                            }
                        }
                        if (!this.notificationId) {
                            this.displayList = result;
                        } else {
                            this.displayList = this.displayList.concat(result);
                        }
                        this.notificationId = result[result.length - 1].notificationId;
                        if (result.length < CONST.PER_PAGE_DEFAULT) {
                            this.$refs.infiniteLoading.$emit('$InfiniteLoading:complete');
                        } else {
                            this.$refs.infiniteLoading.$emit('$InfiniteLoading:loaded');
                        }
                        if (response.data.data.notificationId == null) {
                            this.$setItem(CONST.USER_NOTIFICATION, result);
                        }
                    })
                    .catch(this.handleErrorFunction);
            }, 600);
        }
    },
    data() {
        return {
            notificationId: null,
            //start: 0,
            displayList: [],
            //per_page: ((screen.height - 140)/88).toFixed(0)*1,
            //notifyList: []
        }
    }
}
</script>

<style>

</style>



