<template>
    <div class="box-shadow-bold">
        <div class="bookstop-image-top">
            <div class="row">
                <div class="col-xs-6" style="height:230px">
                    <img v-if="imageLoad" :src="getUrlImage(bookstopInfo.imageId)" class="img-avatar">
                </div>
                <div class="col-xs-6">
                    <div class="biger-text-form-m">{{bookstopInfo.name}}</div>
                    <div class="medium-text-form-r text-6-row">{{bookstopInfo.description}}</div>
                    <div class="row" style="margin-top:15px">
                        <div class="col-xs-1">
                            <div class="circle-25 red">
                                <i class="fa fa-map-marker" aria-hidden="true" style="color:white"></i>
                            </div>
                        </div>
                        <div class="col-xs-11">
                            <div class="text-limit inline-limit">
                                {{address}}
                            </div>
                        </div>
                    </div>
                    <div class="row" style="margin-top:15px">
                        <div class="col-xs-1">
                            <div class="circle-25 blue">
                                <i class="fa fa-envelope-o" aria-hidden="true" style="color:white"></i>
                            </div>
                        </div>
                        <div class="col-xs-11">
                            <div class="text-limit inline-limit">
                                {{bookstopInfo.email}}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="bookstop-image-bottom">
            <div v-if="isShowLoading" style="margin:10px;">
                <img src="../../../assets/images/loading.gif" class="loading-img-medium">
            </div>
            <div v-if="!isShowLoading">
                <div class="row" v-for="(splitedImage,outerIndex) in imageList" :key="outerIndex">
                    <div class="col-xs-4 nopadding" v-for="(image,innerIndex) in splitedImage" :key="innerIndex">
                        <image-in-bookstop :image="image" :inMain="true"></image-in-bookstop>
                    </div>
                </div>
                <div v-if="imageTotal <= per_page" style="height:15px;"></div>
                <div v-if="imageTotal > per_page" class="text-center">
                    <uib-pagination
                        :boundary-links="true"
                        :force-ellipses="false"
                        :boundary-link-numbers="true"
                        previous-text="<" 
                        next-text=">" 
                        first-text="<<"
                        last-text=">>"
                        v-model="pagination" 
                        :items-per-page="per_page" 
                        :total-items="imageTotal" 
                        :max-size="4" 
                        :rotate="true">
                    </uib-pagination>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import ImageInBookstop from '../components/ImageInBookstop.vue'
import CommonService from '../../../services/CommonService.js'
import BookService from '../../../services/BookService.js'
import * as CONST from '../../../common/const.js'
import Label from '../../../common/Label.js'
export default {
    props: [ "bookstopInfo" ],
    components: { ImageInBookstop },
    mixins: [ Label, CommonService, BookService],
    mounted() {
        this.refresh();
    },
    computed: {
        address() {
            return this.bookstopInfo.usuallyLocation? this.bookstopInfo.usuallyLocation[0].address:'';
        }
    },
    watch: {
        '$route': function(){
            this.refresh();
            if (this.bookstopInfo) {
                this.imageLoad = true;
            }
        },
        pagination: function(){
            var page = this.pagination.currentPage;
            var _self = this;
            this.$router.push({query: {page: page}});
        },
        bookstopInfo: function() {
            if (this.bookstopInfo) {
                this.imageLoad = true;
            }
        }
    },
    methods: {
        refresh(){
            var _self = this;
            if(this.$route.query.page==null){
                this.$router.replace({query: {page: '1'}});
            }
            this.pagination = {currentPage: Number(this.$route.query.page)};
            this.getImageTotal();
            this.getImageList();
        },
        getImageTotal() {
            //get data from Service API
            var _self = this;
            var config = {
                params: {
                    bookstopId: _self.$route.params.id
                }
            };
            _self.api.callGetApi(_self.api.api_get_bookstop_image_total, config)
                .then(response => {
                    _self.imageTotal = response.data.data.imageTotal;
                })
                .catch(_self.handleErrorFunction);
        },
        getImageList() {
            //get data from Service API
            var _self = this;
            _self.imageList = [];
            _self.isShowLoading = true;
            var config = {
                params: {
                    bookstopId: _self.$route.params.id,
                    page: _self.pagination.currentPage,
                    per_page: _self.per_page
                }
            };
            _self.api.callGetApi(_self.api.api_get_bookstop_images, config)
                .then(response => {
                    _self.imageList = _.chunk(response.data.data.resultInfo, 3);
                    _self.isShowLoading = false;
                })
                .catch(_self.handleErrorFunction);
        }
    },
    data() {
        return {
            per_page: CONST.USER_BOOK_ITEM_PERPAGE,
            pagination: {},
            imageTotal: null,
            imageList: [],
            imageLoad: false
        }
    }
}
</script>

<style>

</style>



