<template>
    <div>
        <div class="user-info-form">
            <div class="user-profile-image center-block">
                <user-icon 
                    :userIconStyle="userIconStyle" 
                    :imageId="imageId" 
                    :changable="isPrivatePageFlag"
                    @openChangeImageDialog="openChangeImageDialog"></user-icon>
            </div>
            <div class="user-name biger-text-form-m center-block text-limit">
                {{name}}
            </div>
            <div class="address medium-text-form-m center-block text-limit">
                {{address}}
            </div>
            <div class="user-navigation medium-text-form-m center-block">
                <div v-on:click="setTabNumber($event, 'book-shelf')" class="navigation-button" v-bind:class="{ 'active': $route.params.tab=='book-shelf' }">{{label.book_shelf}}</div>
                <div v-on:click="setTabNumber($event, 'read-history')" class="navigation-button" v-bind:class="{ 'active': $route.params.tab=='read-history' }">{{label.read_history}}</div>
                <div v-if="byMyself" v-on:click="setTabNumber($event, 'info-setting')" class="navigation-button navigation-button-bottom" v-bind:class="{ 'active': $route.params.tab=='info-setting' }">{{label.info}}</div>
                <div v-if="!byMyself" v-on:click="openMessageDialog" class="navigation-button navigation-button-bottom">
                    <span style="margin-right:30px;">{{label.sendMessage}}</span>
                    <span class="fa fa-commenting fa-2x"></span>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
import UserIcon from '../../common/userIcon/UserIcon';
import Label from '../../../common/Label.js';
import * as CONST from '../../../common/const.js';

export default {
    components: { UserIcon },
    mixins: [ Label ],
    props: ["userInfo", "byMyself"],
    watch: {
        userInfo: function() {
            this.name = this.userInfo.name;
            this.imageId = this.userInfo.imageId;
            this.address = this.userInfo.usuallyLocation[0].address;
        }
    },
    computed: {
        isPrivatePageFlag(){
            return localStorage.getItem(CONST.USER_ID)==this.$route.params.id;
        }
    },
    data() {
        return {
            name: "",
            imageId: "",
            address: "",
            userIconStyle: {
                'background-color' : 'white',
                'border-radius': '69px',
                'width': '138px',
                'height': '138px',
                'margin': '0 auto',
                'margin-top': '5px'
            }
        }
    },
    methods: {
        setTabNumber(ev, index) {
            $('.navigation-button').removeClass('active');
            $(ev.target).addClass('active');
            if (this.byMyself) {
                this.$router.push({ name: CONST.NAME_USER_VISITOR_PAGE, params: {id: localStorage.getItem(CONST.USER_ID),tab: index}});
            } else {
                this.$router.push({ name: CONST.NAME_USER_VISITOR_PAGE, params: {id: this.$route.params.id, tab: index}});
            }
        },
        openMessageDialog() {
            this.$emit('openMessageDialog', true);
        },
        openChangeImageDialog() {
            this.$emit('openChangeImageDialog', true);
        }
    }
}
</script>

<style>
@import 'userInfo.scss';
</style>



