<template>
    <div class="user-profile-detail-form box-shadow-bold">
        <!--page title-->
        <div class="page-detail-header medium-text-form-m">
            {{requestListTitle}}
        </div>
        <!--page content-->
        <!--request list is empty-->
        <div v-if="requests.length==0" class="request-empty">
            {{emptyText}}
        </div>
        <!--request list is not empty-->
        <div v-if="requests.length>0">
            <div v-for="(item, index) in requests" :key="index" class="each-request" :class="{'each-request-not-bottom' : index<requests.length-1}">
                <!--user-icon-->
                <router-link v-if="$route.query.target==TARGET_TO_YOU"
                    :to="{name: item.borrowerTypeFlag==USER_TYPE_FLAG?label.NAME_USER_VISITOR_PAGE:label.NAME_BOOKSTOP_DETAIL_PAGE, params: {id: item.borrowerId, tab: item.borrowerTypeFlag==USER_TYPE_FLAG?'book-shelf':'books'}}">
                    <div class="display-left target-user-border">
                        <user-icon
                            :userIconStyle="userIconStyle"
                            :imageId="item.borrowerImageId">
                        </user-icon>
                    </div>
                </router-link>
                <router-link v-else
                    :to="{name: item.ownerTypeFlag==USER_TYPE_FLAG?label.NAME_USER_VISITOR_PAGE:label.NAME_BOOKSTOP_DETAIL_PAGE, params: {id: item.ownerId, tab: item.ownerTypeFlag==USER_TYPE_FLAG?'book-shelf':'books'}}">
                    <div class="display-left target-user-border">
                        <user-icon
                            :userIconStyle="userIconStyle"
                            :imageId="item.ownerImageId">
                        </user-icon>
                    </div>
                </router-link>
                <!--center-div-->
                <div class="display-left" style="margin-left: 15px; width: 75%;">
                    <!--user-name-->
                    <div class="half-row-top">
                        <span class="inline-block">
                            <router-link v-if="$route.query.target==TARGET_TO_YOU" style="text-decoration:none;" class="medium-text-form-m text-limit text-muted title-name-color" 
                                :to="{name: item.borrowerTypeFlag==1?label.NAME_USER_VISITOR_PAGE:label.NAME_BOOKSTOP_DETAIL_PAGE, params: {id: item.borrowerId, tab: item.borrowerTypeFlag==1?'book-shelf':'books'}}">
                                {{item.borrowerName}}
                            </router-link>
                            <router-link v-else style="text-decoration:none;" class="medium-text-form-m text-limit text-muted title-name-color" 
                                :to="{name: item.ownerTypeFlag==1?label.NAME_USER_VISITOR_PAGE:label.NAME_BOOKSTOP_DETAIL_PAGE, params: {id: item.ownerId, tab: item.ownerTypeFlag==1?'book-shelf':'books'}}">
                                {{item.ownerName}}
                            </router-link>
                        </span>
                    </div>
                    <!--request-content-->
                    <div class="half-row-bottom small-text-form-r">
                        <div v-if="$route.query.target==TARGET_TO_YOU" class="text-limit" v-html="label.sendRequest(item.editionId,item.editionTitle)">
                        </div>
                        <div v-else class="text-limit" v-html="label.receiveRequest(item.editionId,item.editionTitle)">
                        </div>
                    </div>
                </div>
                <!--right-div-->
                <div class="display-right">
                    <!--date time-->
                    <div class="half-row-top small-text-form-r">{{label.datetime(item.updateDate)}}</div>
                    <!--request status-->
                    <div class="half-row-bottom small-text-form-r">
                        <div v-on:click="viewRequestDetail(item.recordId)" class="mouse-pointer detail-link">
                            {{label.recordStatus(item.recordStatus)}}
                            <span class="fa fa-angle-right"></span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <!--bottom layout-->
        <div class="page-detail-footer">
            <div v-if="isShowLoading" class="">
                <img src="../../../../assets/images/loading.gif" class="loading-img">
            </div>
            <div v-if="!isShowLoading && totalLoaded==PER_PAGE_DEFAULT">
                <button v-on:click="loadMoreItem" class="btn btn-link medium-text-form-m">{{label.viewMore}}</button>
            </div>
            <div v-if="!isShowLoading && totalLoaded<PER_PAGE_DEFAULT" class="medium-text-form-m">{{label.allDisplayed}}</div>
        </div>
    </div>
</template>

<script>
import BookService from '../../../../services/BookService.js';
import UserIcon from '../../../common/userIcon/UserIcon.vue';
import Label from '../../../../common/Label.js';
import * as CONST from '../../../../common/const.js'

export default {
    components: { UserIcon },
    mixins: [ BookService, Label ],
    mounted() {
        if(this.$route.query.target==null||this.$route.query.type==null){
            this.$router.replace({ name: CONST.NAME_USER_REQUEST_PAGE, query: {target: '0', type: '0'}});
        }
        if(this.$route.query.target!=null&&this.$route.query.type!=null&&this.loginToken){
            this.requests = [];
            this.page = 1;
            this.getRequestList();
        }
    },
    watch: {
        '$route': function(){
            if(this.$route.query.target!=null&&this.$route.query.type!=null&&this.loginToken){
                this.requests = [];
                this.page = 1;
                this.getRequestList();
            }
        }
    },
    computed: {
        requestListTitle: function(){
            var listType = null;
            if (this.$route.query.type == 0) {
                listType = CONST.BR_STT_WAIT_CONFIRM;
            } else if (this.$route.query.type == 1) {
                listType = CONST.BR_STT_CONTACTING;
            } else if (this.$route.query.type == 2) {
                listType = CONST.BR_STT_BORROWING;
            } else if (this.$route.query.type == 3) {
                listType = -1;
            }
            return this.label.recordStatus(listType);
        }
    },
    methods: {
        loadMoreItem(){
            this.page += 1;
            this.getRequestList();
        },
        getRequestList() {
            //remove 'has not suitable request' when loading
            this.emptyText = "";
            var lcs = CONST.USER_REQUEST + "_target_" + this.$route.query.target + "_type_" + this.$route.query.type;
            if (this.page == 1) {
                this.$getItem(lcs).then(cacheData => {
                    if (!cacheData) {
                        //set loading flag to true
                        this.isShowLoading = true;
                    } else {
                        this.requests = cacheData;
                        this.totalLoaded = cacheData.length;
                        //display requestNA label if response is empty
                        if(cacheData.length == 0){
                            this.emptyText = this.label.requestNA;
                        }
                    }
                })
            }
            
            var config = {
                params: {
                    target: this.$route.query.target,
                    type: this.$route.query.type,
                    page: this.page
                },
                headers: {
                    'Authorization': this.loginToken
                }
            };
            this.api.callGetApi(this.api.api_get_request_list_web, config)
                .then(response => {
                        if(this.$route.query.target == response.data.data.target && this.$route.query.type == response.data.data.type) {
                            if (this.page == 1) {
                                this.requests = response.data.data.resultInfo;
                            } else {
                                this.requests = this.requests.concat(response.data.data.resultInfo);
                            }
                            this.totalLoaded = response.data.data.resultInfo.length;
                            //display requestNA label if response is empty
                            if(response.data.data.resultInfo.length == 0){
                                this.emptyText = this.label.requestNA;
                            }
                            this.isShowLoading = false;
                            if (this.page == 1) {
                                this.$setItem(lcs, response.data.data.resultInfo);
                            }
                        }
                    })
                .catch(this.handleErrorFunction);
        },
        viewRequestDetail(recordId){
            this.$router.replace({ name: CONST.NAME_USER_REQUEST_DETAIL_PAGE, params: {id: recordId}});
        }
    },
    data() {
        return {
            //innit request list
            requests: [],
            //length of loaded each response
            totalLoaded: 0,
            //init page
            page: 1,
            //default per_page
            PER_PAGE_DEFAULT: CONST.PER_PAGE_DEFAULT,

            //target of request
            TARGET_TO_YOU: CONST.TARGET_TO_YOU,             //0

            USER_TYPE_FLAG: CONST.USER_TYPE_FLAG,             //0

            //style of user icon
            userIconStyle: {
                'background-color': 'white',
                'border-radius': '22px',
                'width': '44px',
                'height': '44px',
                'margin': '2px auto'
            },
            //text display when request list is empty
            emptyText: ""
        }
    }
}
</script>

<style>
</style>




