<template>
    <div>
        <div class="header-arrow-up user-mes"></div>
        <div class="header-extension">
            <div style="height:6px;"></div>
            <div class="header-extension-no-item" v-if="messagedList.length==0">
                {{label.no_mess}}
            </div>
            <header-user-message-row
                v-for="(item, index) in messagedList" :key="index" :item="item"
                ></header-user-message-row>

            <div class="header-extension-split"></div>
            <div class="header-extension-center-item">
                <button v-on:click="goMessagePage" class="btn btn-link medium-text-form-m">{{label.view_all}}</button>
            </div>
            <div style="height:6px;"></div>
        </div>
    </div>
</template>

<script>
import HeaderUserMessageRow from './HeaderUserMessageRow.vue'
import Label from '../../../common/Label.js'

export default {
    props: [ "messagedList" ],
    components: { HeaderUserMessageRow },
    mixins: [ Label ],
    mouted() {
        
    },
    methods: {
        goMessagePage() {
            this.$router.push({name: this.label.NAME_USER_MESSAGE_PAGE});
        }
    },
    data() {
        return {

        }
    }
}
</script>

<style>

</style>



