<template>
    <div class="home-box-top-user">
        <div class="row-header">
            <p class="super-biger-text-form-m text-color-default">{{label.txt_top_users}}</p>
        </div>
        <hr class="line-break reset-padding reset-margin">
        <router-link :title="user.name" :to="{ name: label.NAME_USER_VISITOR_PAGE, params: {id: user.userId, tab: 'book-shelf'}}"
        v-for="(user, index) in users" :key="user.userId">
            <div class="row-user">
                <div class="box-avatar inline-block">
                    <span class="number-user super-super-biger-text-form-r" :style="styleObject(index)">{{index + 1}}</span>
                    <user-icon :userIconStyle="userIconStyle" :imageId="user.imageId"></user-icon>
                </div>
                <div class="inline-block" style="position: absolute;">
                    <p class="medium-text-form-m text-limit" style="width:150px;">{{user.name}}</p>
                    <div class="btn-radius inline-block box-color-number-share-book">
                        <p class="reset-margin small-text-form-r text-color-btn">{{user.sharingCount}}</p>
                    </div>
                    <div class="inline-block">
                        <p class="reset-margin small-text-form-r text-color-author">{{label.sharingBook}}</p>
                    </div>
                </div>
            </div>
            <hr class="line-break-user reset-padding reset-margin">
        </router-link>
    </div>
</template>


<script>
import BookService from '../../../../services/BookService.js';
import CommonService from '../../../../services/CommonService.js';
import UserIcon from '../../../common/userIcon/UserIcon.vue';
import Label from '../../../../common/Label.js';

export default {
    name: "list-user",
    mixins: [CommonService, BookService, Label],
    components: { UserIcon },
    data() {
        return {
            users: [],
            userIconStyle: {
                'background-color': 'white',
                'border-radius': '25px',
                'width': '50px',
                'height': '50px',
                'margin-top': '2px'
            },
            config: {
                params: {
                    per_page: 5,
                    page: 1
                }
            }
        }
    },
    mounted() {
        this.initData();
    },
    methods: {
        styleObject(index) {
            var strColor = '#929292';
            switch (index) {
                case 0:
                    strColor = '#f50909';
                    break;
                case 1:
                    strColor = '#ffa800';
                    break;
                case 2:
                    strColor = '#4196c2';
                    break;
            }
            return {
                color: strColor
            }
        },
        initData() {
            this.api.callGetApi(this.api.api_user, this.config).then(this.setListUser, this.errorCallAPIListUser);
        },
        setListUser(response) {
            if (200 == response.status) {
                var responseData = response.data.data.resultInfo;
                if (responseData && responseData.length > 0) {
                    this.users = responseData;
                } 
            }
        },
        errorCallAPIListUser(error) {
            this.$emit("showErrorDialog", error);
        },
    }
}
</script>



<style>
@import 'ListUsers.scss';
</style>



