<template>
    <div class="box-full box-padding-top">
        <div class="row-header">
            <span class="biger-text-form-m text-color-default">{{label.txt_header_list_best_day}}</span>
            <span class="medium-text-form-r text-color-highlight display-right">
                <a :href="label.NAME_SHARE_BOOKS_PAGE" class="mouse-pointer">{{label.view_all}}
                    <i class="fa fa-angle-right fa-lg" aria-hidden="true"></i>
                </a>
            </span>
        </div>
        <hr class="line-break reset-padding reset-margin">
        <div class="box-list-sharing-book-on-day">
            <div v-for="(book, index) in books" v-if="index < 5" :key="book.editionId">
                <book-info :editionId="book.editionId" :title="book.title" :imageId="book.imageId" :author="book.author" :rateAvg="book.rateAvg" :numberRate="book.number_rate"></book-info>
            </div>
        </div>
    </div>
</template>


<script>
import BookService from '../../../../services/BookService.js';
import CommonService from '../../../../services/CommonService.js';
import * as CONST from '../../../../common/const.js';
import BookInfo from '../../../common/bookInfo/BookInfo.vue';
import Label from '../../../../common/Label.js';

export default {
    name: 'list-sharing-book',
    components: { BookInfo },
    mixins: [CommonService, BookService, Label],
    mounted() {
        this.initData();
    },
    data() {
        return {
            books: [],
            config: {
                params: {
                    per_page: 5,
                    page: 1
                }
            }
        }
    },
    methods: {
        initData() {
            var self = this;
            this.api.callGetApi(this.api.api_list_sharing_books, this.config).then(this.setListSharingBook, this.errorCallApiListSharingBook);
        },
        setListSharingBook(response) {
            if (200 == response.status && response.data.data.resultInfo && response.data.data.resultInfo.length > 0) {
                this.books = response.data.data.resultInfo;
            } else {
                this.$emit("showListSharingBook", false);
            }
        },
        errorCallApiListSharingBook(error) {
            this.$emit("showErrorDialog");
        },
    }
}
</script>
<style>
@import 'ListSharingBook.scss';
</style>



