<template>
    <modal name="book-detail-add-instance" :height="430" :width="480" :clickToClose="false">
        <div class="add-instance-box-title">
            <button class="close reset-outline reset-border" @click="hideDialog">&times;</button>
            <h4 class="modal-title super-biger-text-form-m align-center">{{label.txt_add_instance}}</h4>
        </div>
        <hr class="line-break-category-color reset-padding reset-margin">
        <div class="add-instance-content">
            <div style="margin-bottom: 30px;">
                <div class="add-instance-image" v-show="isNotNull(book.imageId)">
                    <img :src="getUrlImage(book.imageId)" class="image-fill add-instance-image" :alt="book.title">
                    <div style="clear: both;"></div>
                </div>
                <div class="add-instance-info">
                    <p class="biger-text-form-m text-color-default add-instance-info-title">{{getContentLimitLength(book.title, 65)}}</p>
                    <p class="biger-text-form-r text-color-author">{{getAuthor(book.author)}}</p>
                    <div class="add-instance-box-rate-star box-block">
                        <span class="add-instance-rate-star">
                            <star-rating :rating="book.rateAvg" :read-only="true" :increment="0.01" :show-rating="false" inactive-color="white" border-color="#f4c312" :border-width="1" :star-size="18">
                            </star-rating>
                        </span>
                        <span class="biger-text-form-r text-color-author">{{book.rateAvg}}</span>
                    </div>
                </div>
                <div style="clear: both;"></div>
            </div>
            <p class="biger-text-form-m">{{label.txt_confirm_number_of_adding_book}}</p>
            <div class="align-center">
                <i class="fa fa-minus-circle fa-lg add-instance-minus text-color-highlight" :class="{'text-color-author':numberOfBook == totalNumber}" aria-hidden="true" @click="numberOfBook > totalNumber ? numberOfBook-- : numberOfBook"></i>
                <span class="medium-text-form-r">{{numberOfBook}}</span>
                <i class="fa fa-plus-circle fa-lg add-instance-plush text-color-highlight" aria-hidden="true" @click="numberOfBook++"></i>
            </div>
            <div class="add-instance-margin-top">
                <span class="medium-text-form-r display-left">{{label.txt_sharing}}</span>
                <span class="display-right center-vertical-text">
                    <toggle-button :value="sharingStatus == defaultSharingStatus" :color="'#7cc576'" :sync="false" :labels="false" @change="changeSharingStt"></toggle-button>
                </span>
                <div style="clear: both;"></div>
            </div>
            <div class="add-instance-btn" @click="addBookToLibrary">
                <p class="reset-margin medium-text-form-m txt-show-more">{{label.txt_add_book}}</p>
            </div>
        </div>
    </modal>
</template>

<script>
import Label from '../../../../common/Label.js';
import CommonService from '../../../../services/CommonService.js';
import StarRating from 'vue-star-rating';
import BookService from '../../../../services/BookService.js';
import * as CONST from '../../../../common/const.js';

export default {
    name: 'book-detail-add-instance',
    mixins: [Label, CommonService, BookService],
    components: { StarRating },
    data() {
        return {
            totalNumber: 0,
            numberOfBook: 0,
            sharingStatus: 1,
            defaultSharingStatus: CONST.STT_SHARING,
        };
    },
    mounted() {
        this.initData();
    },
    methods: {
        initData() {
            if(this.instanceInfo.notSharingTotal > 0) {
                 this.sharingStatus = 0;
            }
            this.totalNumber = this.instanceInfo.notSharingTotal + this.instanceInfo.sharingTotal 
                                + this.instanceInfo.borrowingTotal + this.instanceInfo.lostTotal;
            this.numberOfBook = this.totalNumber;
        },
        hideDialog() {
            this.$emit('change-status', this.isShow);
            this.$modal.hide('book-detail-add-instance');
        },
        showDialog() {
            this.$modal.show('book-detail-add-instance');
        },
        changeStatusModal(isShow) {
            if (isShow) {
                this.showDialog();
            } else {
                this.hideDialog();
            }
        },
        changeSharingStt(target) {
            this.sharingStatus = target.value ? CONST.STT_SHARING : CONST.STT_NOT_SHARING;
        },
        addBookToLibrary() {
            var number = this.numberOfBook - this.totalNumber;
            this.$emit('addBook', this.sharingStatus, number);
        }
    },
    props: {
        isShow: {
            required: true,
            default: false
        },
        book: {
            required: true,
            default: false
        },
        instanceInfo: {
            required: true,
            default: {}
        }
    },
    watch: {
        'isShow': function() {
            this.changeStatusModal(this.isShow);
        },
        'instanceInfo': function() {
            this.initData();
        },
    }


}
</script>


<style lang="scss">
@import '../../../../scss/text_styles.scss';
@import '../../../../scss/common_styles.scss';
@import 'AddInstance.scss';
</style>




