<template>
    <div class="small-form-cover">
        <div class="free-row-top"></div>
        <div class="login-form-wrapper center-block">
            <div class="row login-form box-shadow">
                <div class="col-xs-4 social-login-form">
                    <social-login @firebaseRegister="firebaseRegister"></social-login>
                </div>
                <div class="col-xs-8 email-login-form">
                    <email-login @firebaseRegister="firebaseRegister"></email-login>
                </div>
            </div>
        </div>
        <div class="free-row-bottom">
            <router-link :to="{name:'home'}" >
                <span class="fa fa-home fa-2x" style="margin-right:10px"></span>
                {{label.back_to_homepage}}
            </router-link>
        </div>
    </div>
</template>

<script>
import Label from '../../../../common/Label.js'
import EmailLogin from '../components/EmailLogin.vue'
import SocialLogin from '../components/SocialLogin.vue'
import BookService from '../../../../services/BookService.js'
import * as CONST from '../../../../common/const.js'
export default {
    name: 'login',
    mixins: [ BookService, Label ],
    components: { EmailLogin, SocialLogin },
    data() {
        return {
            signinMode: true
        }
    },
    methods: {
        firebaseRegister() {
            
        }
    }

}
</script>

<style scoped>
@import 'userLogin.scss';
</style>



