<template>
    <div :class="{'app-gat-scroll': isScroll, 'app-gat': false == isScroll}">
        <div class="app-gat-content">
            <div class="app-gat-content-image">
                <img class="image-fill img-book" src="../../../assets/images/gat_poster.jpg"></img>
                <div style="clear: both;"></div>
            </div>
            <div class="app-gat-content-download">
                <div class="app-gat-btn left-side" style="margin-right: 15px;">
                    <a target="_blank" href="https://play.google.com/store/apps/details?id=com.gat" :title="label.txt_download_app_android">
                        <img class="image-fill img-book " src="../../../assets/images/download_android.png"></img>
                    </a>
                </div>
                <div class="app-gat-btn left-side">
                    <a target="_blank" href="https://itunes.apple.com/vn/app/gat/id1230316898?mt=8" :title="label.txt_download_app_ios">
                        <img class=" image-fill img-book " src="../../../assets/images/download_ios.png"></img>
                    </a>
                </div>
                <div style="clear: both;"></div>
            </div>
            <div style="clear: both;"></div>
        </div>
        <div class="app-gat-fan-page" id="app-gat-fan-page" v-show="isReadyFaceBook">
            <div class="fb-page" data-href="https://www.facebook.com/gatbook.org/" data-width="275" data-height="230" data-small-header="false" data-adapt-container-width="true" data-hide-cover="false" data-show-facepile="true">
                <blockquote cite="https://www.facebook.com/gatbook.org/" class="fb-xfbml-parse-ignore">
                    <a href="https://www.facebook.com/gatbook.org/">GaT - Give and Take The Book</a>
                </blockquote>
            </div>
        </div>
        <div style="clear: both;"></div>
    </div>
</template>

<script>
import CommonService from '../../../services/CommonService.js';
import Label from '../../../common/Label.js';

export default {
    name: 'app-get',
    mixins: [CommonService, Label],
    data() {
        return {
            isReadyFaceBook: false,
            isRuningLoadSDKFacebook: null,
        }
    },
    mounted() {
        var self = this;
        // If processing to load SDK facebook is running, stop it.
        if (null != self.isRuningLoadSDKFacebook) {
            clearInterval(self.isRuningLoadSDKFacebook);
        }
        self.isRuningLoadSDKFacebook = setInterval(function() {
            if (window.isReadyFaceBook == 1) {
                window.isReadyFaceBook = 2;
                self.isReadyFaceBook = true;
                window.domFacebookPage = $('#app-gat-fan-page').html();
                clearInterval(self.isRuningLoadSDKFacebook);
                self.isRuningLoadSDKFacebook = null;
            }
            if (window.isReadyFaceBook > 1) {
                self.isReadyFaceBook = true;
                $('#app-gat-fan-page').html(window.domFacebookPage);
                clearInterval(self.isRuningLoadSDKFacebook);
                self.isRuningLoadSDKFacebook = null;
            }
        }, 500);
    },

    methods: {

    },
    props: {
        isScroll: {
            required: false,
            default: true
        }
    }
}
</script>

<style lang="scss">
@import '../../../scss/common_styles.scss';
@import 'appGat.scss';
</style>







