<template>
    <router-link :to="{ name: label.NAME_USER_MESSAGE_DETAIL_PAGE, params: { id: this.item.targetId }}">
        <div v-on:click="setView" class="each-item"
            :class="{'not-read':item.userId!=thisUserId&&!item.isRead}">
            <div class="display-left target-user-border">
                <user-icon
                    :userIconStyle="userIconStyle"
                    :imageId="data.imageId">
                </user-icon>
            </div>
            <div class="display-left" style="margin-left:15px;width:410px;">
                <!--user-name-->
                <div class="half-row-top display-left each-item-text">
                    <span class="medium-text-form-m text-limit text-muted title-name-color">{{data.name}}</span>
                </div>
                <!--request-content-->
                <div class="half-row-bottom small-text-form-r text-limit display-left clear-left each-item-text">
                    {{data.message}}
                </div>
            </div>
            <div class="display-right small-text-form-r" style="margin-top:4px">
                {{label.datetime(item.timeStamp)}}
            </div>
        </div>
    </router-link>
</template>

<script>
import BookService from '../../../services/BookService.js'
import UserIcon from '../userIcon/UserIcon.vue'
import * as CONST from '../../../common/const.js'
import Label from '../../../common/Label.js'

export default {
    props: ["item"],
    mixins: [ BookService, Label ],
    components: { UserIcon },
    data() {
        return {
            thisUserId : localStorage.getItem(CONST.USER_ID),
            data: {},
            //style of user icon
            userIconStyle: {
                'background-color': 'white',
                'border-radius': '28px',
                'width': '48px',
                'height': '48px'
            },
        }
    },
    mounted() {
        this.getRowDetail();
    },
    watch: {
        item: function() {
            this.getRowDetail();
        }
    },
    methods: {
        getRowDetail() {
            var _self = this;
            _self.$getItem(CONST.VISITOR_USER).then(vu => {
                if (!vu) {
                    vu = [];
                }
                var targetUser = vu.filter(function(i) {
                    return i.userId ==  _self.item.targetId;
                });
                if (targetUser.length > 0) {
                    _self.data = {
                        name: targetUser[0].name,
                        imageId: targetUser[0].imageId,
                        message: _self.item.message
                    }
                } else {
                    _self.getUserDetail();
                }
            });
        },
        getUserDetail() {
            var _self = this;
            var config = {
                params: {
                    userId: _self.item.targetId
                }
            };
            _self.api.callGetApi(_self.api.api_get_username_image, config)
                .then(response => {
                        _self.data = {
                            name: response.data.data.resultInfo.name,
                            imageId: response.data.data.resultInfo.imageId,
                            message: _self.item.message
                        }
                        //update visitor list in localforage
                        _self.$getItem(CONST.VISITOR_USER).then(visitorUser => 
                            {
                                if (!visitorUser) {
                                    visitorUser = [];
                                }
                                var newVisitorUser = visitorUser.filter(function(i) {
                                    return i.userId != response.data.data.resultInfo.userId;
                                });
                                newVisitorUser.push(response.data.data.resultInfo);
                                _self.$setItem(CONST.VISITOR_USER, newVisitorUser);
                            }
                        );
                    })
                .catch(_self.handleErrorFunction);
        },
        setView(){
            this.item.isRead = true;
            let loginUserId = localStorage.getItem(CONST.USER_ID);
            let targetUserId = this.item.targetId;
            let childNode = loginUserId*1 > targetUserId*1? targetUserId+":"+loginUserId:loginUserId+":"+targetUserId;
            //browse the node that has isRead equal false
            let _self = this;
            //firebase db (tabel messages) refer
            let messagesRef =  window.firebaseDB.ref('messages');
            messagesRef.child(childNode).orderByChild('isRead').equalTo(false).once('value').then(function (snapShot) {
                if (snapShot.val()) {
                    var keys = Object.keys(snapShot.val());
                    var arr = Object.keys(snapShot.val()).map(key => snapShot.val()[key]);
                    
                    for (var i=0; i < keys.length; i++) {
                        if(arr[i].userId == "0" || arr[i].userId == _self.item.targetId) {
                            window.firebaseDB.ref('messages/'+childNode+'/'+keys[i]).update({ isRead: true });
                        }
                    }
                }
            });
        }
    }
}
</script>

<style>
  
</style>



