<template>
    <div>
        <div class="header-arrow-up user-req"></div>
        <div class="header-extension">
            <div style="height:6px;"></div>
            <div class="header-extension-no-item" v-if="requestList.length==0">
                {{label.no_request}}
            </div>
            <header-user-request-row
                v-for="(item, index) in requestList" :key="index" :item="item"
                ></header-user-request-row>

            <div class="header-extension-split"></div>
            <router-link :to="{name: label.NAME_USER_REQUEST_PAGE, query: {target: '0', type: '0'}}">
                <div class="header-extension-center-item">
                    <button class="btn btn-link medium-text-form-m">{{label.view_all}}</button>
                </div>
            </router-link>
            <div style="height:6px;"></div>
        </div>
    </div>
</template>

<script>
import HeaderUserRequestRow from './HeaderUserRequestRow.vue'
import Label from '../../../common/Label.js'

export default {
    props: [ "requestList" ],
    components: { HeaderUserRequestRow },
    mixins: [ Label ]
}
</script>

<style>

</style>



