<template>
    <div v-on:mouseenter="mouseIn" v-on:mouseleave="mouseOut" class="box-book-list">
        <!-- modal dialog for write review -->
        <modal :name="'review-dialog-for-' + book.readingId" :clickToClose="false" :width="650" :height="365">
            <div class="full-height" style="margin:0 !important">
                <!-- the left modal dialog -->
                <div class="display-left full-height" style="padding:20px 0 20px 20px;width:188px;">
                    <div class="box-book-dialog">
                        <div class="book-in-modal">
                            <a v-if="label.PATH_BOOK_DETAIL_PAGE" :href="label.PATH_BOOK_DETAIL_PAGE.replace(':id',book.editionId)">
                                <img :title="book.title" :src="getUrlImage(book.editionImageId)" class="image-fill img-book" :alt="book.title">
                            </a>
                        </div>
                        <div class="title-info">
                            <a v-if="label.PATH_BOOK_DETAIL_PAGE" :title="book.title" :href="label.PATH_BOOK_DETAIL_PAGE.replace(':id',book.editionId)">
                                <span class="medium-text-form-m text-limit text-muted title-name-color">{{book.title}}</span>
                            </a>
                        </div>
                        <div class="author-info">
                            <span class="small-text-form-r text-limit text-color-author">{{book.author}}</span>
                        </div>
                        <div class="rate-avg-info">
                            <star-rating 
                                :rating="book.rateAvg" 
                                :read-only="true" 
                                :increment="0.01" 
                                :show-rating="false" 
                                inactive-color="white" 
                                border-color="#f4c312" 
                                :border-width="1"
                                :star-size="14">
                            </star-rating>
                        </div>
                    </div>
                </div>
                <!-- the right side of modal dialog -->
                <div class="display-left full-height" style="padding:20px; width:462px">
                    <div class="medium-text-form-m">
                        {{label.yourReview}}
                    </div>
                    <div style="margin-top:8px;">
                        <star-rating 
                            :rating="selfRateTmp" 
                            :read-only="false" 
                            :increment="1" 
                            :show-rating="false" 
                            inactive-color="white" 
                            border-color="#f4c312" 
                            :border-width="1"
                            :star-size="14"
                            @rating-selected="changeRateTmp">
                        </star-rating>
                    </div>
                    <div style="margin-top:12px;">
                        <textarea :id="'review-form-' + book.readingId" class="form-control review-form" rows="9" v-model="selfReviewTmp"></textarea>
                    </div>
                    <div style="margin-top:18px; margin-left:245px">
                        <button v-on:click="selfUpdateEvaluation" type="button" class="btn btn-gat btn-circle review-form-button" style="float:left">
                            {{label.save}}
                        </button>
                        <button v-on:click="closePopup" type="button" class="btn btn-gat-cancel btn-circle review-form-button" style="float:right">
                            {{label.cancel}}
                        </button>
                    </div>
                </div>
            </div>
        </modal>
        <figure>
        <!-- review detail -->
        <div class="row nopadding">
            <!-- book image -->
            <div class="col-xs-2">
                <div class="box-image-small">
                    <a href="#">
                        <img :title="book.title" :src="getUrlImage(book.editionImageId)" class="image-fill img-book" :alt="book.title">
                    </a>
                </div>
            </div>
            <!-- book info -->
            <div class="col-xs-3" style="padding-left:5px !important">
                <div class="content-info">
                    <a :title="book.title" href="#">
                        <span class="medium-text-form-m text-limit text-muted title-name-color">{{book.title}}</span>
                    </a>
                </div>
                <div class="author-info">
                    <span v-if="book.author" class="small-text-form-r text-limit text-color-author">{{book.author}}</span>
                    <span v-if="!book.author" class="small-text-form-r text-limit text-color-author">&nbsp</span>
                </div>
                <div class="rate-avg">
                    <span class="left-side">
                        <star-rating 
                            :rating="book.rateAvg" 
                            :read-only="true" 
                            :increment="0.01" 
                            :show-rating="false" 
                            inactive-color="white" 
                            border-color="#f4c312" 
                            :border-width="1"
                            :star-size="14">
                        </star-rating>
                    </span>
                    <span class="right-side medium-text-form-r text-color-author" 
                        style="margin-left: 50px;">
                        {{book.rateAvg.toFixed(1)}}
                    </span>
                </div>
                <div v-if="book.borrowFromUserId" style="margin-top:19px; margin-right:10px;">
                    <div class="display-left">
                        <router-link :to="{ name: label.NAME_USER_REQUEST_DETAIL_PAGE, params: {id: book.borrowRecordId}}">
                            <span class="small-text-form-r center-vertical-text">{{label.borrowFrom}}</span>
                        </router-link>
                    </div>
                    <div class="display-rigth">
                        <router-link v-if="targetUserRouter" :title="book.borrowFromUserName" :to="targetUserRouter">
                            <div class="user-border pull-right">
                                <user-icon :userIconStyle="userIconStyle" :imageId="book.borrowFromUserImage"></user-icon>
                            </div>
                        </router-link>
                    </div>
                </div>
            </div>
            <!-- self review -->
            <div class="col-xs-7">
                <!-- edit button on top right -->
                <transition name="fade">
                    <div>
                        <div class="top-right-list">
                            <div v-if="showButton" type="button" v-on:click="showSmallIpop" class="edit-button pull-right">
                                <span class="glyphicon glyphicon-menu-down"></span>
                            </div>
                            <div v-if="showButton&&showButtonGroup" class="edit-button-group small-text-form-r">
                                <div v-on:click="updateReadingStt(READ_STT)" class="row nopadding edit-button-group-item border-bottom">
                                    <div class="col-xs-3 mini-menu-col-center">
                                        <span v-if="this.book.readingStatus==READ_STT" class="glyphicon glyphicon-ok"></span>
                                    </div>
                                    <div class="col-xs-9 mini-menu-col">{{label.read}}</div>
                                </div>
                                <div v-on:click="updateReadingStt(READING_STT)" class="row nopadding edit-button-group-item border-bottom">
                                    <div class="col-xs-3 mini-menu-col-center">
                                        <span v-if="this.book.readingStatus==READING_STT" class="glyphicon glyphicon-ok"></span>
                                    </div>
                                    <div class="col-xs-9 mini-menu-col">{{label.reading}}</div>
                                </div>
                                <div v-on:click="updateReadingStt(TO_READ_STT)" class="row nopadding edit-button-group-item border-bottom">
                                    <div class="col-xs-3 mini-menu-col-center">
                                        <span v-if="this.book.readingStatus==TO_READ_STT" class="glyphicon glyphicon-ok"></span>
                                    </div>
                                    <div class="col-xs-9 mini-menu-col">{{label.toRead}}</div>
                                </div>
                                <div v-on:click="updateReadingStt(NOT_EXIST)" class="row nopadding edit-button-group-item">
                                    <div class="col-xs-3 mini-menu-col-center">
                                        <span class="glyphicon glyphicon-trash"></span>
                                    </div>
                                    <div class="col-xs-9 mini-menu-col">{{label.remove}}</div>
                                </div>
                            </div>
                        </div>
                        <div class="bottom-left">
                            <button v-if="showButton" v-on:click="showReviewDialog" type="button" class="btn btn-gat btn-circle small-text-form-r review-button">
                                {{reviewButtonText}}
                            </button>
                        </div>
                    </div>
                </transition>
                
                <!-- self rate -->
                <div class="content-info">
                    <star-rating 
                        :rating="book.selfRate" 
                        :read-only="false" 
                        :increment="1" 
                        :show-rating="false" 
                        inactive-color="white" 
                        border-color="#f4c312" 
                        :border-width="1"
                        :star-size="14"
                        @rating-selected="updateRating">
                    </star-rating>
                </div>
                
                <!-- self review content -->
                <div class="small-text-form-r text-color-author">
                    <div v-if="book.selfReview">
                        <div class="display-left review-quote" style="width:4%;">
                            <span class="fa fa-quote-left"></span>
                        </div>
                        <div :title="book.selfReview" class="display-right review-info" style="width:96%;">
                            {{book.selfReview}}
                        </div>
                    </div>
                    <div v-else class="review-info">
                            {{label.reviewNA}}
                    </div>
                </div>
                
            </div>
        </div>
        </figure>
    </div>
</template>

<script>
import CommonService from '../../../../services/CommonService.js'
import BookService from '../../../../services/BookService.js'
import UserIcon from '../../../common/userIcon/UserIcon.vue'
import StarRating from 'vue-star-rating'
import Label from '../../../../common/Label.js'
import * as CONST from '../../../../common/const.js'
export default {
    name: 'book-in-read-history-list',
    components: { UserIcon, StarRating },
    mixins: [CommonService, BookService, Label],
    mounted() {
        this.selfRateTmp = this.book.selfRate;
        this.selfReviewTmp = this.book.selfReview;
    },
    computed: {
        reviewButtonText: function () {
            return this.book.selfReview? this.label.updateReview:this.label.writeReview;
        },
        reviewContent: function () {
            return this.book.selfReview? this.book.selfReview:this.label.reviewNA;
        },
        targetUserRouter: function() {
            if (this.book.borrowFromUserTypeFlag) {
                if (this.book.borrowFromUserTypeFlag == CONST.USER_TYPE_FLAG) {
                    return { name: this.label.NAME_USER_VISITOR_PAGE, params: {id: this.book.borrowFromUserId, tab: 'book-shelf'}};
                } else if (this.book.borrowFromUserTypeFlag == CONST.BOOKSTOP_TYPE_FLAG) {
                    return { name: this.label.NAME_BOOKSTOP_DETAIL_PAGE, params: {id: this.book.borrowFromUserId, tab: 'books'}};
                } else {
                    return null;
                }
            } 
        }
    },
    methods: {
        mouseIn() {
            this.showButton = true;
        },

        mouseOut() {
            this.showButton = false;
            this.showButtonGroup = false;
        },

        showSmallIpop() {
            this.showButtonGroup = !this.showButtonGroup;
        },

        showReviewDialog() {
            this.$modal.show('review-dialog-for-' + this.book.readingId);
        },

        changeRateTmp(rate) {
            this.selfRateTmp = rate;
        },

        updateRating(rate) {
            if (rate != this.book.selfRate) {
                var params = {
                    evaluationId: this.book.selfEvaluationId,
                    bookId: this.book.bookId,
                    editionId: this.book.editionId,
                    value: rate
                };
                this.book.selfRate = rate;
                this.updateEvaluation(params);
            }
        },
        selfUpdateEvaluation() {
            this.$modal.hide('review-dialog-for-' + this.book.readingId);
            if (this.selfRateTmp != this.book.selfRate || this.selfReviewTmp != this.book.selfReview) {
                var params = {
                    evaluationId: this.book.selfEvaluationId,
                    bookId: this.book.bookId,
                    editionId: this.book.editionId,
                    value: this.selfRateTmp,
                    review: this.selfReviewTmp
                };
                this.book.selfRate = this.selfRateTmp;
                this.book.selfReview = this.selfReviewTmp;
                this.updateEvaluation(params);
            }
        },
        closePopup() {
            this.$modal.hide('review-dialog-for-' + this.book.readingId);
        },
        updateEvaluation(params) {
            var headers = {
                'Authorization': this.loginToken
            }
            this.api.callPostApi(this.api.api_selfupdate_book_evaluation, params, {headers})
                    .then(this.updateEvaluationSuccess, this.handleErrorFunction);
        },
        updateReadingStt(newStt) {
            this.showButtonGroup = false;
            if (newStt != this.book.readingStatus) {
                var params = {
                    bookId: this.book.bookId,
                    editionId: this.book.editionId,
                    readingStatus: newStt
                };
                var headers = {
                    'Authorization': this.loginToken
                }
                this.book.readingStatus = newStt;
                this.api.callPostApi(this.api.api_selfupdate_reading_stt, params, {headers})
                    .then(this.updateStatusSuccess, this.handleErrorFunction);
            }
        },
        updateStatusSuccess(response) {
            this.refresh();
        },
        updateEvaluationSuccess(response) {
            this.book.selfEvaluationId = response.data.data.evaluationId;
            this.book.rateAvg = response.data.data.newAvgRate;
        }
    },
    data() {
        return {
            selfRateTmp: null,
            selfReviewTmp: "",
            showButton: false,
            showButtonGroup: false,
            READ_STT: CONST.READ_STT,
            READING_STT: CONST.READING_STT,
            TO_READ_STT: CONST.TO_READ_STT,
            NOT_EXIST: CONST.NOT_EXIST,
            userIconStyle: {
                'background-color': 'white',
                'border-radius': '16px',
                'width': '32px',
                'height': '32px',
                'margin': '0 auto'
            }
        }
    },
    props: {
        book: {
            required: true
        },
        refresh: {
            required: true
        }
    }
}
</script>

<style>

</style>



