<template>
    <div class="container-gat reset-padding">
        <div class="row">
            <div class="col-xs-3">
                <div class="affix">
                    <request-nav :requestSummary="requestSummary" :requestDetail="requestDetail"></request-nav>
                </div>
            </div>
            <div v-if="!$route.params.id" class="col-xs-9 padding-left-10">
                <request-list></request-list>
            </div>
            <div v-else class="col-xs-9 padding-left-10">
                <request-detail :getRequestSummary="getRequestSummary" @dataLoaded="requestDetail = $event" @getRequestChange="getRequestChange"></request-detail>
            </div>
        </div>
    </div>
</template>

<script>
import BookService from '../../../../services/BookService.js'
import RequestNav from '../components/RequestNav.vue'
import RequestList from '../components/RequestList.vue'
import RequestDetail from '../components/RequestDetail.vue'
export default {

    mixins: [BookService],
    components: { RequestNav, RequestList, RequestDetail },
    mounted() {
        if(this.loginToken){
            this.getRequestSummary();
        }
    },
    watch: {
        '$route': function(){
            if(this.loginToken){
                this.getRequestSummary();
            }
        }
    },
    methods: {
        getRequestSummary() {
            var config = {
                headers: {
                    'Authorization': this.loginToken
                }
            };
            this.api.callGetApi(this.api.api_get_request_summary, config)
                .then(response => {
                        this.requestSummary = response.data.data;
                    })
                .catch(this.handleErrorFunction);
        },
        getRequestChange() {
            this.$emit('getRequestChange');
        }
    },
    data() {
        return {
            requestSummary: {
                fromYou:{},
                toYou:{}
            },
            requestDetail:{
                target: null,
                type: null
            }
            
        }
    }
}
</script>

<style lang="scss">
@import 'userRequest.scss';
</style>



