<template>
    <div class="row nopadding per-bookstop-info">
        <div class="col-xs-2" style="margin-top:10px">
            <user-icon :userIconStyle="userIconStyle" :imageId="bookstop.imageId"></user-icon>
        </div>
        <div class="col-xs-10" style="margin-top:10px; padding-left:30px;">
            <p class="medium-text-form-m text-color-default text-limit">{{bookstop.name}}</p>
            <p class="small-text-form-r text-color-author text-limit">{{bookstop.address}}</p>
            <div class="row reset-margin list-bookstop-index">
                <div class="col-xs-6 reset-padding">
                    <span>
                        <img src="../../../assets/images/icon_book.png" class="list-bookstop-index-book">
                    </span>
                    <span class="small-text-form-r text-color-author">{{bookstop.sharingCount}}</span>
                </div>
                <div v-if="bookstop.distance>0" class="col-xs-6 reset-padding">
                    <span>
                        <img src="../../../assets/images/icon_distance.png" class="list-bookstop-index-map-marker">
                    </span>
                    <span class="small-text-form-r text-color-author">{{bookstop.distance}}</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import UserIcon from '../../common/userIcon/UserIcon.vue';
import CommonService from '../../../services/CommonService.js'
import * as CONST from '../../../common/const.js'
import Label from '../../../common/Label.js'
export default {
    props: ["bookstop"],
    mixins: [ Label, CommonService ],
    components: { UserIcon },
    data() {
        return {
            userIconStyle: {
                'background-color': 'white',
                'border-radius': '35px',
                'width': '70px',
                'height': '70px',
                'margin-top': '2px'
            },
        }
    },
}
</script>

<style>

</style>



