<template>
  <div id="app">
    <meta-info :title="title"></meta-info>
    <div class="row reset-margin fixed-header">
      <header-gat :imageId="imageId" :getRequestToggle="getRequestToggle"></header-gat>
    </div>
    <div v-if="isTabHeader">
      <tab-header></tab-header>
    </div>
    <div class="row reset-margin main-content" :class="{'no-tab-header-content':!isTabHeader, 'has-tab-header-content':isTabHeader}">
      <router-view @imageChanged="getUserImageId" @getRequestChange="getRequestToggle=!getRequestToggle"></router-view>
    </div>
    <div v-if="isFooter">
      <footer-gat></footer-gat>
    </div>
  </div>
</template>

<script>
import FooterGat from './views/common/footer/FooterGat.vue';
import HeaderGat from './views/common/header/HeaderGat.vue';
import TabHeader from './views/common/tabHeader/TabHeader.vue';
import ErrorDialog from './views/common/dialog/ErrorDialog.vue';
import * as CONST from './common/const.js';
import Firebase from 'firebase';
import MetaInfo from './views/common/metaInfo/MetaInfo.vue';
import Label from './common/Label.js';
import BookService from './services/BookService.js';
import CommonService from './services/CommonService.js';

//define global firebase db variable
let app = Firebase.initializeApp(CONST.FIREBASE_CONFIG);
window.firebaseDB = app.database();

//define uuid
let uuid;
if (localStorage.getItem(CONST.UUID)) {
  uuid = localStorage.getItem(CONST.UUID);
} else {
  let uuidUtil = require("uuid");
  uuid = uuidUtil.v4();
  localStorage.setItem(CONST.UUID, uuid);
}
window.uuid = uuid;


// firebase.auth().createUserWithEmailAndPassword('namnh.as2@test.com', 'password').catch(function(error) {
//   // Handle Errors here.
//   var errorCode = error.code;
//   var errorMessage = error.message;
//   // ...
// });

export default {
  name: 'app',
  components: { HeaderGat, FooterGat, ErrorDialog, MetaInfo, TabHeader },
  mounted() {
    this.initData();
  },
  mixins: [Label, BookService, CommonService],
  watch: {
    '$route': function() {
      this.initData();
    }
  },
  computed: {
    isFooter: function() {
      return this.$route.name == CONST.NAME_HOME_PAGE
        || this.$route.name == CONST.NAME_SHARE_BOOKS_PAGE
        || this.$route.name == CONST.NAME_SEARCH_BOOK_PAGE
        || this.$route.name == CONST.NAME_SEARCH_AUTHOR_PAGE
        || this.$route.name == CONST.NAME_SEARCH_USER_PAGE
        || this.$route.name == CONST.NAME_NEAR_BY_USER
        || this.$route.name == CONST.NAME_BOOK_DETAIL_PAGE
        || this.$route.name == CONST.NAME_LIST_BOOKSTOPS_PAGE
        || this.$route.name == CONST.NAME_BOOKSTOP_DETAIL_PAGE;
    },
    isTabHeader: function() {
      return this.$route.name == CONST.NAME_HOME_PAGE
        || this.$route.name == CONST.NAME_SHARE_BOOKS_PAGE
        || this.$route.name == CONST.NAME_SEARCH_BOOK_PAGE
        || this.$route.name == CONST.NAME_SEARCH_AUTHOR_PAGE
        || this.$route.name == CONST.NAME_SEARCH_USER_PAGE
        || this.$route.name == CONST.NAME_NEAR_BY_USER
        || this.$route.name == CONST.NAME_BOOK_DETAIL_PAGE
        || this.$route.name == CONST.NAME_LIST_BOOKSTOPS_PAGE
        || this.$route.name == CONST.NAME_BOOKSTOP_DETAIL_PAGE;
    }
  },
  data() {
    return {
      imageId: null,
      getRequestToggle: false,
      title: '',
    }
  },
  methods: {
    initData(){
      var nameOfPage = this.$route.name;
      // If loading page which need login to accept, go to login page
      this.checkSecurityPage(nameOfPage);

      // Focus to top of page
      window.scrollTo(0, 0);
      this.getUserImageId();
      this.changeTitlePage(nameOfPage);
    },
    getUserImageId() {
      this.$getItem(CONST.LOGIN_USER).then(response => {
        this.imageId = response ? response.imageId : "";
      });
    },
    changeTitlePage(nameOfPage) {
      switch (nameOfPage) {
        case this.label.NAME_HOME_PAGE:
          this.title = this.label.TITLE_HOME_PAGE;
          break;
        case this.label.NAME_SHARE_BOOKS_PAGE:
          this.title = this.label.TITLE_SHARE_BOOKS_PAGE;
          break;
        case this.label.NAME_LIST_BOOKSTOPS_PAGE:
          this.title = this.label.TITLE_LIST_BOOKSTOPS_PAGE;
          break;
        case this.label.NAME_SEARCH_BOOK_PAGE:
        case this.label.NAME_SEARCH_AUTHOR_PAGE:
        case this.label.NAME_SEARCH_USER_PAGE:
          this.title = this.label.TITLE_SEARCH_PAGE.replace(CONST.KEY_REPLACE_01, this.$route.query.str);
          break;
        default:
          this.title = this.label.TITLE_HOME;
      }
    },
    checkSecurityPage(nameOfPage){
      //If user does not login and accept to page which need login
      if(this.isNull(this.loginToken) && CONST.ARRAY_NAME_PAGE_CHECK_LOGIN.indexOf(nameOfPage) >= 0){
        this.$router.replace({
          path: '/login/signin'
        });
      }
    }
  }
}

</script>

<style lang="scss">
@import './scss/text_styles.scss';
@import './scss/common_styles.scss';

@import url('https://fonts.googleapis.com/css?family=Montserrat:300,400,500');

#app {
  position: relative;
  background: #f8f8f8;
  font-family: 'Montserrat';
  font-size: 14px;
  font-weight: 400;
  background-color: #ffffff;
}
</style>




