<template>
    <div class="user-near-you-item">
        <div class="user-near-you-item-icon">
            <user-icon :userIconStyle="userIconStyle" :imageId="userNearYou.imageId"></user-icon>
        </div>
        <div class="user-near-you-item-content">
            <div class="user-near-you-item-box-name">
                <div class="super-biger-text-form-m text-limit user-near-you-item-txt-name">{{userNearYou.name}}</div>
                <div class="medium-text-form-r text-color-address user-near-you-item-txt-distance">{{getDistance(userNearYou.distance)}}</div>
            </div>
            <p class="medium-text-form-m text-color-address user-near-you-item-address">{{userNearYou.address}}</p>
            <div class="btn-radius inline-block box-color-number-share-book">
                <p class="reset-margin small-text-form-r text-color-btn">{{userNearYou.sharingCount}}</p>
            </div>
            <div class="inline-block">
                <p class="reset-margin small-text-form-r text-color-highlight-number">{{label.sharingBook}}</p>
            </div>
        </div>
        <div style="clear: both;"></div>
    </div>
</template>
<script>
import UserIcon from '../../../common/userIcon/UserIcon.vue';
import CommonService from '../../../../services/CommonService.js';
import Label from '../../../../common/Label.js';

export default {
    name: 'user-near-you-item',
    mixins: [CommonService, Label],
    components: { UserIcon},
    data() {
        return {
            userIconStyle: {
                'background-color': 'white',
                'border-radius': '25px',
                'width': '50px',
                'height': '50px',
                'margin-top': '2px'
            },
            userNearYou: {
                "userId": "",
                "locationId": "",
                "name": "",
                "imageId": "",
                "address": "",
                "latitude": 0,
                "longitude": 0,
                "distance": 0,
                "sharingCount": 0,
                "readCount": 0
            }
        }
    },
    mounted() {
        this.initData();
    },
    methods: {
        initData() {
            if (this.isNotNull(this.user)) {
                this.userNearYou = this.user;
            }
        },
    },
    props: {
        user: {
            required: true
        },
        currentLocation: {
            required: true
        },
    },
    watch: {
        'user': function () {
            this.initData();
        },
        'currentLocation': function () {
            this.initData();
        }
    }
}
</script>
<style lang="scss">
@import '../../../../scss/common_styles.scss';
@import 'UserNearYouItem.scss';
</style>



