<template>
    <div>
        <borrow-dialog :dialogName="'book-detail-list-user-share-book-borrow-confirm'" :isOpen="isShowSendRequestModal" :content="confirmDialogContent" :book="book" @requestSent="$emit('showLoadingDialog');" @requestComplete="updateRequestStatus" @requestFailed="requestBorrowFailed" @dialogClosed="closeSendRequestDialog"></borrow-dialog>
        <div class="book-detail-content-right-list-share-left">
            <div class="book-detail-content-right-list-share-user">
                <router-link :title="user.name" :to="{ name: label.NAME_USER_VISITOR_PAGE, params: {id: user.userId, tab: 'book-shelf'}}"
                v-for="(user, index) in totalUserShare.sharingRepresent" :key="user.userId" v-if="index < 2">
                    <user-icon :userIconStyle="userIconStyle" :imageId="user.imageId"></user-icon>
                </router-link>
            </div>
            <p class="medium-text-form-r text-limit txt-number-user-share-book">{{getTotalUserShareBook}}</p>
        </div>
        <button class="book-detail-content-right-list-share-btn btn-no-border" @click="getListUserShare" v-if="totalUserShare.sharingCount > 0">
            {{label.txt_list_share_book}}
        </button>
        <modal name="list-user-share-book" :height="380" :clickToClose="false">
            <div class="book-detail-list-user-share-box-title">
                <button class="close reset-outline reset-border" @click="closePopup">&times;</button>
                <h4 class="modal-title super-biger-text-form-m align-center">{{label.txt_user_share}}</h4>
            </div>
            <hr class="line-break-category-color reset-padding reset-margin">
            <vue-perfect-scrollbar class="book-detail-list-user-share-content">
                <div v-for="(user, index) in listUser" :key="user.userId">
                    <div class="row book-detail-list-user-share-popup-item">
                        <div class="book-detail-content-right-list-share-user-icon">
                            <user-icon :userIconStyle="userIconStyle" :imageId="user.imageId"></user-icon>
                        </div>
                        <div class="book-detail-content-right-list-share-user-info">
                            <div class="box-block">
                                <span class="medium-text-form-m text-color-default left-side" v-if="isNotNull(user.name)">{{user.name}}</span>
                                <span class="medium-text-form-r text-color-author right-side" v-if="isNotNull(user.distance)">{{getDistance(user.distance)}}</span>
                                <div style="clear: both;"></div>
                            </div>
                            <p class="medium-text-form-r text-color-author">{{getContentLimitLength(user.address, 48)}}</p>
                            <p class="medium-text-form-r text-color-default" v-if="isWaitBorrow(user) || 1 == user.recordStatus">{{label.txt_borrowing_book}}</p>
                            <!-- <p class="medium-text-form-r text-color-default" v-if="isShowRecordStatusUnderAddress(user)">{{user.name}}</p> -->
                        </div>
                        <div v-if="loginUserId != user.userId" class="book-detail-list-share-user-btn">
                            <button class="book-detail-content-right-list-share-user-action box-color-header btn-no-border" @click="showSendRequestDialog(index, user.userId)" v-if="isRequest(user)">
                                {{label.txt_user_action[0]}}
                            </button>
                            <button class="book-detail-content-right-list-share-user-action box-color-btn-wait btn-no-border" @click="showSendRequestDialog(index, user.userId)" v-if="isWaitBorrow(user)">
                                {{label.txt_user_action[1]}}
                            </button>
                            <button class="book-detail-wait-confirm btn-no-border" v-if="isShowRecordStatus(user)" @click="clickBtnWaitConfirm(user)">
                                <span class="medium-text-form-r text-color-author">{{label.txt_record_status[user.recordStatus]}}</span>
                                <span class="text-color-author" style="margin-left:5px;">
                                    <i class="fa fa-angle-right fa-lg" aria-hidden="true"></i>
                                </span>
                            </button> 
                            <img class="book-detail-list-share-user-img-reading" src="../../../../assets/images/icon_reading_here.png" v-if="isReadingHere(user)"></img>
                            <div style="clear: both;"></div>
                            <!-- <button class="book-detail-wait-confirm btn-no-border" v-if="isWaitConfirm" @click="clickBtnWaitConfirm">
                                <span class="medium-text-form-r text-color-author">{{label.txt_user_action[2]}}</span>
                                <span class="text-color-author" style="margin-left:5px;">
                                    <i class="fa fa-angle-right fa-lg" aria-hidden="true"></i>
                                </span>
                            </button> -->
                        </div>
                    </div>
                    <hr class="line-break-category-color reset-padding reset-margin" v-if="index < listUser.length - 1">
                </div>
                <infinite-loading :on-infinite="getListUserShareMore" ref="infiniteLoading"></infinite-loading>
                <div style="clear: both;"></div>
            </vue-perfect-scrollbar>
        </modal>
    </div>
</template>

<script>
import InfiniteLoading from 'vue-infinite-loading'
import * as CONST from '../../../../common/const.js';
import UserIcon from '../../../common/userIcon/UserIcon';
import CommonService from '../../../../services/CommonService.js';
import StarRating from 'vue-star-rating';
import BookService from '../../../../services/BookService.js';
import VuePerfectScrollbar from 'vue-perfect-scrollbar';
import Label from '../../../../common/Label.js';
import BorrowDialog from '../../../common/dialog/BorrowDialog.vue';

export default {
    name: 'book-detail-list-user-share-book',
    components: {
        StarRating, UserIcon, VuePerfectScrollbar, InfiniteLoading, BorrowDialog
    },
    mixins: [CommonService, BookService, Label],
    data() {
        return {
            userIconStyle: {
                'background-color': 'white',
                'border-radius': '18px',
                'width': '40px',
                'height': '40px',
                'float': 'left',
            },
            listUser: [],
            totalUserShare: {sharingCount : 0, sharingRepresent : {}},
            currentPageListUser: 1,
            config_get_list_user: {
                params: {
                    editionId: this.book.editionId,
                    userId: this.loginUserId,
                    per_page: 10,
                    page: 1,
                    latitude: null,
                    longitude: null,
                }
            },
            config_get_total_share: {
                params: {
                    editionId: this.book.editionId
                }
            },
            config_create_request: {
                bookId: this.book.bookId,
                editionId: this.book.editionId,
                ownerId: this.loginUserId,
                expectation: 0
            },
            isWaitConfirm: false,
            requestToUserIndex: -1,
            isShowConfirmDialog: false,
            isShowSendRequestModal: false,
            confirmDialogContent: '',
        }
    },
    mounted() {
        this.initData();
    },
    computed: {
        getTotalUserShareBook() {
            try {
                var count = this.totalUserShare.sharingCount;
                if (this.isNull(count) || count <= 0) {
                    count = 0;
                }
                var listUserTotal = this.totalUserShare.sharingRepresent;

                if (0 == count || this.isNull(listUserTotal) || listUserTotal.length == 0) {
                    return this.label.txt_number_user_share_book_0;
                } else if (count == 1) {
                    return this.label.txt_number_user_share_book_1.replace("{0}", listUserTotal[0].name);
                } else if (count == 2) {
                    return this.label.txt_number_user_share_book_2.replace("{0}", listUserTotal[0].name).replace("{1}", listUserTotal[1].name);
                } else {
                    return this.label.txt_number_user_share_book_3.replace("{0}", listUserTotal[0].name).replace("{1}", listUserTotal[1].name).replace("{2}", count - 2);
                }
            }
            catch (err) {
                console.log(err);
                return this.label.txt_number_user_share_book_0;
            }
        }
    },
    methods: {
        initData() {
            this.config_get_list_user.params.userId = this.loginUserId;
            this.updateConfigToGetListUser();

            // Convert author from array to string
            this.book.author = this.getAuthor(this.book.author);

            // Set total user share book
            if(window.callApiTotalUserShare){
                this.totalUserShare = this.listUserShareObject.totalUserShare;
            }

            // Set list user share book
            if(window.callApiListUserShare){
                this.listUser = this.listUserShareObject.listUserShare;
            } 
        },
        getListUserShare() {
            // If list user share book is loaded from parent component, display
            // Else call api to get list
            if(window.callApiListUserShare){
                this.$modal.show('list-user-share-book');
            }else{
                this.$emit("showLoadingDialog");
                this.api.callGetApi(this.api.api_get_edition_sharing_user, this.config_get_list_user).then(this.setListUserShare, this.handleErrorFunction);
            }
        },
        setListUserShare(response) {
            this.$emit("hideLoadingDialog");
            if (200 == response.status) {
                this.listUser = response.data.data.resultInfo;
                this.$modal.show('list-user-share-book');
            } else {
                this.$modal.hide('list-user-share-book');
            }
        },
        updateConfigToGetListUser(){
            if(this.isNull(this.loginUser.usuallyLocation) || this.isNull(this.loginUser.usuallyLocation[0])) {
                return;
            }
            if(null == window.isRuningLoadSDKGoogle && window.isReadyGoogle && this.isNotNull(window.currentLocation)) {
                this.loginUser.usuallyLocation[0].latitude = window.currentLocation.lat;
                this.loginUser.usuallyLocation[0].longitude = window.currentLocation.lng;
            }
            this.config_get_list_user.params.latitude = this.loginUser.usuallyLocation[0].latitude;
            this.config_get_list_user.params.longitude = this.loginUser.usuallyLocation[0].longitude;
        },
        setListUserShareMore(response) {
            if (200 == response.status) {
                var data = response.data.data.resultInfo;
                this.listUserShareObject.listUserShare = this.listUser.concat(data);
                if (data.length < 10) {
                    this.$refs.infiniteLoading.$emit('$InfiniteLoading:complete');
                } else {
                    this.$refs.infiniteLoading.$emit('$InfiniteLoading:loaded');
                }
            } else {
                this.$refs.infiniteLoading.$emit('$InfiniteLoading:loaded');
            }
        },
        getListUserShareMore() {
            setTimeout(() => {
                this.currentPageListUser = this.currentPageListUser + 1;
                var config = this.config_get_list_user;
                config.params.page = this.currentPageListUser;
                this.api.callGetApi(this.api.api_get_edition_sharing_user, config).then(this.setListUserShareMore, this.handleErrorFunction);
            }, 600);
        },
        closePopup() {
            this.$modal.hide('list-user-share-book');
        },
        isRequest(user) {
            if (2 != user.userTypeFlag && 1 == user.availableStatus && 0 == user.requestingStatus) {
                return true;
            }
            return false;
        },
        isWaitBorrow(user) {
            if (2 != user.userTypeFlag && 0 == user.availableStatus && 0 == user.requestingStatus) {
                return true;
            }
            return false;
        },
        isReadingHere(user) {
            if ( 2 == user.userTypeFlag ) {
                return true;
            }
            return false;
        },
        isShowRecordStatus(user) {
            if ((0 == user.availableStatus || 1 == user.availableStatus) && 1 == user.requestingStatus) {
                return true;
            }
            return false;
        },
        isShowRecordStatusUnderAddress(user) {
            if (this.loginUserId != user.userId && 0 == user.availableStatus && 1 == user.requestingStatus) {
                return true;
            }
            return false;
        },
        clickBtnWaitConfirm(user) {
            if (this.isNotNull(this.loginToken)) {
                this.$router.push({ name: this.label.NAME_USER_REQUEST_DETAIL_PAGE, params: {id: user.recordId}});
            } else {
                this.$emit('showConfirmDialog');
            }
        },
        showSendRequestDialog(index, userId) {
            if (this.isNotNull(this.loginToken)) {
                this.isShowSendRequestModal = true;

                // Update index of user who login user send request
                this.requestToUserIndex = index;
                this.book.userId = this.listUser[index].userId;

                // Create content
                this.confirmDialogContent = this.label.sendBorrowRequestConfirm(this.userName, this.book.title);

                // Update paramaster to call api
                this.config_create_request.ownerId = userId;
                this.$modal.hide('list-user-share-book');
            } else {
                this.$emit('showConfirmDialog');
            }
        },
        closeSendRequestDialog() {
            this.isShowSendRequestModal = false;
            this.$modal.show('list-user-share-book');
        },

        updateRequestStatus(response) {
            this.listUser[this.requestToUserIndex].requestingStatus = 1;
            this.listUser[this.requestToUserIndex].recordStatus = this.book.recordStatus;
            this.listUser[this.requestToUserIndex].recordId = this.book.recordId;
            this.$emit("hideLoadingDialog");
        },

        requestBorrowFailed($event) {
            this.$emit("hideLoadingDialog");
            this.$emit('showErrorDialog', $event);
            this.$modal.show('list-user-share-book');
        },
    },
    watch: {
        'listUserShareObject.totalUserShare' : function () {
            this.totalUserShare = this.listUserShareObject.totalUserShare;
        },
        'listUserShareObject.listUserShare' : function () {
            this.listUser = this.listUserShareObject.listUserShare;
        },
        'loginUser.usuallyLocation' : function () {
            this.updateConfigToGetListUser();
        },
    },
    props: {
        book: {
            require: true
        },
        editionId: {
            require: true
        },
        listUserShareObject: {
            required: true
        },
        loginUser: {
            required: true
        },
    },
}
</script>

<style lang="scss">
@import '../../../../scss/common_styles.scss';
@import '../../../../scss/text_styles.scss';
@import 'listUserShareBookInDetailPage.scss'
</style>




