<template>
    <div class="user-profile-detail-form box-shadow-bold">
        <!-- title -->
        <div class="page-detail-header medium-text-form-m">
            <div v-if="!isLoading" class="">
                {{targetUser.name}}
            </div>
        </div>
        <!-- message container -->
        <div v-if="isLoading" class="center-x190 loading-cover">
            <img src="../../../../assets/images/loading.gif" class="loading-img-medium">
        </div>
        <vue-perfect-scrollbar class="message-container" id="chatbox">
            <!-- infinite loading -->
            <div style="margin-left:15px; margin-right:20px;">
                <!-- each row -->
                <div v-for="(item, index) in msg" :key="index" class="row nopadding-hoz">
                    <div v-if="index==0||getDate(item.timeStamp) != getDate(msg[index-1].timeStamp)" class="row medium-button-text text-color-author split-by-date">
                        {{label.getDateTimeMessageSplit(item.timeStamp)}}
                    </div>
                    <div v-else-if="index>0&&item.userId != msg[index-1].userId" class="row" style="height:15px"></div>
                    <!-- target user message row -->
                    <div v-if="item.userId!=loginUserId">
                        <!-- target user icon -->
                        <div v-if="item.userId==$route.params.id&&(index==msg.length-1||(msg[index+1]&&msg[index+1].userId==loginUserId)||(msg[index+1]&&msg[index+1].userId==0))" class="col-xs-2 nopadding-hoz target-user-border">
                            <router-link v-if="targetUserRouter" :to="targetUserRouter">
                                <user-icon
                                    :userIconStyle="userIconStyle"
                                    :imageId="targetUser.imageId">
                                </user-icon>
                            </router-link>
                        </div>
                        <div v-else-if="item.userId==0&&(index==msg.length-1||(msg[index+1]&&msg[index+1].userId==loginUserId)||(msg[index+1]&&msg[index+1].userId==$route.params.id))" class="col-xs-2 nopadding-hoz target-user-border">
                            <user-icon
                                :userIconStyle="userIconStyle"
                                :imageId="SYSTEM_IMAGE">
                            </user-icon>
                        </div>
                        <div v-else class="col-xs-2 nopadding-hoz" style="width:52px;height:52px;"></div>
                        
                        <!-- target message content -->
                        <div class="col-xs-10">
                            <div class="message-content-container target-user">
                            {{item.message}}
                            </div>
                        </div>
                    </div>

                    <!-- your own message row -->
                    <div v-if="item.userId==loginUserId">
                        <div class="display-right message-content-container your-own">
                            {{item.message}}
                        </div>
                    </div>
                </div>
            </div>
        </vue-perfect-scrollbar>
        <!-- input message -->
        <div class="write-message-box">
            <div class="row">
                <div class="col-xs-11">
                    <input type="text" id="message-box" class="input-message-form" @keyup.enter="sendMessage" v-model="text" :placeholder="label.inputMessagePlaceholder">
                </div>
                <div class="col-xs-1">
                    <!-- send button -->
                    <button v-on:click="sendMessage" class="btn btn-gat btn-circle send-message-button">
                        <span class="fa fa-paper-plane-o"></span>
                    </button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import InfiniteLoading from 'vue-infinite-loading'
import VuePerfectScrollbar from 'vue-perfect-scrollbar'
import BookService from '../../../../services/BookService.js'
import UserIcon from '../../../common/userIcon/UserIcon.vue'
import Label from '../../../../common/Label.js'
import * as CONST from '../../../../common/const.js'

//firebase db (tabel users) refer
let userRef = window.firebaseDB.ref('users');
//firebase db (tabel messages) refer
let messagesRef = window.firebaseDB.ref('messages');

export default {
    mixins: [ Label, BookService ],
    components: { InfiniteLoading, VuePerfectScrollbar, UserIcon },
    mounted() {
        this.init();
    },
    watch: {
        msg: function(to, from) {
            if (to){
                //set scroll to bottom after load message from firebase
                setTimeout(()=>{
                    $("#chatbox").scrollTop($('#chatbox').prop("scrollHeight"));
                }, 100);
            }
        },
        isLoading: function(to) {
            if (!to) {
                //set scroll to bottom after load message from firebase
                setTimeout(()=>{
                    $("#chatbox").scrollTop($('#chatbox').prop("scrollHeight"));
                }, 100);
            }
        },
        '$route': function(){
            this.init();
        }
    },
    computed: {
        //get message child node from login user id and target user id
        childNode: function() {
            let loginUserId = localStorage.getItem(CONST.USER_ID);
            let targetUserId = this.$route.params.id;
            return loginUserId*1 > targetUserId*1? targetUserId+":"+loginUserId:loginUserId+":"+targetUserId;
        },
        //current conversation
        msg: function(){
            for (var i = 0; i<this.messages.length; i++) {
                if (this.messages[i].targetId == this.$route.params.id) {
                    return this.messages[i].msg;
                }
            }
        },
        targetUserRouter: function() {
            if (this.targetUser) {
                if (this.targetUser.userTypeFlag == CONST.USER_TYPE_FLAG) {
                    return { name: this.label.NAME_USER_VISITOR_PAGE, params: {id: this.$route.params.id, tab: 'book-shelf'}};
                } else if (this.targetUser.userTypeFlag == CONST.BOOKSTOP_TYPE_FLAG) {
                    return { name: this.label.NAME_BOOKSTOP_DETAIL_PAGE, params: {id: this.$route.params.id, tab: 'books'}};
                } else {
                    return null;
                }
            } 
        }
    },
    methods: {
        init() {
            var _self = this;
            this.getTargetUser();
            //check this.messages include current target user?
            if (_self.messages.filter(e => e.targetId == _self.$route.params.id).length == 0) {
                var targetUser = {
                    targetId : _self.$route.params.id,
                    msg: []
                }
                //push current user to this.messages
                _self.messages.push(targetUser);
                messagesRef.child(_self.childNode).on('child_added', function (data) {
                    _.each(_self.messages, function(item){
                        var uic = data.ref_.parent.key.split(':');
                        var userId = localStorage.getItem(CONST.USER_ID);
                        if ((item.targetId!=userId&&(uic[0] == item.targetId || uic[1] == item.targetId))
                            ||(item.targetId==userId&&uic[0]==userId&&uic[1]==userId) ) {
                            item.msg.push(data.val());
                        }
                    })
                });
            }
        },
        //load past message
        sendMessage() {
            if (this.text) {
                //insert into users table
                userRef.child(localStorage.getItem(CONST.USER_ID)).child(this.childNode).set(true);
                userRef.child(this.$route.params.id).child(this.childNode).set(true);
                //insert into messages table
                messagesRef.child(this.childNode).push({
                    isRead: false,
                    message: this.text,
                    userId: localStorage.getItem(CONST.USER_ID),
                    timeStamp: new Date().getTime()
                });
                //reset input to blank
                this.text = "";
            }
        },
        //get target user info
        getTargetUser() {
            var _self = this;
            _self.isLoading = true;
            _self.$getItem(CONST.VISITOR_USER).then(vu => {
                if (!vu) {
                    vu = [];
                }
                var targetUser = vu.filter(function(i) {
                    return i.userId == _self.$route.params.id;
                });
                if (targetUser.length > 0) {
                    setTimeout(()=>{
                        //update isLoading flag
                        _self.isLoading = false;
                    }, 100);
                    _self.targetUser = {
                        name: targetUser[0].name,
                        imageId: targetUser[0].imageId,
                        userTypeFlag: targetUser[0].userTypeFlag
                    }
                } else {
                    _self.getUserDetail();
                }
            });
        },
        getUserDetail() {
            var _self = this;
            var config = {
                //params: userId
                params: {
                    userId: _self.$route.params.id
                }
            };
            _self.api.callGetApi(_self.api.api_get_username_image, config)
                .then(response => {
                        //on succes, set target user to result info
                        _self.targetUser = {
                            name: response.data.data.resultInfo.name,
                            imageId: response.data.data.resultInfo.imageId,
                            userTypeFlag: response.data.data.resultInfo.userTypeFlag
                        }
                        setTimeout(()=>{
                            //update isLoading flag
                            _self.isLoading = false;
                        }, 100);
                        //update visitor list in localforage
                        _self.$getItem(CONST.VISITOR_USER).then(visitorUser => 
                            {
                                if (!visitorUser) {
                                    visitorUser = [];
                                }
                                var newVisitorUser = visitorUser.filter(function(i) {
                                    return i.userId != response.data.data.resultInfo.userId;
                                });
                                newVisitorUser.push(response.data.data.resultInfo);
                                _self.$setItem(CONST.VISITOR_USER, newVisitorUser);
                            }
                        );
                    })
                .catch(_self.handleErrorFunction);
        },
        getDate (offset) {
            return new Date(offset).getDate();
        }
    },
    data() {
        return {
            messages: [],
            //GaT default Image
            SYSTEM_IMAGE: CONST.SYSTEM_IMAGE,
            //v-model input message
            text: "",
            //target user in conversation
            targetUser: {
                imageId: "",
                name: "",
                userTypeFlag: null
            },
            isLoading: true,
            //style of user icon
            userIconStyle: {
                'background-color': 'white',
                'border-radius': '28px',
                'width': '48px',
                'height': '48px'
            },
        };
    }
}
</script>

<style>

</style>



