<template>
    <div class='top-review-item-container' v-if="review.editionImageId" @click='goToBookDetail()'>
        <figure>
            <div class='top-review-item-book'>
                <div class="box-block box-image">
                    <img class="image-fill img-book" :src='imgBookUrl'/>
                </div>
                <div class='box-block top-review-item-book-info'>
                    <p class='biger-text-form-m text-limit'>{{review.editionTitle}}</p>
                    <span class='top-review-item-start-rate'>
                        <star-rating :rating='review.value' :read-only='true' :increment='0.01' :show-rating='false' inactive-color='white' border-color='#f4c312' :border-width='1' :star-size='15'>
                        </star-rating>
                    </span>
                    <span class='small-text-form-r text-color-author display-left'>{{review.value}}</span>
                </div>
                <div style='clear: both;'></div>
            </div>
            <div class='top-review-item-user'>
                <div class="box-block">
                    <div class='top-review-item-user-avatar'>
                        <user-icon :userIconStyle='userIconStyle' :imageId='review.userImageId'></user-icon>
                    </div>
                    <div class='top-review-item-user-review'>
                        <p class='biger-text-form-m text-limit'>{{review.userName}}</p>
                        <!-- <p class='reset-margin small-text-form-r text-color-author text-limit'>{{label.sharingBook}}</p> -->
                    </div>
                </div>
                <p  class='medium-text-form-r text-color-author'>{{getContentLimitLength(review.review, maxLengthOfReview)}}</p>
                <div style='clear: both;'></div>
            </div>
            <div style='clear: both;'></div>
        </figure>
    </div>
</template>

<script>
import UserIcon from "../../../common/userIcon/UserIcon";
import StarRating from "vue-star-rating";
import CommonService from "../../../../services/CommonService.js";
import Label from "../../../../common/Label.js";

export default {
  name: "top-review-item",
  components: { StarRating, UserIcon },
  mixins: [CommonService, Label],
  data() {
    return {
      userIconStyle: {
        "background-color": "white",
        "border-radius": "18px",
        width: "50px",
        height: "50px",
        float: "left"
      },
      isShowLimitReview: true,
      maxLengthOfReview: 200,
    };
  },
  methods: {
      goToBookDetail(){
        this.$router.push({ name: this.label.NAME_BOOK_DETAIL_PAGE, params: { id: this.review.editionId }});
      }
  },
  props: {
    review: {
      required: true
    },
    imgBookUrl: {
      required: true
    }
  }
};
</script>

<style scoped>
@import "../../../../scss/text_styles.scss";
@import "../../../../scss/common_styles.scss";
@import "TopReviewItem.scss";
</style>


