<template>
    <div class="main-content bookstop-info-form">
        <message-dialog
            :dialogName="'send-message-'+bookstopInfo.userId"
            :isOpen="isMessageDialogOpen"
            :user="bookstopInfo"
            @dialogClosed="isMessageDialogOpen = false"></message-dialog>
        <div v-if="imageLoad" class="bookstop-background-image">
            <img :src="getUrlImage(bookstopInfo.imageId)" class="img-avatar">
        </div>
        <div v-if="imageLoad" class="bookstop-avatar">
            <img :src="getUrlImage(bookstopInfo.imageId)" class="img-avatar">
        </div>
        <div class="bookstop-info">
            <div class="super-biger-text-form-m text-limit" style="color:#4196c2;">{{bookstopInfo.name}}</div>
            <div class="text-limit" style="margin-top:6px;">{{address}}</div>
        </div>
        <div class="bookstop-contact">
            <a v-if="bookstopInfo.fbLink" target="_blank" :href="bookstopInfo.fbLink">
                <button class="btn btn-gat btn-circle" style="width:150px;height:40px;">
                    <img src="../../../assets/images/facebook.png" style="width:auto;height:20px;">
                    <span style="margin-left:20px;">{{label.facebook}}</span>
                </button>
            </a>
            <button v-on:click="openMessageDialog" class="btn btn-gat btn-circle" style="margin-left:10px;width:150px;height:40px;">
                <img src="../../../assets/images/message.png" style="width:20px;height:20px;">
                <span style="margin-left:20px;">{{label.sendMessage}}</span>
            </button>
        </div>
    </div>
</template>

<script>
import MessageDialog from '../../common/dialog/MessageDialog.vue'
import CommonService from '../../../services/CommonService.js'
import Label from '../../../common/Label.js'
import * as CONST from '../../../common/const.js'
export default {
    props: ["bookstopInfo"],
    mixins: [ CommonService, Label ],
    components: { MessageDialog },
    computed: {
        address() {
            return this.bookstopInfo.usuallyLocation? this.bookstopInfo.usuallyLocation[0].address:'';
        }
    },
    watch: {
        bookstopInfo: function() {
            if (this.bookstopInfo) {
                this.imageLoad = true;
            }
        }
    },
    methods: {
        openMessageDialog() {
            this.isMessageDialogOpen = true;
        }
    },
    data() {
        return {
            isMessageDialogOpen: false,
            imageLoad: false
        }
    }
}
</script>

<style>

</style>



