<template>
    <div class="forgot-form box-shadow">
        <loading-dialog :loadingDialogName="'input-new-pass-loading'" :isLoadingOpen="isSystemExecuting"></loading-dialog>
        <div class="forgot-title biger-text-form-m">
            {{label.forgot_pass_1}}&nbsp( 3 )
        </div>
        <div class="align-center" style="margin-top:30px;">
            {{label.forgot_notice_3}}
        </div>
        <div style="margin-top:30px;">
            <input v-model="new_password" type="password" class="form-control gat-form" :placeholder="label.new_password">
        </div>
        <div style="margin-top:34px;">
            <button v-on:click="resetPass" type="button" class="btn btn-gat btn-circle btn-forgot">
                {{label.send}}
            </button>
        </div>
    </div>
</template>

<script>
import Label from '../../../../common/Label.js'
import LoadingDialog from '../../../common/dialog/LoadingDialog.vue'
import BookService from '../../../../services/BookService.js'
import * as CONST from '../../../../common/const.js'
export default {
    props: [ "tokenVerify" ],
    mixins: [ Label, BookService ],
    components: { LoadingDialog },
    methods: {
        resetPass() {
            this.isSystemExecuting = true;
            var data = {
                newPassword: this.new_password,
                tokenVerify: this.tokenVerify,
                uuid: window.uuid
            };
            this.api.callPostApi(this.api.api_reset_password, data, {})
                .then(this.initData)
                .catch(this.handleErrorFunction);
        },
        initData(response) {
            if (response.data.data.loginToken) {
                localStorage.setItem(CONST.LOGIN_TOKEN, response.data.data.loginToken);
            }
            if (response.data.data.firebasePassword) {
                localStorage.setItem(CONST.FIREBASE_PASS, response.data.data.firebasePassword);
            }
            var config = {
                headers: {
                    'Authorization': response.data.data.loginToken
                }
            };
            this.api.callGetApi(this.api.api_get_user_private_info, config)
                .then(response => {
                        localStorage.setItem(CONST.USER_ID, response.data.data.resultInfo.userId);
                        if(response.data.data.resultInfo.usuallyLocation.length==0){
                            response.data.data.resultInfo.usuallyLocation[0] = {};
                        }
                        this.$setItem(CONST.LOGIN_USER, response.data.data.resultInfo);
                        this.$router.push({ name: CONST.NAME_USER_VISITOR_PAGE, params: {id: localStorage.getItem(CONST.USER_ID), tab: 'book-shelf'}});
                        this.isSystemExecuting = false;
                    })
                .catch(this.handleErrorFunction);
        }
    },
    data() {
        return {
            new_password: "",
        }
    }
}
</script>

<style>

</style>



