<template>
    <div class="row container-gat">
        <error-dialog :errorDialogName="'home-error-dialog'"
            :isErrorOpen="isApiserviceErrorDialogOpen" 
            :errorMessage="apiErrorDialogContent" 
            :errorStatus="errorStatus"
            @errorDialogClosed="isApiserviceErrorDialogOpen = false"></error-dialog>
        <div class="home-content-middle inline-block">
            <div class="home-box-best-book-on-day" v-if="isShowListSharingBook">
                <list-sharing-book @showErrorDialog="showErrorDialog" @showListSharingBook="showListSharingBook"></list-sharing-book>
            </div>
            
            <div class="home-box-top-review" v-if="isShowTopReview">
                <top-review @showErrorDialog="showErrorDialog" @showTopReview="showTopReview"></top-review>
            </div>

            <div class="home-box-share-book-near">
                <share-book-near @showErrorDialog="showErrorDialog" ></share-book-near>
            </div>
            
            <div class="home-box-list-bookstop" v-if="isShowListBookstop">
                <list-book-stop @showErrorDialog="showErrorDialog" @showListBookstops="showListBookstops"></list-book-stop>
            </div>

            <list-events v-if="false" @showErrorDialog="showErrorDialog"></list-events>
        </div>
        <div class="home-content-right inline-block">
            <list-users @showErrorDialog="showErrorDialog"></list-users>
            <div class="box-padding-top ">
                <app-gat :isScroll="false"></app-gat>
            </div>
        </div>
    </div>
</template>

<script>
import ListUsers from '../components/ListUsers/ListUsers.vue';
import ListSharingBook from '../components/listSharingBook/ListSharingBook.vue';
import ListBookStop from '../components/listBookStop/ListBookStop.vue';
import ShareBookNear from '../components/ShareBookNear/ShareBookNear.vue';
import ListEvents from '../components/listEvents/ListEvents.vue';
import AppGat from '../../common/appGat/AppGat.vue';
import BookService from '../../../services/BookService.js';
import TopReview from "../components/topReview/TopReview.vue";

export default {
    name: 'home',
    components: { ListUsers, ListSharingBook, ListBookStop, ShareBookNear, ListEvents, AppGat, TopReview },
    mixins: [BookService],
    data() {
        return {
            isShowListSharingBook: true,
            isShowTopReview: true,
            isShowListBookstop: true
        }
    },

    methods: {
        showErrorDialog(error){
            if (error.response.data.message) {
                this.apiErrorDialogContent = error.response.data.message;
            } else{
                this.apiErrorDialogContent = error;
            }
            this.isApiserviceErrorDialogOpen = true;
        },
        showListSharingBook(isShow){
            this.isShowListSharingBook = isShow;
        },
        showTopReview(isShow){
            this.isShowTopReview = isShow;
        },
        showListBookstops(isShow){
            this.isShowListBookstop = isShow;
        }
    }
}
</script>

<style scoped>
@import 'home.scss';
</style>



