<template>
    <div v-on:mouseenter="mouseIn" v-on:mouseleave="mouseOut" class="box-book-grid">
        <!-- loading dialog -->
        <loading-dialog :loadingDialogName="'delete-instance-loading'+book.instanceId" :isLoadingOpen="isSystemExecuting"></loading-dialog>
        <confirm-dialog
            :dialogName="'delete-instance-'+book.instanceId"
            :isOpen="isDialogOpen"
            :content="confirmDialogContent"
            :execute="deleteInstance"
            @dialogClosed="isDialogOpen = false"></confirm-dialog>
        <figure>
        <transition name="fade">
            <div v-if="showButton&&book.sharingStatus!=borrowingStt" class="top-right-grid">
                <div v-on:click="openDeleteConfirm" type="button" class="delete-button">
                    <span class="glyphicon glyphicon-trash"></span>
                </div>
            </div>
        </transition>
        <div class="box-image">
            <a v-if="label.PATH_BOOK_DETAIL_PAGE" :href="label.PATH_BOOK_DETAIL_PAGE.replace(':id',book.editionId)">
                <img :title="book.title" :src="getUrlImage(book.imageId)" class="image-fill img-book" :alt="book.title">
            </a>
        </div>
        <div class="title-info">
            <a v-if="label.PATH_BOOK_DETAIL_PAGE" :title="book.title" :href="label.PATH_BOOK_DETAIL_PAGE.replace(':id',book.editionId)">
                <span class="medium-text-form-m text-limit text-muted title-name-color">{{book.title}}</span>
            </a>
        </div>
        <div class="author-info">
            <span v-if="book.author" class="small-text-form-r text-limit text-color-author">{{book.author}}</span>
            <span v-if="!book.author" class="small-text-form-r text-limit text-color-author">&nbsp</span>
        </div>
        <div v-if="book.sharingStatus==borrowingStt" class="sharing-info">
            <div class="display-left">
                <router-link :title="book.borrowingUserName" :to="{ name: label.NAME_USER_VISITOR_PAGE, params: {id: book.borrowingUserId, tab: 'book-shelf'}}">
                    <div class="user-border">
                        <user-icon :userIconStyle="userIconStyle" :imageId="book.borrowingUserImage"></user-icon>
                    </div>
                </router-link>
            </div>
            <div class="display-right">
                <router-link :to="{ name: label.NAME_USER_REQUEST_DETAIL_PAGE, params: {id: book.borrowingRecordId}}">
                    <span class="small-text-form-r center-vertical-text">{{label.borrowing}}</span>
                </router-link>
            </div>
        </div>
        <div v-if="book.sharingStatus==sharingStt || book.sharingStatus==notSharingStt" class="sharing-info">
            <div class="display-left">
                <span class="small-text-form-r center-vertical-text">{{label.share}}</span>
            </div>
            <div class="display-right center-vertical-text">
                <toggle-button 
                    :id="book.instanceId"
                    :value="book.sharingStatus===sharingStt" 
                    :color="'#7cc576'" :sync="false" :labels="false"
                    @change="changeSharingStt"></toggle-button>
            </div>
        </div>
        <div v-if="book.sharingStatus==lostStt" class="sharing-info">
            <div class="display-left">
                <router-link v-if="targetUserRouter" :title="book.borrowingUserName" :to="targetUserRouter">
                    <div class="user-border">
                        <user-icon :userIconStyle="userIconStyle" :imageId="book.borrowingUserImage"></user-icon>
                    </div>
                </router-link>
            </div>
            <div class="display-right">
                <router-link :to="{ name: label.NAME_USER_REQUEST_DETAIL_PAGE, params: {id: book.borrowingRecordId}}">
                    <span class="small-text-form-r center-vertical-text text-danger">{{label.lost}}</span>
                </router-link>
            </div>
        </div>
        </figure>
    </div>
</template>


<script>
import ConfirmDialog from '../../../common/dialog/ConfirmDialog.vue'
import LoadingDialog from '../../../common/dialog/LoadingDialog.vue'
import CommonService from '../../../../services/CommonService.js'
import BookService from '../../../../services/BookService.js'
import UserIcon from '../../../common/userIcon/UserIcon.vue'
import * as CONST from '../../../../common/const.js'
import Label from '../../../../common/Label.js'

export default {
    name: 'book-in-book-shelf',
    components: { UserIcon, ConfirmDialog, LoadingDialog },
    mixins: [ Label, CommonService, BookService],
    mounted() {
        this.isDialogOpen = false;
    },
    computed: {
        targetUserRouter: function() {
            if (this.book.borrowingUserTypeFlag) {
                if (this.book.borrowingUserTypeFlag == CONST.USER_TYPE_FLAG) {
                    return { name: this.label.NAME_USER_VISITOR_PAGE, params: {id: this.book.borrowingUserId, tab: 'book-shelf'}};
                } else if (this.book.borrowingUserTypeFlag == CONST.BOOKSTOP_TYPE_FLAG) {
                    return { name: this.label.NAME_BOOKSTOP_DETAIL_PAGE, params: {id: this.book.borrowingUserId, tab: 'books'}};
                } else {
                    return null;
                }
            } 
        }
    },
    methods: {
        mouseIn() {
            this.showButton = true;
        },
        mouseOut() {
            this.showButton = false;
        },
        openDeleteConfirm() {
            this.isDialogOpen = true;
            this.confirmDialogContent = this.label.deleteInstanceConfirm(this.book.title);
        },
        deleteInstance(){
            this.isSystemExecuting = true;
            var params = {
                instanceId: this.book.instanceId
            };
            var headers = {
                'Authorization': this.loginToken
            }
            this.api.callPostApi(this.api.api_selfremove_instance, params, {headers})
                .then(this.updateStatusSuccess, this.handleErrorFunction);
        },
        changeSharingStt(target) {
            this.isSystemExecuting = true;
            var newStt = target.value? CONST.STT_SHARING:CONST.STT_NOT_SHARING;
            var params = {
                instanceId: this.book.instanceId,
                sharingStatus: newStt
            };
            var headers = {
                'Authorization': this.loginToken
            }
            this.api.callPostApi(this.api.api_selfchange_instance_stt, params, {headers})
                .then(this.updateStatusSuccess, this.handleErrorFunction);
        },
        updateStatusSuccess(response) {
            this.isSystemExecuting = false;
            this.refresh();
        }
    },
    data() {
        return {
            isDialogOpen: false,
            confirmDialogContent: "",
            showButton: false,
            notSharingStt: CONST.STT_NOT_SHARING,
            sharingStt: CONST.STT_SHARING,
            borrowingStt: CONST.STT_BORROWING,
            lostStt: CONST.STT_LOST,
            userIconStyle: {
                'background-color': 'white',
                'border-radius': '16px',
                'width': '32px',
                'height': '32px',
                'margin': '0 auto'
            }
        }
    },
    props: {
        book: {
            required: true
        },
        refresh: {
            required: true
        }
    }
}

</script>

<style>

</style>



