<template>
    <div class="forgot-form box-shadow">
        <loading-dialog :loadingDialogName="'input-verify-loading'" :isLoadingOpen="isSystemExecuting"></loading-dialog>
        <div class="forgot-title biger-text-form-m">
            {{label.forgot_pass_1}}&nbsp( 2 )
        </div>
        <div class="align-center" style="margin-top:30px;">
            {{label.forgot_notice_2}}
        </div>
        <div style="margin-top:30px;">
            <input v-model="verify_token" type="text" class="form-control gat-form" :placeholder="label.verify_code">
        </div>
        <div style="margin-top:34px;">
            <button v-on:click="checkVerify" type="button" class="btn btn-gat btn-circle btn-forgot">
                {{label.send}}
            </button>
        </div>
    </div>
</template>

<script>
import Label from '../../../../common/Label.js'
import LoadingDialog from '../../../common/dialog/LoadingDialog.vue'
import BookService from '../../../../services/BookService.js'
import * as CONST from '../../../../common/const.js'
export default {
    props: [ "tokenReset" ],
    mixins: [ Label, BookService ],
    components: { LoadingDialog },
    methods: {
        checkVerify() {
            this.isSystemExecuting = true;
            var data = {
                code: this.verify_token,
                tokenResetPassword: this.tokenReset
            };
            this.api.callPostApi(this.api.api_verify_reset_token, data, {})
                .then(response => {
                    this.$emit("setTokenVerify", response.data.data.tokenVerify);
                    this.$router.push({ name: CONST.NAME_FORGOT_PASS_PAGE, params: {tab: 'input-new-pass'}});
                    this.isSystemExecuting = false;
                })
                .catch(this.handleErrorFunction);
        }
    },
    data() {
        return {
            verify_token: "",
        }
    }
}
</script>

<style>

</style>



