<template>
    <div class="user-profile-detail-form box-shadow-bold">
        <error-dialog
            :errorDialogName="'get-sharing-false'"
            :isErrorOpen="isApiserviceErrorDialogOpen"
            :errorMessage="apiErrorDialogContent"
            :errorStatus="errorStatus"
            @errorDialogClosed="isApiserviceErrorDialogOpen = false"></error-dialog>
        <div class="right-layout-full">
            <div style="margin-top:10px; margin-left:10px;" v-html="sharingText"></div>
            <div v-if="isShowLoading" style="margin:10px;">
                <img src="../../../../assets/images/loading.gif" class="loading-img-medium">
            </div>
            <div v-if="!isShowLoading&&sharingTotal > 0" class="center-block" style="margin-top:30px;">
                <div class="row" v-for="(splitedBook, i) in sharingBooks" :key="i">
                    <div class="col-xs-3 nopadding" v-for="(book, j) in splitedBook" :key="j">
                        <book-in-visitor-books :book="book" :userName="userName"></book-in-visitor-books>
                    </div>
                </div>
                <div v-if="sharingTotal <= per_page" style="height:15px;"></div>
                <div v-if="sharingTotal > per_page" class="text-center">
                    <uib-pagination
                        :boundary-links="true"
                        :force-ellipses="false"
                        :boundary-link-numbers="true"
                        previous-text="<" 
                        next-text=">" 
                        first-text="<<"
                        last-text=">>"
                        v-model="sharingPagination" 
                        :items-per-page="per_page" 
                        :total-items="sharingTotal" 
                        :max-size="4" 
                        :rotate="true">
                    </uib-pagination>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import BookService from '../../../../services/BookService.js';
import * as CONST from '../../../../common/const.js';
import Label from '../../../../common/Label.js';
import BookInVisitorBooks from './BookInVisitorBooks.vue';
export default {
    mixins: [BookService, Label],
    components: {BookInVisitorBooks},
    props: ["userName"],
    mounted() {
        this.refresh();
    },
    watch: {
        '$route': function(){
            this.refresh();
        },
        sharingPagination: function(){
            var page = this.sharingPagination.currentPage;
            var _self = this;
            this.$router.push({ name: _self.$route.name, params: {tab: _self.$route.params.tab}, query: {page: page}});
        }
    },
    computed: {
        sharingText() {
            if (this.userName) {
                if (this.sharingTotal!=null) {
                    return this.sharingTotal == 0?
                    this.label.visitorSharingBookNA(this.userName):this.label.visitorSharingBook(this.userName, this.sharingTotal);
                }
            }
        }
    },
    methods: {
        refresh(){
            var _self = this;
            if(this.$route.query.page==null){
                this.$router.replace({ name: _self.$route.name, params: {tab: _self.$route.params.tab}, query: {page: '1'}});
            }
            this.sharingPagination = {currentPage: Number(this.$route.query.page)};
            this.getSharingTotal();
            this.getSharingBooks();
        },
        getSharingTotal(){
            var _self = this;
            var lcs = CONST.VISITOR_BOOK_SHELF_TOTAL + "_user_" + _self.$route.params.id;
            _self.$getItem(lcs).then(cacheData => {
                if (cacheData) {
                    _self.sharingTotal = cacheData.sharingTotal;
                }
            });
            var config = {
                params: {
                    ownerId: _self.$route.params.id
                }
            };
            _self.api.callGetApi(_self.api.api_get_user_sharing_editions_total, config)
                .then(response => {
                    //set sharing total when page = 1
                    _self.sharingTotal = response.data.data.sharingTotal;
                    _self.$setItem(lcs, response.data.data);
                })
                .catch(_self.handleErrorFunction);
        },
        getSharingBooks(){
            var _self = this;
            _self.sharingBooks = [];
            var lcs = CONST.VISITOR_BOOK_SHELF + "_user_" + _self.$route.params.id + "_page_" + _self.sharingPagination.currentPage;

            _self.$getItem(lcs).then(cacheData => {
                if (!cacheData) {
                     _self.isShowLoading = true;
                }
                _self.sharingBooks = _.chunk(cacheData, 4);
            })

            var config = {
                params: {
                    ownerId: _self.$route.params.id,
                    userId: _self.loginUserId,
                    page: _self.sharingPagination.currentPage,
                    per_page: _self.per_page
                }
            };
            _self.api.callGetApi(_self.api.api_get_user_sharing_editions, config)
                .then(response => {
                    _self.sharingBooks = _.chunk(response.data.data.resultInfo, 4);
                    _self.isShowLoading = false;
                    _self.$setItem(lcs, response.data.data.resultInfo);
                })
                .catch(_self.handleErrorFunction);
        }
    },
    data() {
        return {
            per_page: CONST.USER_BOOK_ITEM_PERPAGE,

            sharingPagination: {},
            sharingTotal: null,
            sharingBooks: [],
        }
    }
}
</script>

<style>

</style>



