<template>
    <div>
        <div class="row container-gat box-margin-top search-book-container">
            <div class="search-book-content-left inline-block">
                <app-gat></app-gat>
            </div>
            <div class="search-book-content-right inline-block">
                <div>
                    <a href="\" class="text-color-category medium-text-form-m">{{label.home_page}}</a>
                    <span class="text-color-category medium-text-form-m">
                        <i class="fa fa-angle-double-right fa-lg" aria-hidden="true"></i>
                    </span>
                    <span class="text-color-highlight medium-text-form-m">{{label.txt_result}}</span>
                    <div style="clear: both;"></div>
                </div>
                <div v-if="isShowLoading">
                    <img src="../../../../assets/images/loading.gif" style="width:50px; height: 50px;">
                </div>
                <div class="search-book-content-right-margin-top" v-else>
                    <span class="text-color-highlight super-biger-text-form-m text-style-bold">{{total}}</span>
                    <span class="medium-text-form-m"> {{label.txt_result_for}} </span>
                    <span class="text-color-highlight super-biger-text-form-m text-style-bold">'{{search_text}}'</span>
                </div>
                <div class="search-book-content-right-container" v-if="false == isShowLoading">
                    <div class="search-book-content-right-content ">
                        <div v-for="(book, index) in books" :key="index">
                            <book-info :editionId="book.editionId" :title="book.title" :imageId="book.imageId" :author="book.author" :rateAvg="book.rateAvg" :numberRate="book.number_rate"></book-info>
                        </div>
                        <div style="clear: both;"></div>
                    </div>
                    <div class="text-center">
                        <uib-pagination :boundary-links="true" :force-ellipses="false" :boundary-link-numbers="true" previous-text="<" next-text=">" first-text="<<" last-text=">>" v-model="pagination" :items-per-page="config.params.per_page" :total-items="total" :max-size="4" :rotate="true" @change="getBooks">
                        </uib-pagination>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
import AppGat from '../../../common/appGat/AppGat.vue';
import BookInfo from '../../../common/bookInfo/BookInfo.vue';
import BookService from '../../../../services/BookService.js';
import CommonService from '../../../../services/CommonService.js';
import Label from '../../../../common/Label.js';

export default {
    name: 'search-book',
    components: { AppGat, BookInfo },
    mixins: [CommonService, BookService, Label],
    data() {
        return {
            books: [],
            search_text: '',
            search_by: '',
            config: {
                params: {
                    per_page: 15,
                    page: 1
                }
            },
            total: 0,
            pagination: { currentPage: 1 },
        }
    },
    computed: {
    },
    mounted() {
        this.initData();
    },
    methods: {
        initData() {
            this.isShowLoading = true;
            this.search_text = this.$route.query.str;
            this.resetData();
            this.getTotal();
            this.callAPISearch();
        },
        resetData() {
            this.config = {
                params: {
                    per_page: 15,
                    page: 1
                }
            };
            this.total = 0;
        },
        setListBooks(response) {
            if (200 == response.status) {
                var responseData = response.data;
                this.books = responseData.data.resultInfo;
            }
        },
        setTotal(response) {
            if (200 == response.status) {
                var responseData = response.data;
                this.total = responseData.data.totalResult;
                this.isShowLoading = false;
            }
        },
        getTotal() {
            if (0 == this.searchBy) {
                // Get total
                var data = {
                    title: this.search_text
                };
                this.api.callPostApi(this.api.api_search_title_total, data, {}).then(this.setTotal, this.handleErrorFunction);
            } else {
                // Get total
                var data = {
                    authorName: this.search_text
                };
                this.api.callPostApi(this.api.api_search_author_total, data, {}).then(this.setTotal, this.handleErrorFunction);
            }
        },
        callAPISearch() {
            switch (this.searchBy) {
                case 0:
                    //Call api to search by title
                    this.config.params.title = this.search_text;
                    this.api.callGetApi(this.api.api_search_title, this.config).then(this.setListBooks, this.handleErrorFunction);
                    break;
                case 1:
                    //Call api to search author
                    this.config.params.authorName = this.search_text;
                    this.api.callGetApi(this.api.api_search_author, this.config).then(this.setListBooks, this.handleErrorFunction);
                    break;
                default:
                    //Call api to search something
                    this.config.params.title = this.search_text;
                    this.api.callGetApi(this.api.ap_search_title, this.config).then(this.setListBooks, this.handleErrorFunction);
                    break;
            }
        },
        getBooks() {
            this.config.params.page = this.pagination.currentPage;
            this.callAPISearch();
        }
    },
    watch: {
        '$route': function() {
            this.initData();
        }
    },
    props: {
        searchBy: {
            required: true,
            default: 0
        },
    }
}
</script>
,
<style lang="scss">
@import '../../../../scss/common_styles.scss';
@import 'searchBook.scss';
</style>



