<template>
    <div class="request-nav-form">
        <div class="block-title biger-text-form-m">
            {{label.title}}
        </div>
        <div v-for="(item, outerIndex) in label.from_to" :key="outerIndex" class="request-tab">
            <div class="row">
                <div class="col-xs-9 medium-text-form-m">
                    {{item}}
                </div>
            </div>
            <div 
                v-for="(row, innerIndex) in label.requestType"
                :key="innerIndex"
                v-on:click="selectType($event, outerIndex, innerIndex)"
                class="row request-type"
                v-bind:class="{'active': ($route.query.target==outerIndex&&$route.query.type==innerIndex) || (requestDetail.target==outerIndex&&requestDetail.type==innerIndex)}">
                <div class="col-xs-9">
                    {{row}}
                </div>
                <div class="col-xs-3" style="text-align:right;">
                    {{getNumber(outerIndex, innerIndex)}}
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Label from '../../../../common/Label.js';
export default {
    props: ["requestSummary", "requestDetail"],
    mixins: [Label],
    methods: {
        //event click filter in request navigation
        selectType(event, outerIndex, innerIndex){
            $('.request-type').removeClass('active');
            $(event.target).parent().addClass('active');
            //set parameter for calling api in request list component
            this.$router.push({ name: this.label.NAME_USER_REQUEST_PAGE, query: {target: outerIndex, type: innerIndex}});
        },
        //set the number of each request type
        getNumber(outerIndex, innerIndex) {
            var x=['toYou', 'fromYou'];
            var y=['waitToConfirm', 'contacting', 'borrowing', 'others'];
            return this.requestSummary[x[outerIndex]][y[innerIndex]];
        }
    },
    data() {
        return {
        }
    }
}
</script>

<style>

</style>

    


