<template>
	 <div class="box-full box-padding-top">
        <div class="row-header">
            <span class="biger-text-form-m text-color-default text-uppercase">{{label.txt_header_top_review}}</span>
            <span class="medium-text-form-r text-color-highlight display-right">
                <a :href="label.NAME_SHARE_BOOKS_PAGE" class="mouse-pointer">{{label.view_all}}
                    <i class="fa fa-angle-right fa-lg" aria-hidden="true"></i>
                </a>
            </span>
        </div>
        <hr class="line-break reset-padding reset-margin">
        <div class="box-list-sharing-book-on-day">
            <!-- swiper -->
            <swiper :options="swiperOption" v-if="listReview && listReview.length > 0">
                <swiper-slide v-for="review in listReview" :key="review.evaluationId"><top-review-item :review="review" :imgBookUrl="getUrlImage(review.editionImageId)"></top-review-item></swiper-slide>
                <div class="swiper-pagination" slot="pagination"></div>
            </swiper>
        </div>
    </div> 
</template>

<script>
import Label from "../../../../common/Label.js";
import BookService from "../../../../services/BookService.js";
import CommonService from "../../../../services/CommonService.js";
import TopReviewItem from "../topReviewItem/TopReviewItem.vue";
import { swiper, swiperSlide } from "vue-awesome-swiper";

export default {
  name: "top-review",
  mixins: [Label, BookService, CommonService ],
  components: { swiper, swiperSlide, TopReviewItem },
  data() {
    return {
      swiperOption: {
        slidesPerView: 3,
        spaceBetween: 30,
        // centeredSlides: true,
        loop: true,
        loopFillGroupWithBlank: true,
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        },
        autoplay: {
          delay: 5000,
          disableOnInteraction: false
        }
      },
      listReview: [],
      config: {
        params: {
          per_page: 10,
          page: 1
        }
      }
    };
  },
  mounted() {
    this.getListReview();
  },
  methods: {
    getListReview(){
      this.api.callGetApi(this.api.api_get_review_list, this.config).then(this.setListReview, this.errorCallApiGetListReview);
    },
    setListReview(response){
      if (200 == response.status && response.data.data.resultInfo && response.data.data.resultInfo.length > 0) {
        this.listReview = response.data.data.resultInfo;
      } else {
          this.$emit("showTopReview", false);
      }
    },
    errorCallApiGetListReview(error) {
        this.showErrorDialog(error);
    },
  }
};
</script>

<style>
@import "../../../../scss/text_styles.scss";
@import "TopReview.scss";
</style>


