            if ((event.target.id!="send-message"+this.item.key && event.target.id!="mark-as-read"+this.item.key && this.item.targetId)) {
                this.$router.push({ name: CONST.NAME_USER_REQUEST_DETAIL_PAGE, params: {id: this.item.referId}});
