<template>
    <div class="box-book-info" @click="goToBookDetail">
        <figure>
            <div class="box-block box-image ">
                <img :src="getUrlImage(imageId)" class="image-fill img-book" :alt="title">
            </div>
            <div class="box-block book-info-box-title book-info-margin">
                <span class="medium-text-form-m text-limit book-info-title">{{title}}</span>
            </div>
            <div class="box-block book-info-box-title book-info-margin">
                <p class="small-text-form-r text-limit text-color-author book-info-author">{{getAuthor(author)}}</p>
            </div>
            <div class="box-block book-info-box-title book-info-margin">
                <span class="book-info-start-rate">
                    <star-rating :rating="rateAvg" :read-only="true" :increment="0.01" :show-rating="false" inactive-color="white" border-color="#f4c312" :border-width="1" :star-size="15">
                    </star-rating>
                </span>
                <span class="small-text-form-r text-color-author display-left">{{rateAvg}}</span>
            </div>
        </figure>
    </div>
</template>


<script>
import StarRating from 'vue-star-rating';
import CommonService from '../../../services/CommonService.js';
import Label from '../../../common/Label.js';

export default {
    name: 'book-info',
    components: { StarRating },
    mixins: [CommonService, Label],
    mounted() {
    },
    methods: {
        goToBookDetail() {
            var nameOfPage = this.$route.name;
            if (this.label.NAME_SEARCH_BOOK_PAGE == nameOfPage || this.label.NAME_SEARCH_AUTHOR_PAGE == nameOfPage) {
                window.isSearching = 1;
            }else{
                window.isSearching = 0;
            }

            if (this.label.NAME_SHARE_BOOKS_PAGE == nameOfPage) {
                window.isShareBook = 1;
            }else{
                window.isShareBook = 0;
            }
            this.$router.push({ name: this.label.NAME_BOOK_DETAIL_PAGE, params: { id: this.editionId }});
        }
    },
    props: {
        editionId: {
            required: true
        },
        imageId: {
            required: true
        },
        title: {
            required: true
        },

        author: {
            required: true
        },

        rateAvg: {
            required: true
        },

        numberRate: {
            required: true
        }

    }
}

</script>

<style lang="scss">
@import 'bookInfo.scss';
</style>



