<template>
    <div class="small-form-cover">
        <div class="forgot-free-row-top"></div>
        <div class="forgot-form-wrapper center-block">
            <input-email v-if="$route.params.tab=='input-email'" @setTokenReset="tokenReset=$event"></input-email>
            <input-verify v-if="$route.params.tab=='input-verify'" :tokenReset="tokenReset" @setTokenVerify="tokenVerify=$event"></input-verify>
            <input-new-pass v-if="$route.params.tab=='input-new-pass'" :tokenVerify="tokenVerify"></input-new-pass>
        </div>
        <div class="forgot-free-row-bottom">
            <router-link :to="{name:'home'}" >
                <span class="fa fa-home fa-2x" style="margin-right:10px"></span>
                {{label.back_to_homepage}}
            </router-link>
        </div>
    </div>
</template>

<script>
import Label from '../../../../common/Label.js'
import InputEmail from '../components/InputEmail.vue'
import InputVerify from '../components/InputVerify.vue'
import InputNewPass from '../components/InputNewPass.vue'
export default {
    components: { InputEmail, InputVerify, InputNewPass },
    mixins: [ Label ],
    data() {
        return {
            tokenReset: "",
            tokenVerify: ""
        }
    }

}
</script>

<style>
@import 'forgotPass.scss';
</style>



