<template>
    <div>
        <div class="header-book-search-history">
            <div class="header-book-search-history-left">
                <img class="image-fill" src="../../../assets/images/icon_search_title.png" style="height: 15px; width: 15px;" v-if="0 == isType">
                <img class="image-fill" src="../../../assets/images/icon_search_author.png" style="height: 20px; width: 20px;" v-if="1 == isType">
                <img class="image-fill" src="../../../assets/images/icon_search_user.png" style="height: 20px; width: 20px;" v-if="2 == isType">
            </div>
            <div class="header-book-search-history-right">
                <p class="medium-text-form-m text-color-author">{{searchHistoryItem}}</p>
            </div>
            <div style="clear: both;"></div>
        </div>
        <hr class="line-break-user reset-margin" v-if="isShowLineBreak">
        <div style="clear: both;"></div>
    </div>
</template>
<script>
import Label from '../../../common/Label.js'
export default {
    name: "header-search-history-item",
    mixins: [Label],
    props: {
        searchHistoryItem: {
            required: true
        },
        isShowLineBreak: {
            required: false,
            default: true
        },
        isType: {
            required: false,
            default: 0
        }
    }

}
</script>

<style lang="scss">
.header-book-search-history {
    position: relative;
    cursor: pointer;
    width: 100%;
    height: 51px;
    padding-top: 8px;
    padding-bottom: 8px;
}

.header-book-search-history-left {
    float: left;
    border-radius: 50%;
    background-color: #d5d5d5;
    height: 35px;
    width: 35px;
    text-align: center;
    position: relative;
}

.header-book-search-history-left img {
    position: absolute;
    margin: auto;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}

.header-book-search-history-right {
    float: right;
    text-align: left;
    position: relative;
    width: 390px;
    padding-left: 20px;
    padding-top: 8px;
}
</style>



