<template>
    <modal :name="dialogName" :clickToClose="false" :width="480" :height="300">
        <div class="message-dialog-title biger-text-form-m">
            <button class="close" @click="reject">&times;</button>
            <div class="text-limit" style="width:430px">{{label.send_message_to}}: <span style="color:#4196c2">{{user.name}}</span></div>
        </div>

        <div style="margin: 25px;">
            <div style="margin-top: 10px;">
                <textarea style="background-color:#eff9ff" :id="'message-form-' + user.userId" class="form-control review-form" :placeholder="label.borrow_message_placeholder" rows="6" v-model="message"></textarea>
            </div>
        </div>

        <!-- <div v-html="content" class="confirm-dialog-content"></div> -->
        <div style="margin:30px 25px;">
            <button v-on:click="accept" class="btn btn-gat btn-circle display-left" style="width:100%;">{{label.sendMessage_1}}</button>
        </div>
    </modal>
</template>

<script>
import Label from '../../../common/Label.js'
import * as CONST from '../../../common/const.js'

//firebase db (tabel users) refer
let userRef = window.firebaseDB.ref('users');
//firebase db (tabel messages) refer
let messagesRef = window.firebaseDB.ref('messages');

export default {
    props: ["dialogName", "isOpen", "user"],
    mixins: [ Label ],
    computed: {
        //get message child node from login user id and target user id
        childNode: function() {
            let loginUserId = localStorage.getItem(CONST.USER_ID);
            let targetUserId = this.user.userId;
            return loginUserId*1 > targetUserId*1? targetUserId+":"+loginUserId:loginUserId+":"+targetUserId;
        }
    },
    watch: {
        isOpen: function(){
            if(this.isOpen){
                this.$modal.show(this.dialogName);
            }else{
                this.$modal.hide(this.dialogName);
            }
        }
    },
    methods: {
        accept() {
            this.sendMessage();
            this.$emit('dialogClosed');
        },
        reject() {
            this.$emit('dialogClosed');
        },
        sendMessage() {
            //insert firebase db
            if (this.message) {
                //insert into users table
                userRef.child(localStorage.getItem(CONST.USER_ID)).child(this.childNode).set(true);
                userRef.child(this.user.userId).child(this.childNode).set(true);
                //insert into messages table
                messagesRef.child(this.childNode).push({
                    isRead: false,
                    message: this.message,
                    userId: localStorage.getItem(CONST.USER_ID),
                    timeStamp: new Date().getTime()
                });
                //reset input to blank
                this.message = "";
            }
        }
    },
    data() {
        return {
            message: "",
            //style of user icon
            userIconStyle: {
                'background-color': 'white',
                'border-radius': '22px',
                'width': '44px',
                'height': '44px',
                'margin': '2px auto'
            }
        }
    }
}
</script>

<style>
.close {
    position: absolute;
    top: 13px;
    right: 15px;
}
.message-dialog-title {
    height: 50px;
    line-height: 50px;
    padding-left: 25px;
    border-bottom: 1px solid #c8c8c8;
}
</style>


