<template>
    <div class="row container-gat box-margin-top list-book-container ">
        <div class="list-books-content-left inline-block">
            <app-gat></app-gat>
        </div>
        <div class="list-books-content-right inline-block">
            <div>
                <a href="\" class="text-color-category medium-text-form-m">{{label.home_page}}</a>
                <span class="text-color-category medium-text-form-m list-books-margin-right-left">
                    <i class="fa fa-angle-double-right fa-lg" aria-hidden="true"></i>
                </span>
                <span class="text-color-highlight medium-text-form-m">{{label.txt_list_share_book}}</span>
            </div>
            <div v-if="isShowLoading">
                <img src="../../../assets/images/loading.gif" style="width:50px; height: 50px;">
            </div>
            <div class="list-books-content-right-container" v-else>
                <div class="list-books-content-right-content ">
                    <div v-for="(book, index) in books" :key="index">
                       <book-info :editionId="book.editionId" :title="book.title" :imageId="book.imageId" :author="book.author" :rateAvg="book.rateAvg" :numberRate="book.number_rate">
                       </book-info> 
                    </div>
                    <div style="clear: both;"></div>
                </div>

                <div class="list-books-content-right-box-show-more" @click="showMore()" v-if="isActiveBtn">
                    <p class="reset-margin medium-text-form-m txt-show-more">{{label.txt_show_more}}</p>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
import AppGat from '../../common/appGat/AppGat.vue';
import BookInfo from '../../common/bookInfo/BookInfo.vue';
import BookService from '../../../services/BookService.js';
import CommonService from '../../../services/CommonService.js';
import Label from '../../../common/Label.js';

export default {
    name: 'list-book',
    components: { AppGat, BookInfo },
    mixins: [CommonService, BookService, Label],
    data() {
        return {
            books: [],
            isActiveBtn: false,
            config: {
                params: {
                    per_page: 20,
                    page: 1
                }
            },
            handleSuccess: this.initData,
        }
    },
    computed: {

    },
    mounted() {
        // init function when call api is successful.
        this.handleSuccess = this.initData;
        // Call api to load data
        this.callAPIToUpdateData();
    },
    methods: {
        initData(response) {
            if (200 == response.status) {
                this.isShowLoading = false;
                var responseData = response.data;
                this.books = responseData.data.resultInfo;
                this.isActiveBtn = (this.books.length == 20);
            }
        },
        addData(response) {
            if (200 == response.status) {
                this.isShowLoading = false;
                var responseData = response.data.data;
                this.books = this.books.concat(responseData.resultInfo);
                this.isActiveBtn = (responseData.resultInfo.length == 20);
            }
        },
        showMore() {
            this.config.params.page += 1;
            this.handleSuccess = this.addData;
            this.callAPIToUpdateData();
        },
        resetParams() {
            this.config = {
                params: {
                    per_page: 20,
                    page: 1
                }
            }
        },
        getListNearByBook(position) {
            var tmpConfig = this.config;
            tmpConfig.params.curLat = position.coords.latitude;
            tmpConfig.params.curLong = position.coords.longitude;
            tmpConfig.params.range = 200000;
            //Call api to get list book which near user
            this.api.callGetApi(this.api.api_near_by_book, tmpConfig).then(this.handleSuccess, this.handleErrorFunction);
        },
        callAPIToUpdateData() {
            this.isShowLoading = true;
            //Call api to get list sharing book
            this.api.callGetApi(this.api.api_list_sharing_books, this.config).then(this.handleSuccess, this.handleErrorFunction);
        },
    }
}
</script>

<style lang="scss">
@import '../../../scss/common_styles.scss';
@import 'ListBooks.scss';
</style>



