<template>
    <div class="item-event">
        <div class="item-event-left">
            <img class="image-fill img-book" src="../../../../assets/images/event_1.png" :alt="title">
        </div>
        <div class="item-event-right">
            <p class="medium-text-form-m text-color-default text-limit item-event-title">{{title}}</p>
            <p class="small-text-form-r text-color-author">
                <i class="fa fa-clock-o" aria-hidden="true"></i>
                {{time}}
            </p>
            <div class="medium-text-form-r text-color-highlight text-limit item-event-name">
                {{name}}
            </div>
        </div>
    </div>
</template>


<script>
import Label from '../../../../common/Label.js';

export default {
    name: 'item-event',
    mixins: [Label],
    components: {},
    data() {

        return {
        }
    },
    props: {
        time: {
            required: true
        },
        name: {
            required: true
        },
        title: {
            required: true
        }
    }
}
</script>

<style lang="scss">
@import '../../../../scss/text_styles.scss';
@import '../../../../scss/common_styles.scss';
@import 'ItemEvent.scss';
</style>



