<template>
    <div>
        <div class="header-arrow-up user-noti"></div>
        <div class="header-extension">
            
            <div style="height:6px;"></div>
            <div class="header-extension-no-item" v-if="notifyList.length==0">
                {{label.no_notify}}
            </div>
            <header-user-notification-row
                v-for="(item, index) in notifyList" :key="index" :item="item"
                ></header-user-notification-row>
            
            <div class="header-extension-split"></div>
            <div class="header-extension-center-item">
                <button v-on:click="goNotificationPage" class="btn btn-link medium-text-form-m">{{label.view_all}}</button>
            </div>
            <div style="height:6px;"></div>
        </div>
    </div>
</template>

<script>
import HeaderUserNotificationRow from './HeaderUserNotificationRow.vue'
import Label from '../../../common/Label.js'
export default {
    props: [ "notifyList" ],
    mixins: [ Label ],
    components: { HeaderUserNotificationRow },
    methods: {
        goNotificationPage() {
            this.$router.push({name: this.label.NAME_USER_NOTIFICATION_PAGE});
        }
    }
}
</script>

<style>

</style>



