import * as CONST from '../common/const.js'
import Label from '../common/Label.js';

export default {
  mounted() {
    this.api_image = this.root_api + this.api_image;
  },
  mixins: [Label],
  data() {
    return {
      root_api: CONST.BASE_URL,
      api_image: "common/get_image/",
      maxRowReview: 3,
    }
  },
  methods: {
    getUrlImage(imageId) {
      if (imageId) {
        return this.api_image + imageId;
      }
      return "";
    },

    getLinkImageLocation(imageName) {
      return require('../assets/images/' + imageName + '.png');
    },

    getLocation() {
      var promiseObj = new Promise(function (handleSussessFunction, handleErrorFunction) {
        if (navigator.geolocation) {
          navigator.geolocation.getCurrentPosition(handleSussessFunction, handleErrorFunction);
        } else {
          handleErrorFunction("Geolocation is not supported by this browser.");
        }
      });
      return promiseObj;
    },

    displayError(error) {
      switch (error.code) {
        case error.PERMISSION_DENIED:
          console.log("User denied the request for Geolocation.");
          break;
        case error.POSITION_UNAVAILABLE:
          console.log("Location information is unavailable.");
          break;
        case error.TIMEOUT:
          console.log("The request to get user location timed out.");
          break;
        case error.UNKNOWN_ERROR:
          console.log("An unknown error occurred.");
          break;
      }
    },
    isNotNull(item) {
      if ("undefined" == typeof (item) || null == item) {
        return false;
      }
      return true;
    },
    isNull(item) {
      return !this.isNotNull(item);
    },
    getContentLimitLength(content, maxLength) {
      var ret = content;
      if (this.isNull(ret)) {
        return "";
      }
      if (this.isNull(maxLength)) {
        maxLength = 750;
      }
      if (ret.length > maxLength) {
        ret = ret.substr(0, maxLength - 3) + '...';
      }
      return ret;
    },
    setMetaTags(metaInfo) {
      $("#mTitle").attr("content", metaInfo.title);
      $("#mUrl").attr("content", metaInfo.urlTarget);
      $("#mImage").attr("content", metaInfo.imageUrl);
      $("#mDescription").attr("content", metaInfo.description);
    },
    getAuthor(arrAuthor) {
      var author = "";
      var length = 0;
      // arrAuthor is String, Return author of book if arrAuthor is not empty
      // else return "ang cp nht"
      if (typeof arrAuthor === 'string' || arrAuthor instanceof String) {
        if (this.trimSpace(arrAuthor) == "") {
          return this.label.txt_updating;;
        } else {
          return arrAuthor;
        }
      }
      // arrAuthor is array, Return list author of book if arrAuthor is not empty,
      // else return "ang cp nht"
      if (arrAuthor && arrAuthor.length) {
        length = arrAuthor.length;
      }
      if (length) {
        return arrAuthor[0].authorName;
      }
      for (var index = 0; index < length; index++) {
        author += arrAuthor[index].authorName;
      }
      if (this.trimSpace(author) == "") {
        author = this.label.txt_updating;
      }
      return author;
    },
    goToLoginPage() {
      this.$router.push({
        path: '/login/signin'
      })
    },
    typingReview(id) {
      var maxLengthInLine = 76;
      var start = 0;
      id = '#' + id;
      var editReviewText = $(id).val();
      var length = editReviewText.length;
      var numberOfLineBreaks = (editReviewText.match(/\n/g) || []).length;
      var lastEnterIndex = editReviewText.lastIndexOf("\n");
      if (numberOfLineBreaks > 0) {
        length = (length - lastEnterIndex) + numberOfLineBreaks * 76;
      }
      var numberLine = length / maxLengthInLine;
      if (numberLine >= this.maxRowReview - 1) {
        this.maxRowReview += 2;
      }
    },
    trimSpace(str) {
      return str.replace(/^\s*/, "").replace(/\s*$/, "");
    },
    goBack(){
        history.back();
    },
    getDistance(distance){
      var met = " m";
      var kilomet = " km";
      if(this.isNull(distance)){
        return 0 + met;
      }
      if(distance >= 1000){
        if(distance % 1000 == 0){
          return distance/1000 + kilomet;
        }
        return (distance/1000).toFixed(2) + kilomet;
      } else {
        return distance + met;
      }
    }
  }
}



