<template>
    <div>
        <!-- for private user page -->
        <div v-if="isPrivateFlag" class="container-gat reset-padding">
            <!-- loading dialog -->
            <loading-dialog :loadingDialogName="'upload-profile-image-loading'" :isLoadingOpen="isSystemExecuting"></loading-dialog>
            <!-- upload profile image modal -->
            <modal :name="'change-image-dialog'" :clickToClose="false" :width="400" :height="450">
                <!-- in case of image src is existed -->
                <div v-if="!imageSrc" class="upload-image-container" style="padding-top:166px;">
                    <label id="uploader" for="upload-photo">
                        <span class="fa fa-file-image-o fa-2x" ></span>
                        <span class="medium-text-form-r">&nbsp;&nbsp;{{label.chooseImage}}</span>
                    </label>
                    <input type="file" name="photo" id="upload-photo" v-on:change="readURL($event)"/>
                </div>
                <!-- in case of image src is not existed -->
                <div v-if="imageSrc" class="upload-image-container">
                    <label for="upload-photo" style="height:100%;width:100%;">
                        <img id="image-field" :src="imageSrc" class="mouse-pointer" style="height:100%;width:100%;"/>
                    </label>
                    <input type="file" name="photo" id="upload-photo" v-on:change="readURL($event)"/>
                </div>
                <!-- reset button -->
                <div class="reset-image-button">
                    <button v-on:click="reset" class="btn btn-gat-wait btn-circle display-left" style="width:34px;padding-left:10px">
                        <i class="fa fa-refresh"></i>
                    </button>
                </div>
                <!-- OK/Cancel button -->
                <div class="confirm-dialog-button" style="right:20px;">
                    <button v-on:click="accept" class="btn btn-gat btn-circle display-left" style="width:120px;">{{label.confirmButton}}</button>
                    <button v-on:click="reject" class="btn btn-gat-cancel btn-circle display-right" style="width:120px;margin-left:10px;">{{label.ignoreButton}}</button>
                </div>
            </modal>
            <!-- user profile page layout -->
            <div class="row">
                <!-- user info block -->
                <div class="col-xs-3">
                    <div class="affix">
                        <user-info :userInfo="userInfo" :byMyself="true" @openChangeImageDialog="openChangeImageDialog"></user-info>
                    </div>
                </div>
                <!-- user book shelf block -->
                <div v-if="$route.params.tab=='book-shelf'" class="col-xs-9 padding-left-10">
                    <user-book-shelf></user-book-shelf>
                </div>
                <!-- user read history block -->
                <div v-if="$route.params.tab=='read-history'" class="col-xs-9 padding-left-10">
                    <user-read-history></user-read-history>
                </div>
                <!-- user info setting block -->
                <div v-if="$route.params.tab=='info-setting'" class="col-xs-9 padding-left-10">
                    <user-info-detail :userInfo="userInfo" @infoChanged="userInfo = $event"></user-info-detail>
                </div>
            </div>
        </div>

        <!-- for public user page -->
        <div v-else class="container-gat reset-padding">
            <message-dialog
                :dialogName="'send-message-'+userInfo.userId"
                :isOpen="isDialogOpen"
                :user="userInfo"
                @dialogClosed="isDialogOpen = false"></message-dialog>
            <div class="row">
                <div class="col-xs-3">
                    <div class="affix">
                        <user-info :userInfo="userInfo" :byMyself="false" @openMessageDialog="openMessageDialog"></user-info>
                    </div>
                </div>
                <div v-if="$route.params.tab=='book-shelf'" class="col-xs-9 padding-left-10">
                    <visitor-book-shelf :userName="userInfo.name"></visitor-book-shelf>
                </div>
                <div v-if="$route.params.tab=='read-history'" class="col-xs-9 padding-left-10">
                    <visitor-read-history :userName="userInfo.name"></visitor-read-history>
                </div>
            </div>
        </div>

    </div>
</template>
<script>
import UserBookShelf from '../components/UserBookShelf.vue'
import UserReadHistory from '../components/UserReadHistory.vue'
import UserInfoDetail from '../components/UserInfoDetail.vue'
import UserInfo from '../../../common/userInfo/UserInfo.vue'
import LoadingDialog from '../../../common/dialog/LoadingDialog.vue'

import VisitorBookShelf from '../components/VisitorBookShelf.vue'
import VisitorReadHistory from '../components/VisitorReadHistory.vue'
import MessageDialog from '../../../common/dialog/MessageDialog.vue'

import BookService from '../../../../services/BookService.js'
import * as CONST from '../../../../common/const.js'
import Label from '../../../../common/Label'
export default {
    name: 'userProfile',
    mixins: [ Label, BookService ],
    components: { UserBookShelf, UserReadHistory, UserInfoDetail, UserInfo, LoadingDialog, VisitorBookShelf, VisitorReadHistory, MessageDialog },
    mounted() {
        this.initPage();
    },
    watch: {
        '$route': function() {
            this.initPage();
        }
    },
    computed: {
        isPrivateFlag() {
            return localStorage.getItem(CONST.USER_ID)==this.$route.params.id;
        }
    },
    data() {
        return {
            //image src
            imageSrc: null,
            userInfo: {},
            isDialogOpen: false
        }
    },
    methods: {
        initPage(){
            if (localStorage.getItem(CONST.USER_ID)==this.$route.params.id) {
                this.$getItem(CONST.LOGIN_USER).then(response => 
                    {
                        this.userInfo = response;
                    }
                );
            } else {
                this.isDialogOpen = false;
                this.getTargetUserInfo();
            }
        },
        openChangeImageDialog() {
            this.$modal.show('change-image-dialog');
        },
        accept() {
            if (this.imageSrc) {
                this.$modal.hide('change-image-dialog');
                this.isSystemExecuting = true;
                var params = {
                    name: this.userInfo.name,
                    image: this.imageSrc.split(",")[1],
                    changeImageFlag: true
                };
                var headers = {
                    'Authorization': this.loginToken
                }
                var _self = this;
                this.api.callPostApi(this.api.api_update_user_info, params, {headers})
                    .then(response => {
                        //reset imageId of userInfo
                        this.userInfo.imageId = response.data.data.imageId;
                        
                        //reset localforage
                        this.$getItem(CONST.LOGIN_USER).then(loginUser => 
                            {
                                loginUser.imageId = response.data.data.imageId;
                                this.$setItem(CONST.LOGIN_USER, loginUser).then(function () {
                                    _self.$emit('imageChanged');
                                });
                                this.userInfo = loginUser;
                            }
                        );
                        this.isSystemExecuting = false;
                        this.imageSrc = null;
                    })
                    .catch(this.handleErrorFunction);
            }
        },
        reject() {
            this.$modal.hide('change-image-dialog');
            this.imageSrc = null;
        },
        reset() {
            this.imageSrc = null;
        },
        readURL(ev) {
            if (ev.target.files && ev.target.files[0]) {
                var _self = this;
                var reader = new FileReader();
                reader.onload = function (e) {
                    $('#image-field')
                        _self.imageSrc = e.target.result;
                };
                reader.readAsDataURL(ev.target.files[0]);
            }
        },
        openMessageDialog() {
            this.isDialogOpen = true;
        },
        getTargetUserInfo() {
            var config = {
                params: {
                    userId: this.$route.params.id
                }
            };
            this.api.callGetApi(this.api.api_get_user_public_info, config)
                .then(response => {
                        if(response.data.data.resultInfo.usuallyLocation.length==0){
                            response.data.data.resultInfo.usuallyLocation[0] = {};
                        }
                        this.userInfo = response.data.data.resultInfo;
                    })
                .catch(this.handleErrorFunction);
        }
    }
}
</script>
<style lang="scss">
@import 'userProfile.scss';

</style>



