<template>
    <div class="row list-events-contain">
        <div class="row-header">
            <span class="biger-text-form-m text-color-default">{{label.txt_event}}</span>
        </div>
        <hr class="line-break reset-padding reset-margin">
        <div class="list-events">
            <div class="list-events-left" v-if="isShowEventLeft">
                <div class="box-block list-events-first-img"  v-on:click="goToEventDetail(listEvents[0])">
                    <img class="image-fill img-book" src="../../../../assets/images/event_1.png" alt="">
                </div>
                <p class="biger-text-form-m text-color-default text-limit list-events-first-title">{{(listEvents[0].title)}}</p>
                <div class="list-events-first-info">
                    <div class="left-side medium-text-form-r text-color-author">
                        <span>
                            <i class="fa fa-clock-o" aria-hidden="true"></i>
                        </span>
                        <span>{{listEvents[0].time}}</span>
                    </div>
                    <div class="right-side medium-text-form-r text-color-highlight">
                        {{listEvents[0].name}}
                    </div>
                </div>
                <div class="medium-text-form-r text-color-default list-events-first-content">
                    {{getContentLimitLength(listEvents[0].description, maxLengthOfDescript)}}
                    <router-link :to="{ name: label.NAME_EVENT_DETAIL_PAGE, query: { id: listEvents[0].id }}" v-if="isShowLimitReview" class="medium-text-form-r text-color-highlight">{{label.txt_show_more}}</router-link>       
                </div>
            </div>
            <div class="row list-events-right">
                <div v-for="(event, index) in listEvents" :key="index" v-if="isShowListEvents && index > 0" 
                    :class="{'margin-top-between-events' : index > 1}"
                    v-on:click="goToEventDetail(event)">
                    <item-event  :title="event.title" :name="event.name" :time="event.time">
                    </item-event>
                </div>
            </div>
            <div style="clear: both;"></div>
        </div>
        <div class="list-events-old">
            <div class="row list-events" v-if="isShowMoreEvent">
                <div class="list-events-item margin-top-between-events" v-for="(event, index) in listOldEvents" :key="index" 
                    :class="{'left-side': index%2 != 0, 'right-side': index%2 == 0}" 
                    v-on:click="goToEventDetail(event)">
                    <item-event 
                    :title="event.title" :name="event.name" :time="event.time">
                    </item-event>
                </div>
            </div>
            <div class="list-events-show-more" v-on:click="showMoreEvents">
                <p class="reset-margin medium-text-form-m list-events-txt-show-more">{{label.txt_show_more}}</p>
            </div>
            <div style="clear: both;"></div>
        </div>
    </div>
</template>

