<template>
    <modal :name="errorDialogName" :clickToClose="false" :width="480" :height="200" class="dialog-eclipse">
        <div class="dialog-title biger-text-form-m">
            <button class="close" @click="closeDialog">&times;</button>
            <div>{{label.dialogTitle}}</div>
        </div>
        <div v-html="errorMessage" class="confirm-dialog-content"></div>
        <div class="confirm-dialog-button" style="left:145px">
            <button v-on:click="closeDialog" class="btn btn-gat btn-circle display-left" style="width:190px;">{{label.closeButton}}</button>
        </div>
    </modal>
</template>

<script>
import Label from '../../../common/Label.js'
import * as CONST from '../../../common/const.js'
export default {
    props: ["errorDialogName", "isErrorOpen", "errorMessage", "errorStatus"],
    mixins: [Label],
    watch: {
        isErrorOpen: function(){
            if(this.isErrorOpen){
                this.$modal.show(this.errorDialogName);
            }else{
                this.$modal.hide(this.errorDialogName);
            }
        }
    },
    methods: {
        closeDialog() {
            this.$emit('errorDialogClosed');
            this.$modal.hide(this.errorDialogName);
            if (this.errorStatus == CONST.UNAUTHORIZED_STT) {
                localStorage.removeItem(CONST.USER_ID);
                localStorage.removeItem(CONST.LOGIN_TOKEN);
                localStorage.removeItem(CONST.FIREBASE_PASS);
                this.$router.push({name: CONST.NAME_LOGIN_PAGE, params: {tab:"signin"}});
                // let loginUserId = localStorage.getItem(CONST.USER_ID);
                // let notificationsRef = window.firebaseDB.ref('notifications');
                // notificationsRef.child(loginUserId).off('child_added');

                //firebase db (tabel messages) refer
                let messagesRef =  window.firebaseDB.ref('messages');
                messagesRef.off('child_added');
            }
        }
    },
    data() {
        return {
            
        }
    }
}
</script>

<style>

</style>



